import Commons from "./commons";
import AnimationS from "./animation";
import IQuery from "./iquery";
//驱动器公用函数
class DriverMethods{
    constructor(vue,driver){
        this.vue = vue;
        this.driver = driver;//驱动器实体
    }
    //攻击
    MinionAttack(attNode,attacker,recNode,receiver,attackerTeam,receiverTeam,attBloodNode,recBloodNode){
        let attX = attNode.offsetLeft,attY = attNode.offsetTop;
        let recX = recNode.offsetLeft,recY = recNode.offsetTop;
        let maxBackswingDistance = this.driver.screenW * 0.07;
        let finalX = recX,finalY = recY;
        let w = this.vue.$store.state.MINION_WIDTH;//随从的宽度
        let h = this.vue.$store.state.MINION_HEIGHT;//随从高度


        let recGetHurt = false,attGetHurt = false;
        //随从跳起，准备攻击
        return new Promise(async (resolve,reject)=>{
            await this.driver.ams.Delay(200);
            //随从跳起
            attNode.style.transition = 'all .2s';
            attNode.style.transform = 'scale(1.1)';
            attNode.style.zIndex = '1';
            await this.driver.ams.Delay(200);

            //随从跳起后后摇
            attNode.style.transition = 'all .09s';
            attNode.style.left = attX + (0.3 * (attX - recX) >  maxBackswingDistance? maxBackswingDistance : 0.3 * (attX - recX)) + 'px';
            attNode.style.top = attY + (0.3 * (attY - recY) > maxBackswingDistance ? maxBackswingDistance : 0.3 * (attY - recY)) + 'px';
            await this.driver.ams.Delay(90);
            
            //攻击者轮询全队的攻击前函数调用
            let attTeam = attacker.battleAttr.belongPlayer ? this.driver.PlayerMinions : this.driver.EnemyMinions;
            for(let i in attTeam){
                await attTeam[i].TriggerBeforeAttack(attacker,this.driver,receiver);
            }
            //轮询被攻击者队伍的函数调用
            let recTeam = receiver.battleAttr.belongPlayer ? this.driver.PlayerMinions : this.driver.EnemyMinions;
            for(let i in recTeam){
                await recTeam[i].TriggerBeforeBeAttacked(attacker,receiver,this.driver);
            }
            //目标没死的情况下才可以冲过去
            let dodge = false;
            if(receiver.type.search('虚空') != -1){
                let rand = Math.floor(Math.random()*100);
                if(rand < 10) dodge = true;
            }
            if(receiver.battleAttr.hp < 0 || dodge){
                if(dodge){
                    recNode.style.transition = 'all .3s';
                    await this.driver.ams.Delay(1);
                    recNode.style.opacity = 0;
                    await this.driver.ams.Delay(300);
                    recNode.style.opacity = 1;
                    await this.driver.ams.Delay(300);
                    recNode.style.transition = 'none';
                }
                //恢复到原始位置
                attNode.style.transform = 'scale(1)';
                attNode.style.zIndex = '0';
                this.driver.com.AdjustMinionsCoordinate(true);
                this.driver.com.AdjustMinionsCoordinate(false);
                await this.driver.ams.TransitionEffect(true,true,false,200);
                resolve(true);
                return;
            }
            let moveTime = Math.floor(13 + .8 * Math.abs(attX - recX)/w)/100;
            attNode.style.transition = `all ${moveTime}s`;
            if(attX > recX) finalX += w *.25;
            else if(attX < recX) finalX -= w*.25;
            if(attY > recY) finalY += h *.25;
            else if(attY < recY) finalY -= h*.25;
            attNode.style.left = finalX + 'px';
            attNode.style.top = finalY + 'px';
            attNode.style.transform = 'scale(1)';
            await this.driver.ams.Delay(moveTime * 1002);
            //此时撞击到目标
            //第一时间打出伤害
            let overKill = attacker.battleAttr.ad > receiver.battleAttr.hp ? true : false;
            if(receiver.battleAttr.hp > 0 && !receiver.battleAttr.divine_shield){
                if(!attacker.battleAttr.poisonous) this.driver.ams.MinionGetDamage(recNode,attacker.battleAttr.ad,"normal",1000);
                else{
                    this.driver.ams.MinionGetDamage(recNode,attacker.battleAttr.ad,"poisonous",1000);
                    receiver.battleAttr.hp = 0;
                }
                this.driver.ams.AttrReduceAnimate(attacker.battleAttr.ad,receiver,recNode);
                recGetHurt = true;
            }
            //火花效果
            this.driver.ams.ImpactSpark(attX,attY,recX,recY,attacker.battleAttr.ad);
            //攻击者恢复到原始位置
            attNode.style.transform = 'scale(1)';
            this.driver.com.AdjustMinionsCoordinate(true);
            this.driver.com.AdjustMinionsCoordinate(false);
            await this.driver.ams.TransitionEffect(true,true,false,moveTime * 1000);
            attNode.style.zIndex = '0';
            if(!receiver.battleAttr.divine_shield) await this.driver.ams.AttrReduceAnimate(0,receiver,recNode,null,attacker);
            //攻击者受到伤害
            if(attacker.battleAttr.hp > 0 && !attacker.battleAttr.divine_shield){
                if(!receiver.battleAttr.poisonous) this.driver.ams.MinionGetDamage(attNode,receiver.battleAttr.ad,"normal",800);
                else{
                    this.driver.ams.MinionGetDamage(attNode,receiver.battleAttr.ad,"poisonous",800);
                    attacker.battleAttr.hp = 0;
                }
                this.driver.ams.AttrReduceAnimate(receiver.battleAttr.ad,attacker,attNode);
                attGetHurt = true;
            }
            if(receiver.battleAttr.divine_shield){
                await this.driver.ams.Delay(250);
                await this.driver.ams.LostDivineShield(recNode,receiver);
                for(let j in receiverTeam){
                    await receiverTeam[j].TriggerLostDivineShield(this.driver,receiver);
                }
            }
            //受到伤害后调用
            if(!attacker.battleAttr.divine_shield) await this.driver.ams.AttrReduceAnimate(0,attacker,attNode,null,receiver);
            if(attacker.battleAttr.divine_shield){
                if(receiver.battleAttr.divine_shield) await this.driver.ams.Delay(50);
                else await this.driver.ams.Delay(250);
                await this.driver.ams.LostDivineShield(attNode,attacker);
                for(let i in attackerTeam){
                    await attackerTeam[i].TriggerLostDivineShield(this.driver,attacker);
                }
            }
            
            //攻击者执行超杀
            if(overKill){
                let friT = attacker.battleAttr.belongPlayer ? this.driver.PlayerMinions : this.driver.EnemyMinions;
                for(let j =0;j<friT.length;j++){
                    if(friT[j] == attacker){
                        await attacker.TriggerOverKill(j);
                        break;
                    }
                }
            }
            await this.driver.ams.Delay(200);
            if(attacker.battleAttr.hp < 0 && receiver.battleAttr.hp > 0) await this.driver.ams.Delay(200);

            if(recGetHurt) await receiver.TriggerGetHurt(attacker,attacker.battleAttr.ad,this.driver);
            if(attGetHurt) await attacker.TriggerGetHurt(receiver,receiver.battleAttr.ad,this.driver);
            resolve(true);
        })
    }
    
    
    //死亡判定事件结束
    DeathDeterminate(isPlayerFirst){
        //isPlayer : true表示 敌对随从和玩家随从同时死亡，先触发玩家随从
        let com = new Commons(this.vue);
        let ams = new AnimationS(this.vue);
        return new Promise(async (resolve,reject)=>{
            if(isPlayerFirst){
                for(let i = 0;i<7 && await this.TriggerDeathRattle(true);i++);
                for(let i = 0;i<7 && await this.TriggerDeathRattle(false);i++);
            }
            else{
                for(let i = 0;i<7 && await this.TriggerDeathRattle(false);i++);
                for(let i = 0;i<7 && await this.TriggerDeathRattle(true);i++);
            }
            //调整随从的位置
            com.AdjustMinionsCoordinate(true);
            com.AdjustMinionsCoordinate(false);
            await ams.TransitionEffect(true,true,false,200);

            resolve(true);
        })
    }
    //触发一个队伍里的亡语
    TriggerDeathRattle(isPlayer){
        let com = new Commons(this.vue);
        let ams = new AnimationS(this.vue);
        let query = new IQuery(this.vue);
        return new Promise(async (resolve,reject)=>{
            let targetTeam = isPlayer ? this.driver.PlayerMinions : this.driver.EnemyMinions;
            let targetNodes = isPlayer ? document.querySelectorAll('.player_row li') : document.querySelectorAll('.bob_row li');
            let haveDead = false;
            for(let i = 0;i<targetTeam.length;i++){
                let tgt = targetTeam[i];
                if(tgt.battleAttr.hp <= 0){
                    //数据上删除死亡随从
                    targetTeam.splice(i,1);
                    //先触发调用亡语的动画
                    if(tgt.battleAttr.death_rattle) ams.TriggerDeathRattleAnimation(targetNodes[i])
                    //调用随从死亡动画
                    await ams.MinionDeathAnimation(targetNodes[i]);
                    //删除死亡随从的结点
                    targetNodes[i].remove();
                    //调整随从的位置
                    com.AdjustMinionsCoordinate(true);
                    com.AdjustMinionsCoordinate(false);
                    await ams.TransitionEffect(true,true,false,200);
                    //触发死亡随从的退场效果
                    tgt.TriggerOutBattle(i);
                    //触发死亡随从亡语
                    for(let j=0;j < query.DeathRattleTimes(isPlayer,this.driver);j++){
                        await this.ExecuteDeathRattle(tgt,i);
                    }
                    //触发复仇
                    for(let j in targetTeam){
                        await targetTeam[j].TriggerRevenge(tgt,this.driver);
                    }
                    //复生
                    if(tgt.battleAttr.reborn){
                        let q = new IQuery(this.vue);
                        for(let j =0;j<q.SummonTimes(tgt.battleAttr.belongPlayer,true,this.driver) && targetTeam.length < 7;j++){
                            let min = await com.SummonMinionsInFighting(tgt,tgt,tgt.battleAttr.belongPlayer,this.driver,i,true,false,false);
                            let minNode = null;
                            for(let k in targetTeam){
                                if(targetTeam[k] == min){
                                    minNode = isPlayer ? document.querySelectorAll('.player_row li')[k] : document.querySelectorAll('.bob_row li')[k];
                                }
                            }
                            for(let k = minNode.children.length-1;k>=0;k--){
                                //修改数据结点
                                if(minNode.children[k].className == 'blood'){
                                    min.battleAttr.hp = 1;
                                    minNode.children[k].children[0].innerText = 1;
                                    minNode.children[k].children[0].classList.remove('higher');
                                    minNode.children[k].children[0].classList.add('lower');
                                }
                                if(minNode.children[k].className == 'reborn'){
                                    minNode.children[k].style.transition = 'all .2s';
                                    await ams.Delay(1);
                                    minNode.children[k].style.transform = 'scale(1.2)';
                                    minNode.children[k].style.opacity = '0';
                                    await ams.Delay(200);
                                    minNode.children[k].remove();
                                }
                            }
                            min.battleAttr.reborn = false;
                            min.battleAttr.attacked = false;
                        }
                    }
                    haveDead = true;
                    break;
                }
            }
            resolve(haveDead);
        })
    }
    //执行亡语
    ExecuteDeathRattle(target,index){
        return new Promise(async (resolve,reject)=>{
            if(!target.battleAttr.death_rattle){
                resolve(false);
                return;
            }
            //调用死者的亡语
            let data = {
                //驱动器实体
                battleEntity : this.driver,
                //亡语触发者数据实体
                deadEntity : target,
                //亡语触发者在原数组的数组下标
                deadIndex : index,
            }
            
            //实际调用
            await target.TriggerDeathRattle(data);
            //随从死亡动画
            resolve(false);
        })
    }
    

}


export default DriverMethods;