class Block{
    constructor(type,impactor,impactorClass,remainTime,execute,battleCryLimit = null){
        //该块的类型
        this.type = type || null,
        //施加这个块的角色
        this.impactor = impactor || null;
        //施加这个块的角色类型
        this.impactorClass = impactorClass || null;
        //持续时间,<=0表示过时，null表示持续存在
        this.remainTime = remainTime || null;
        //块的执行函数
        this.execute = execute || null;
        //块的执行次数
        this.executeTime = 0;
        //战吼限制
        this.battleCryLimit = battleCryLimit;
    }
    //执行块
    Execute(dataItem,domItem){
        //dataItem ：数据
        //domItem ：DOM结点
        return new Promise(async (resolve,reject)=>{
            //dataItem:打出手牌的数据对象
            if(this.execute == null){
                return false;
            }
            await this.execute(dataItem,domItem);
            this.executeTime += 1;
            resolve(true);
        })
    }
}

export default Block;