import Hero from "../data/hero";
import Commons from "../function/commons";
import AnimationS from "../function/animation";
//雷诺
class Renault extends Hero{
    constructor(vue,dataItem){
        super(vue,dataItem);
        this.powerType = 'active';
        this.immediateActive = false;
        this.selectPlayerMinion = true;
        this.selectBobMinion = false;
        this.useTime = 1;
    }
    Active(dataItem,domItem){
        let ams = new AnimationS(this.vue);
        let com = new Commons(this.vue);
        return new Promise(async (resolve,reject)=>{
            dataItem.golden = true;
            dataItem.ad *= 2;
            dataItem.hp *= 2;
            //DOM更新
            //结点更新
            await ams.MinionNodeGetGolden(domItem,dataItem);
            com.AdjustMinionsCoordinate(true);
            com.AdjustMinionsCoordinate(false);
            await ams.Delay(10);
            resolve(true);
        })
    }
}


export default Renault;