// import Commons from "./commons";
import AnimationS from "./animation";
class Special{
    constructor(vue){
        this.vue = vue;
        this.screenW = this.vue.$store.state.ScreenSize.width;
        this.screenH = this.vue.$store.state.ScreenSize.height;
    }
    //红色奥数的效果
    SpecialArcaneRunes(targetNode){
        //targetNode释放红色奥数的目标结点
        let ams = new AnimationS(this.vue);
        return new Promise(async (resolve,reject)=>{
            let arcArr = [];
            for(let i =0;i<2;i++){
                let arcane = document.createElement('div');
                arcane.classList.add('arcane_runes');
                arcane.style.left = targetNode.offsetLeft + this.screenW*0.035 + 'px'; 
                arcane.style.top = targetNode.offsetTop + this.screenW *0.05 + 'px'; 
                this.vue.$refs.specialMask.appendChild(arcane);
                arcArr.push(arcane);
                await ams.Delay(300);
            }
            await ams.Delay(2500);
            for(let i = 1;i>=0;i--) arcArr[i].remove();
            resolve(true);
        })
    }
    //蓝色色奥数的效果
    SpecialBlueArcaneRunes(targetNode){
        //targetNode释放红色奥数的目标结点
        let ams = new AnimationS(this.vue);
        return new Promise(async (resolve,reject)=>{
            let arcane = document.createElement('div');
            arcane.classList.add('blue_arcane_runes');
            arcane.style.left = targetNode.offsetLeft + this.screenW*0.035 + 'px'; 
            arcane.style.top = targetNode.offsetTop + this.screenW *0.05 + 'px'; 
            this.vue.$refs.specialMask.appendChild(arcane);
            await ams.Delay(2000);
            arcane.remove();
            resolve(true);
        })
    }
    //魔蝠的效果
    SpecialEat(eater,food){
        return new Promise((resolve,reject)=>{
            food.style.transition = 'all .3s';
            food.style.left = eater.style.left;
            food.style.top = eater.style.top;
            food.style.transform = 'scale(.1)';
            setTimeout(() => {
                food.style.transition = 'none';
                resolve(true);
            }, 300);
        })
    }
    //磁力动画
    SpecialMagnetic(targetNode){
        return new Promise((resolve,reject)=>{
            targetNode.style.transition = 'all .2s';
            targetNode.style.marginLeft = '0';
            setTimeout(() => {
                targetNode.style.marginLeft = this.screenW * 0.08 + 'px';
                targetNode.style.opacity = '0';
                setTimeout(() => {
                    resolve(true);
                    return;
                }, 200);
            }, 1);
        })
    }
    //喷火效果
    SpecialSpurtFire(attX,attY,recX,recY){
        let sparksSize = 10;//定义粒子大小
        let ams = new AnimationS(this.vue);
        //获取画布上下文
        let ctx = document.querySelector('#canvas').getContext('2d');
        //粒子容器
        let sparksContainer = [];
        //持续时间
        let durTime = 0;
        // 创建渐变
        // let gradientColor = ctx.createRadialGradient(175, 150, 0, 175, 150, 150);
        // gradientColor.addColorStop(0, "rgb(235, 235, 5)");
        // gradientColor.addColorStop(1, "rgb(183, 8, 8)");
        let sparksNumber = 300;//粒子个数
        let img = new Image();
        img.src = require('../../assets/special/sp (1).png');
        // let angle = Math.tan((recY - attY)/(recX - attX))
        return new Promise((resolve,reject)=>{
            for(let i = 0;i<sparksNumber;i++){
                let sp = {
                    x : attX + this.vue.$store.state.MINION_WIDTH/1.8 - sparksSize/2,//粒子的x坐标
                    y : attY + this.vue.$store.state.MINION_HEIGHT/1.8 - sparksSize/2,//粒子的y坐标
                    xa : (recX-attX)/20 + .25 - Math.random()/2, // xa , ya 为  粒子 xy 轴加速度
                    ya : (recY-attY)/20 + .25 - Math.random()/2,
                    size : sparksSize,
                }
                
                sparksContainer.push(sp);
                //重新绘制路径
                ctx.beginPath();
                // ctx.fillStyle = Math.floor(Math.random()*2) == 1 ? randColor1 : randColor2;
                // ctx.arc(sp.x, sp.y, sparksSize,0, Math.PI*2);
                ctx.drawImage(img,sp.x-sparksSize/2,sp.y-sparksSize/2,sparksSize,sparksSize);
                // ctx.globalAlpha = 1 ; //透明度
                ctx.fill();
            }
            let drawable = 1;
            let timer = setInterval(async () => {
                // ctx.clearRect(0,0,this.vue.$store.state.ScreenSize.width,this.vue.$store.state.ScreenSize.height)
                ctx.globalAlpha = .1;
                if(durTime > 50) ctx.clearRect(0,0,this.vue.$store.state.ScreenSize.width,this.vue.$store.state.ScreenSize.height)
                for(let i=0;i<sparksContainer.length && i < drawable;i++){
                    sparksContainer[i].x += sparksContainer[i].xa;
                    sparksContainer[i].y += sparksContainer[i].ya;
                    sparksContainer[i].xa = sparksContainer[i].xa*.999;
                    sparksContainer[i].ya = sparksContainer[i].ya*.999;
                    sparksContainer[i].size = sparksContainer[i].size + 1;
                    //重新绘制路径
                    ctx.beginPath();
                    // ctx.fillStyle = Math.floor(Math.random()*2) == 1 ? randColor1 : randColor2;
                    // ctx.arc(sparksContainer[i].x, sparksContainer[i].y, sparksContainer[i].size, 0, Math.PI*2);
                    ctx.drawImage(img,sparksContainer[i].x - sparksContainer[i].size/2,sparksContainer[i].y - sparksContainer[i].size/2,sparksContainer[i].size,sparksContainer[i].size);
                    
                    // ctx.globalAlpha = 1 ; //透明度
                    ctx.fill();
                    
                }
                durTime ++;
                drawable += 3;
                
                if(durTime >= 200){
                    ctx.globalAlpha = 1;
                    clearInterval(timer);
                    resolve(true);
                    return;
                }
            }, 1);
        })
    }

    //触发暴怒效果
    TriggerFrenzy(targetNode){
        let ams = new AnimationS(this.vue);
        return new Promise(async (resolve,reject)=>{
            for(let i in targetNode.children){
                if(targetNode.children[i].className == 'frenzy'){
                    let triggerIcon = document.createElement('div');
                    triggerIcon.classList.add('frenzy_trigger_icon');
                    triggerIcon.style.transition = 'all .25s';
                    triggerIcon.style.transform = 'scale(0)';
                    targetNode.insertBefore(triggerIcon,targetNode.children[i]);
                    await ams.Delay(1);
                    triggerIcon.style.transform = 'scale(3.4)';
                    await ams.Delay(250);
                    triggerIcon.style.transform = 'scale(1)';
                    await ams.Delay(250);
                    triggerIcon.remove();
                }
            }
            resolve(true);
        })
    }
    //触发被动效果
    TriggerPassive(targetNode){
        let ams = new AnimationS(this.vue);
        return new Promise(async (resolve,reject)=>{
            for(let i in targetNode.children){
                if(targetNode.children[i].className == 'passive'){
                    let triggerIcon = document.createElement('div');
                    triggerIcon.classList.add('frenzy_trigger_icon');
                    triggerIcon.style.transition = 'all .25s';
                    triggerIcon.style.transform = 'scale(0)';
                    targetNode.insertBefore(triggerIcon,targetNode.children[i]);
                    await ams.Delay(1);
                    triggerIcon.style.transform = 'scale(3.4)';
                    await ams.Delay(250);
                    triggerIcon.style.transform = 'scale(1)';
                    await ams.Delay(250);
                    triggerIcon.remove();
                }
            }
            resolve(true);
        })
    }
    //创造一个圣盾
    CreateDivineShield(targetNode){
        let ams = new AnimationS(this.vue);
        return new Promise(async (resolve,reject)=>{
            for(let i in targetNode.children){
                //圣盾的前驱结点是生命值结点
                if(targetNode.children[i].className == 'blood'){
                    let divineShield = document.createElement('div');
                    divineShield.classList.add('divine_shield')
                    divineShield.style.transform = 'scale(0)';
                    divineShield.style.transition = 'all .2s';
                    if(targetNode.children[i+1] && targetNode.children[i+1].className == 'wind_fury_mask') targetNode.children[i+1].after(divineShield);
                    else targetNode.children[i].after(divineShield);
                    await ams.Delay(10);
                    divineShield.style.transform = 'scale(1.1)';
                    await ams.Delay(200);
                    divineShield.style.transition = 'all .06s';
                    divineShield.style.transform = 'scale(.96)';
                    await ams.Delay(60);
                    divineShield.style.transition = 'all .03s';
                    divineShield.style.transform = 'scale(1)';
                    await ams.Delay(30);
                    divineShield.style.transition = 'none';
                    break;
                }
            }
            resolve(true);
        })
    }
    //附上嘲讽
    CreateTaunt(targetNode,isGolden){
        let tn = document.createElement('div');
        let childrenNodes = targetNode.children;//结点的孩子节点
        let ams = new AnimationS(this.vue);
        return new Promise(async (resolve,reject)=>{
            isGolden ? tn.classList.add('golden_taunt') : tn.classList.add('taunt');
            tn.style.transform = 'scale(.5)';
            for(let j=0;j<childrenNodes.length;j++){
                if(childrenNodes[j].className == 'min_img'){
                    //在图像结点前面插入嘲讽节点
                    targetNode.insertBefore(tn,childrenNodes[j]);
                    tn.style.transition = 'all .2s';
                    await ams.Delay(20);
                    tn.style.transform = 'scale(1)';
                    await ams.Delay(200);
                    tn.style.transition = 'none';
                    break;
                }
            }
            resolve(true);
        })
    }
    //创造剧毒
    CreatePoisonous(targetNode){
        let ams = new AnimationS(this.vue);
        let pn = document.createElement('div');
        return new Promise(async (resolve,reject)=>{
            pn.classList.add('poisonous');
            for(let i in targetNode.children){
                if(targetNode.children[i].className == 'ad'){
                    pn.style.transform = 'scale(.5)';
                    pn.style.transition = 'all .1s';
                    targetNode.insertBefore(pn,targetNode.children[i]);
                    await ams.Delay(10);
                    pn.style.transform = 'scale(1)';
                    await ams.Delay(110);
                    pn.style.transition = 'none';
                    break;
                }
            }
            resolve(true);
        })
    }
    //附上风怒效果
    CreateWindFury(targetNode){
        let ams = new AnimationS(this.vue);
        return new Promise(async (resolve,reject)=>{
            for(let i in targetNode.children){
                //风怒的前驱结点是生命值结点
                if(targetNode.children[i].className == 'blood'){
                    let windFury = document.createElement('div');
                    for(let i=0;i<3;i++){
                        let tape = document.createElement('span');
                        tape.classList.add(`tape${i+1}`);
                        windFury.appendChild(tape);
                    }
                    windFury.classList.add('wind_fury_mask');
                    targetNode.children[i].after(windFury);
                    break;
                }
            }
            resolve(true);
        })
    }
    //创造亡语标志
    CreateDeathRattleSign(targetNode){
        let ams = new AnimationS(this.vue);
        let dn = document.createElement('div');
        return new Promise(async (resolve,reject)=>{
            dn.classList.add('death_rattle');
            for(let i in targetNode.children){
                if(targetNode.children[i].className == 'ad'){
                    dn.style.transform = 'scale(.5)';
                    dn.style.transition = 'all .1s';
                    targetNode.insertBefore(dn,targetNode.children[i]);
                    await ams.Delay(10);
                    dn.style.transform = 'scale(1)';
                    await ams.Delay(110);
                    dn.style.transition = 'none';
                    break;
                }
            }
            resolve(true);
        })
    }
    //附上复生
    CreateReborn(targetNode){
        let ams = new AnimationS(this.vue);
        return new Promise(async (resolve,reject)=>{
            let rebornNode = document.createElement('div');
            rebornNode.classList.add('reborn');
            for(let j=0;j<targetNode.children.length;j++){
                if(targetNode.children[j].className == 'min_img'){
                    targetNode.children[j].after(rebornNode)
                    rebornNode.classList.add('mount');
                    await ams.Delay(2000);
                    rebornNode.classList.remove('mount');
                    break;
                }
            }
            resolve(true);
        })
    }
}

export default Special;