<template> 
    <div class="pub_view">
        <div class="resource_container" ref="resource_container" style="display: none;"></div>
        <div class="pub_container">
            <!-- bob区域 -->
            <div class="opponent_area" v-show="$store.state.Pub.turn % 2 != 0">
                <!-- 升级酒馆按钮 -->
                <div class="up_btn" ref="up_btn" 
                    v-show="$store.state.Pub.level < 6"
                    @touchstart="NodeClick($refs.up_btn)"
                    @click="UpPubLevel">
                    <div class="little_coin">{{ $store.state.Pub.upCost }}</div>
                </div>
                <div class="head">
                    <img src="../assets/ui/ui (24).png" class="cover" alt="bob">
                    <img :src="StarBarImgUrl($store.state.Pub.level)" alt="" class="pub_level">
                </div>
                <div class="freeze_btn" ref="freeze_btn" @touchstart="NodeClick($refs.freeze_btn)" @click="FreezeMinions">
                    <div class="little_coin">{{ $store.state.Pub.freezeCost }}</div>
                </div>

                <!-- 刷新按钮 -->
                <div class="refresh_btn" ref="refresh_btn" @touchstart="NodeClick($refs.refresh_btn)"
                    @click="RefreshMinions(true)">
                    <div class="little_coin">{{ RealRefreshCost() }}</div>
                </div>
                
                
            </div>

            <!-- 敌方英雄 -->
            <div class="enemy_area" v-show="$store.state.Pub.turn % 2 == 0">
                <div class="enemy_hero">
                    <img :src="require('../assets/hero/' + $store.state.Enemy.hero.heroImage)" alt="" class="cover">
                    <div class="health">{{ $store.state.Enemy.health }}</div>
                </div>
            </div>
            <!-- 回合按钮 -->
            <div class="turn_btn" ref="turn_btn" @touchstart="$refs.turn_btn.classList.add('click')"
            @touchend="$refs.turn_btn.classList.remove('click')"
            @click="TurnOut">
                <span v-show="$store.state.Pub.turn % 2 == 0">
                    {{ $store.state.Control.isFighting ? "战斗中" : "结束战斗"}}
                </span>
                <span v-show="$store.state.Pub.turn % 2 != 0">结束回合</span>
            </div>

            <!-- 你的英雄 -->
            <PlayerHero/>
        </div>
        

        <!-- 中部随从区域 -->
        <div class="middle">
            <!-- bob的随从 -->
            <div class="bob_row" ref="bob_minions" v-show="!$store.state.Control.hideBobMinions">
                
            </div>

            <!-- 我的随从 -->
            <div class="player_row" ref="player_minions" v-show="!$store.state.Control.hidePlayerMinions">
                
            </div>
        </div>

        <!-- 助手板块 -->
        <Assistant/>

        

        <!-- 铸币区 -->
        <div class="coin_tape" v-show="$store.state.Pub.turn % 2 != 0">
            <li v-for="c in $store.state.Player.coin" :key="c.index"></li>
        </div>

        <!-- 手牌选择的遮罩层 -->
        <div class="select_hand_card_mask" v-show="$store.state.Control.selectHandCardsView"
        @touchstart="SelectHandCard(false)">
        </div>

        <!-- 总的金币数 -->
        <div class="coin_manage">{{ $store.state.Player.coin }}</div>
        <!-- <div class="coin_manage">{{ handsCopy }}</div> -->


        <!-- 右下角手牌预览区 -->
        <div class="hand_card" @click="SelectHandCard(true)" ref="hand_card" v-show="!$store.state.Control.selectHandCardsView">
            <li v-for="(c) in $store.state.Player.handCardQueue" :key="c.index"
                ref="hand_cards">
                <img :src="c.img" alt="" class="minion_img" v-show="c.class == 'minion'">
                <img :src="c.img" alt="" class="spell_img" v-show="c.class == 'spell'">
                <img src="../assets/card/card (3).png" alt="" class="cover" v-show="!c.golden && c.class == 'minion'">
                <img src="../assets/card/card (4).png" alt="" class="cover" v-show="c.golden && c.class == 'minion'">
                <img src="../assets/card/card (10).png" alt="" class="cover" v-show="c.class == 'spell' && !c.golden">
                <img src="../assets/card/card (11).png" alt="" class="cover golden_frame" v-show="c.class == 'spell' && c.golden">
                <div class="golden_legend_icon" v-if="c.legend && c.golden"></div>
                <div class="legend_icon" v-if="c.legend && !c.golden"></div>
                <div class="star_bar" >
                    <img :src="StarBarImgUrl(c.star)" alt="" class="cover" v-show="c.class == 'minion'">
                    <div class="coin" v-if="c.class == 'spell'">{{ c.cost > 0 ? c.cost : null }}</div>
                </div>
                <div class="name" v-if="c.class == 'minion'">
                    <div class="text">{{ c.name }}</div>
                </div>
                <div class="spell_name" v-if="c.class == 'spell'">
                    <div class="text">{{ c.name }}</div>
                </div>
                <div class="blood" v-if="c.class == 'minion'">{{ c.hp }}</div>
                <div class="ad" v-if="c.class == 'minion'">{{ c.ad }}</div>
                <div class="describe" v-show="!c.golden">{{c.describe}}</div>
                <div class="golden_describe" v-show="c.golden">{{c.describe}}</div>
                <div class="spell_craft_mask" v-if="c.class == 'spell' && c.type == 'spellCraft'"></div>
            </li>
        </div>
        
        <!-- 手牌选择区 -->
        <div class="hand_card_select_area" ref="hand_card_select_area" v-show="$store.state.Control.selectHandCardsView">
        </div>

        <!-- 粒子效果层 -->
        <div class="special_mask" ref="specialMask" v-show="$store.state.Control.isFighting"></div>
        <!-- 战吼面板 -->
        <div class="battleCry_select_panel_mask" v-show="$store.state.Control.battleCrySelect" @click="CancelBattleCrySelect"></div>
        <!-- 发现选择面板 -->
        <div class="discover_mask" v-show="$store.state.Control.discover">
            <div class="card_container">
                <li v-for="c in $store.state.Player.discoverQueue" :key="c.id" @click="SelectDiscover(c)">
                    <div class="green_backlight_spell"></div>
                    <img :src="c.img" alt="" class="minion_img" v-show="c.class == 'minion'">
                    <img :src="c.img" alt="" class="spell_img" v-show="c.class == 'spell'">
                    <img src="../assets/card/card (3).png" alt="" class="cover" v-show="!c.golden && c.class == 'minion'">
                    <img src="../assets/card/card (4).png" alt="" class="cover" v-show="c.golden && c.class == 'minion'">
                    <img src="../assets/card/card (10).png" alt="" class="cover" v-show="c.class == 'spell' && !c.golden">
                    <img src="../assets/card/card (11).png" alt="" class="cover golden_frame" v-show="c.class == 'spell' && c.golden">
                    <div class="legend_icon" v-if="c.legend"></div>
                    <div class="star_bar" >
                        <img :src="StarBarImgUrl(c.star)" alt="" class="cover" v-show="c.class == 'minion'">
                        <div class="coin" v-if="c.class == 'spell'">{{ c.cost }}</div>
                    </div>
                    <div class="name" v-if="c.class == 'minion'">
                        <div class="text">{{ c.name }}</div>
                    </div>
                    <div class="spell_name" v-if="c.class == 'spell'">
                        <div class="text">{{ c.name }}</div>
                    </div>
                    <div class="blood" v-if="c.class == 'minion'">
                        <div class="number">{{ c.hp }}</div>
                    </div>
                    <div class="ad" v-if="c.class == 'minion'">
                        <div class="number">{{ c.ad }}</div>
                    </div>
                    <div class="describe" v-show="!c.golden">{{c.describe}}</div>
                    <div class="golden_describe" v-show="c.golden">{{c.describe}}</div>
                    <div class="type">{{ c.type == "混合" ? "全部" : c.type }}</div>
                </li>
            </div>
            
        </div>
    </div>
</template>

<script>
import Card from '../mixin/data/card.js'
//导入类
import IQuery from '../mixin/function/iquery.js'
import AnimationS from '../mixin/function/animation.js';
import CommonS from '../mixin/function/commons.js';
import InitPub from '../mixin/function/init';
import Discover from '../mixin/function/discover.js'
import TurnControl from '../mixin/function/turncontrol';
//导入小的部件
import PlayerHero from './part/PlayerHero.vue';
import Assistant from './part/Assistant.vue';
export default {
    name : 'Pub',
    components:{
        PlayerHero,
        Assistant,
    },
    data(){
        return{
            t : false,
            //查询体
            q : new IQuery(this),
            //公共体
            com : new CommonS(this),
            //东话题
            ams : new AnimationS(this),
            //玩家手牌长度
            hands : this.$store.state.Player.handCardQueue,
            //玩家的随从

        }
    },
    
    methods:{
        //冻结酒馆随从
        FreezeMinions(){
            //检查场上bob随从有没有被冻结的情况
            let freezeLock = true;
            for(let i = 0;i<this.$store.state.Pub.bobMinionsQueue.length;i++){
                if(!this.$store.state.Pub.bobMinionsQueue[i].isFreeze){
                    //有随从没有被冻结
                    freezeLock = false;
                    break;
                }
            }
            let bobM_nodes = document.querySelectorAll('.bob_row li');
            //冻结
            if(!freezeLock){
                for(let i = 0;i<bobM_nodes.length;i++){
                    if(!this.$store.state.Pub.bobMinionsQueue[i].isFreeze){
                        this.$store.state.Pub.bobMinionsQueue[i].isFreeze = true;
                        for(let j=0;j<bobM_nodes[i].children.length;j++){
                            if(bobM_nodes[i].children[j].className == 'ad'){
                                let freezeMask = document.createElement('div');
                                freezeMask.classList.add('freeze_mask');
                                bobM_nodes[i].insertBefore(freezeMask,bobM_nodes[i].children[j]);
                                break;
                            }
                        }
                    }
                }
            }
            //解冻
            else{
                for(let i = 0;i<bobM_nodes.length;i++){
                    this.$store.state.Pub.bobMinionsQueue[i].isFreeze = false;
                    for(let j=bobM_nodes[i].children.length-1;j>=0;j--){
                        if(bobM_nodes[i].children[j].className == 'freeze_mask'){
                            bobM_nodes[i].children[j].remove();
                        }
                    }
                }
            }
            
        },
        //选择发现的随从
        async SelectDiscover(data){
            let dis = new Discover(this);
            await dis.SelectDiscoverRes(data);
        },
        //节点点击效果
        async NodeClick(node){
            node.classList.add('click');
            await this.ams.Delay(50);
            node.classList.remove('click');
        },
        //取消目标选择
        async CancelBattleCrySelect(){
            //清除高亮
            let bobNodes = document.querySelectorAll('.bob_row li');
            let playerNodes = document.querySelectorAll('.player_row li');
            
            for(let i=0;i<bobNodes.length;i++){
                bobNodes[i].classList.remove('highlight');
            }
            for(let i=0;i<playerNodes.length;i++){
                playerNodes[i].classList.remove('highlight');
            }

            if(this.$store.state.Player.battleCryMounted.isSpellCast){
                let handsNodes = document.querySelectorAll('.hand_card_select_area li');
                for(let i = 0;i<handsNodes.length;i++){
                    console.log(handsNodes[i]);
                    handsNodes[i].style.zIndex = '0';
                    handsNodes[i].style.opacity = '1';
                }
            }
            
            //收回手牌
            this.com.AdjustCardCoordinate(1);
            this.ams.ControlMinionsTransition('player',200,true);
            this.ams.ControlMinionsTransition('bob',200,true);
            this.ams.ControlMinionsTransition('hands',200,true);
            await this.ams.Delay(200);
            this.ams.ControlMinionsTransition('player',200,false);
            this.ams.ControlMinionsTransition('bob',200,false);
            this.ams.ControlMinionsTransition('hands',200,false);
            //状态重置
            this.$store.state.Player.battleCryMounted = {
                //战吼随从目标的DOM
                battleCryNode : null,
                //战吼随从的数组索引
                battleCryIndex : -1,
                //战吼随从的执行块
                executeBlock : null,
                //是否处于法术执行体
                isSpellCast : false,
            }
            //关闭战吼面板
            this.$store.state.Control.battleCrySelect = false;

        },  
        //打开手牌区
        async SelectHandCard(open){
            //手牌是空的则不会打开选择手牌面板
            if(this.$store.state.Player.handCardQueue.length == 0){
                return false;
            }
            this.$store.state.Control.selectHandCardsView = open;
            open ? this.com.AdjustCardCoordinate(1) : this.com.AdjustCardCoordinate(0);
        },
        //升级酒馆
        async UpPubLevel(){
            let Player = this.$store.state.Player;
            //有足够的的铸币
            if(Player.coin >= this.$store.state.Pub.upCost){
                this.$store.state.Pub.level += 1;
                this.$store.state.Player.coin -= this.$store.state.Pub.upCost;
                //调用场上购买随从的函数
                for(let i in this.$store.state.Player.minionsQueue){
                    await this.$store.state.Player.minionsQueue[i].TriggerCostCoin(this.$store.state.Pub.upCost);
                }
                //重置升级酒馆所需的铸币
                this.$store.state.Pub.upCost = this.$store.state.Pub.level + 5;
                //调用酒馆升级后随从事件
                for(let i in Player.minionsQueue){
                    Player.minionsQueue[i].TriggerUpgradePub();
                }
            }
            
        },  
        //刷新酒馆随从
        RefreshMinions(cost = false,limit = null){
            //limit限制刷新的随从种类
            let Player = this.$store.state.Player;
            let Pub = this.$store.state.Pub;
            let a = new AnimationS(this);
            let waitFreeze = this.$store.state.Pub.freezeMinionsQueue.length > 0 ? true : false;
            return new Promise(async (resolve,reject)=>{
                if(this.$store.state.Control.limitRefreshBtn) return;
                if(cost){
                    //如果玩家铸币小于刷新酒馆需要铸币则不能刷新
                    if(Player.coin < this.RealRefreshCost()){
                        resolve(true);
                        return false;
                    }
                    Player.coin -= this.RealRefreshCost();
                    //调用场上随从花费铸币的函数
                    for(let i in this.$store.state.Player.minionsQueue){
                        this.$store.state.Player.minionsQueue[i].TriggerCostCoin(this.RealRefreshCost());
                    }
                    Pub.refreshCost.zeroCostTime > 0 ? Pub.refreshCost.zeroCostTime -= 1 : Pub.refreshCost.zeroCostTime = 0;
                    Pub.refreshCost.twoCostTime > 0 ? Pub.refreshCost.twoCostTime -= 1 : Pub.refreshCost.twoCostTime = 0;
                }
                this.$store.state.Control.limitRefreshBtn = true;
                this.$store.state.Control.hideBobMinions = true;//暂时隐藏酒馆随从            

                this.$store.state.Pub.bobMinionsQueue = [];
                let lis = document.querySelectorAll('.bob_row li');
                for(let i =lis.length-1;i>=0;i--) lis[i].remove();

                //可刷新出的随从个数
                let getNumber = 3 + Math.floor(this.$store.state.Pub.level/2) - this.$store.state.Pub.freezeMinionsQueue.length;
                //选出符合等级的随从
                let indexArr = [];//保存符合条件的随从索引
                for(let i=0;i<Pub.allMinions.length;i++){
                    if(Pub.allMinions[i].star <= Pub.level && (limit == null ? true : this.q.IsRepeatType(limit,Pub.allMinions[i].type))){
                        switch(Pub.allMinions[i].star){
                            case 1 : for(let j=0;j<3;j++) indexArr.push(i); break;
                            case 2 : for(let j=0;j<3;j++) indexArr.push(i); break;
                            case 3 : for(let j=0;j<2;j++) indexArr.push(i); break;
                            case 4 : for(let j=0;j<2;j++) indexArr.push(i); break;
                            case 5 : for(let j=0;j<1;j++) indexArr.push(i); break;
                            case 6 : for(let j=0;j<1;j++) indexArr.push(i); break;
                        }
                    }
                }
                //恢复被冻结的随从
                for(let i =0;i<this.$store.state.Pub.freezeMinionsQueue.length;i++){
                    let newMinion = new Card(this,"minion");
                    newMinion.LoadingAttr(this.$store.state.Pub.freezeMinionsQueue[i],true,-1);
                    newMinion.load(this,newMinion);
                    Pub.bobMinionsQueue.push(newMinion);
                    //更新DOM
                    let minion = this.com.CreateMinionDom(newMinion,true);
                    this.$refs.bob_minions.appendChild(minion);
                    for(let j in minion.children){
                        if(minion.children[j].className == 'ad'){
                            let freezeMask = document.createElement('div');
                            freezeMask.classList.add('freeze_mask');
                            minion.insertBefore(freezeMask,minion.children[j]);
                            freezeMask.style.transition = 'all .3s';

                            setTimeout(async ()=>{
                                freezeMask.style.transform = 'scale(1.5)';
                                freezeMask.style.opacity = '0';
                                await this.ams.Delay(300);
                                freezeMask.remove();
                                if(waitFreeze) resolve(true);
                                return;
                            },600);
                            break;
                        }
                    }
                }
                this.$store.state.Pub.freezeMinionsQueue = [];//恢复结束清除
                for(let i=0;i<getNumber;i++){
                    let random = Math.floor(Math.random()*indexArr.length);
                    let newMinion = new Card(this,"minion");
                    newMinion.LoadingAttr(Pub.allMinions[indexArr[random]],true,-1);
                    newMinion.load(this,newMinion);
                    //入队bob随从队列,数据实体
                    Pub.bobMinionsQueue.push(newMinion);
                    //更新DOM
                    let minion = this.com.CreateMinionDom(newMinion,true);
                    this.$refs.bob_minions.appendChild(minion);
                    indexArr.splice(random,1);
                    
                }
                this.$store.state.Control.hideBobMinions = false;
                //整理dom实体的物理位置
                this.com.AdjustMinionsCoordinate(true);
                await a.RefreshAnimation();
                await a.Delay(100);
                this.$store.state.Control.limitRefreshBtn = false;
                if(!waitFreeze) resolve(true);
            })
        },
        //回合结束
        async TurnOut(){
            //在战斗中是不可以进行回合结束操作的
            if(this.$store.state.Control.isFighting) return;
            let tc = new TurnControl(this);
            //当前回合数为偶数，表示当前回合为战斗回合
            if(this.$store.state.Pub.turn % 2 == 0) tc.TurnBegin();
            //当前回合数为奇数，表示当前回合为招募回合
            else tc.TurnOver();
        },
        //返回酒馆等级logo
        StarBarImgUrl(star){
            if(star == 1) return require("../assets/ui/ui (11).png");
            else if(star == 2) return require("../assets/ui/ui (15).png");
            else if(star == 3) return require("../assets/ui/ui (16).png");
            else if(star == 4) return require("../assets/ui/ui (17).png");
            else if(star == 5) return require("../assets/ui/ui (18).png");
            else if(star == 6) return require("../assets/ui/ui (19).png");
        },
        //返回酒馆的实际刷新消耗
        RealRefreshCost(){
            if(this.$store.state.Pub.refreshCost.zeroCostTime > 0){
                return 0;
            }
            else if(this.$store.state.Pub.refreshCost.twoCostTime > 0){
                return 2;
            }
            return this.$store.state.Pub.refreshCost.base;
        },
        //载入随从,把所有随从加载到酒馆随从池子中
        Init(){
            return new Promise(async(resolve,reject)=>{
                let init = new InitPub(this);
                //获得随从禁用情况
                let res = init.GetTypeOfMinions(this.$store.state.Control.editionItem.banNumber);
                this.$store.state.Pub.banArr = res.ban;
                this.$store.state.Pub.alowArr = res.arr;
                await init.InitAllMinions();
                await this.ams.Delay(300);

                //获得人机的难度阵容表
                this.$store.state.Enemy.GetTeam();
                this.RefreshMinions();
                if(this.$store.state.Player.minionsQueue.length != 0){
                    this.com.AdjustMinionsCoordinate(false);
                }
                //调用英雄的开局函数
                this.$store.state.Player.hero.GameBegin();
                resolve(true);
            })
        },
    },
    watch:{
        "hands" :{
            async handler(){
                //调用手牌插入随从的效果
                for(let i=0;i<this.$store.state.Player.minionsQueue.length;i++){
                    if(this.$store.state.Control.isJoinHands){
                        this.$store.state.Player.minionsQueue[i].TriggerJoinHands();
                    }   
                }
                //执行等待手牌进入手牌队列
                if(this.$store.state.Player.handCardQueue.length < 10 && this.$store.state.Player.waitingQueue.length > 0 && !this.$store.state.Control.discover){
                    for(let i = this.$store.state.Player.waitingQueue.length-1;i >= 0;i--){
                        let temp = this.$store.state.Player.waitingQueue.pop();
                        this.$store.state.Player.handCardQueue.push(temp);
                        let cardNode = this.com.CreateNewHandCard(temp,temp.class);
                        this.$refs.hand_card_select_area.appendChild(cardNode);
                        this.com.AdjustCardCoordinate(0)
                        this.com.AdjustCardCoordinate(1)
                        await this.ams.TransitionEffect(false,false,true);
                        if(this.$store.state.Player.handCardQueue.length == 10) break;
                    }
                }
                this.$store.state.Control.isJoinHands = false;
            },
            deep : true,
        }
        
    },
    async mounted(){
        this.$store.state.vue = this;
        //修改英雄对象上vue的实例
        this.$store.state.Player.hero.vue = this;
        //将该组件实例加载上Enemy
        this.$store.state.Enemy.GetVueComponents(this);
        //载入所有随从
        await this.Init();
        
        
        

        
        

    },

}
</script>

<style lang="less">
@import url('../assets/css/pub.css');
.a{
    color: rgb(170, 0, 0);
}
</style>