        img : require('../../assets/minion/海盗/min (1).png'),
        version : [20210505,20220511,20220831],//野猪人版本
        //填装函数
        load(that,target){
            //that是vue的实例对象
            target.nature.death_rattle = true;
            let calling = function(data){
                let com = new Commons(that);
                let q = new IQuery(that);
                let entity = data.battleEntity,deadEntity = data.deadEntity,index = data.deadIndex
                let summonTime = q.SummonTimes(target.battleAttr.belongPlayer,true,target.battleAttr.driver)
                let summonOne = {
                    name : '空中海盗',
                    ad : target.golden ? 2 : 1,
                    hp : target.golden ? 2 : 1,
                    type : '海盗',
                    star : 1,
                    describe : '',
                    golden_describe : '',
                    img : require('../../assets/minion/海盗/min (19).png'),
                    version : [20210505,20220511,20220831],//野猪人版本
                    load(vue,tgt){
                    }
                }
                return new Promise(async (resolve,reject)=>{
                    for(let i =0;i<summonTime;i++){
                        if(deadEntity.battleAttr.belongPlayer ? entity.PlayerMinions.length >= 7 : entity.EnemyMinions.length >= 7){
                            resolve(true);
                            return;
                        }
                        //召唤一个随从
                        let res = await com.SummonMinionsInFighting(summonOne,target,deadEntity.battleAttr.belongPlayer,entity,index,true,true,false);
                        //立即攻击
                        if(!await entity.Action(deadEntity.battleAttr.belongPlayer,true,res)){
                            resolve(true);
                            return;
                        }
                    }
                    resolve(true);
                })
                
            }
            let block = new Block("death_rattle",target,'minion',null,calling)
            target.AfterDead.push(block);
            
        }
