        legend : false,
        type : '龙',
        star : 1,
        describe : '战斗开始时：随机对一个敌方随从造成等同于友方龙的数量的伤害',
        golden_describe : '战斗开始时：随机对一个敌方随从造成等同于友方龙的数量*2的伤害',
        img : require('../../assets/minion/龙/min (2).png'),
        version : [20210505,20220511,20220831],//野猪人版本
        load(that,target){
            let func = function(){
                let sp = new Special(that);
                let ams = new AnimationS(that);
                let dm = new DriverMethods(that,target.battleAttr.driver);
                let friT = target.battleAttr.belongPlayer ? target.battleAttr.driver.PlayerMinions : target.battleAttr.driver.EnemyMinions;
                let enemyT = target.battleAttr.belongPlayer ? target.battleAttr.driver.EnemyMinions : target.battleAttr.driver.PlayerMinions
                let dragonNumber = 0;
                return new Promise(async (resolve,reject)=>{
                    if(enemyT.length == 0){
                        resolve(true);
                        return;
                    }
                    let selfNode= null;
                    for(let i in friT){
                        if(friT[i].type == '龙' || friT[i].type == '混合') dragonNumber += 1;
                        if(friT[i] == target) selfNode = target.battleAttr.belongPlayer ? document.querySelectorAll('.player_row li')[i] : document.querySelectorAll('.bob_row li')[i];
                    }
                    let damage = target.golden ? 2 * dragonNumber : dragonNumber;
                    let rand = Math.floor(Math.random()*enemyT.length);
                    let query = new IQuery(that);
                    let tgt = enemyT[rand];
                    let tgtNode = query.GetTargetAndNode(!target.battleAttr.belongPlayer,true,target.battleAttr.driver,tgt).nodes;
                    await sp.SpecialSpurtFire(selfNode.offsetLeft,selfNode.offsetTop,tgtNode.offsetLeft,tgtNode.offsetTop);
                    if(!tgt.battleAttr.divine_shield){
                        ams.MinionGetDamage(tgtNode,damage,'huge',1000);
                        await ams.AttrReduceAnimate(damage,tgt,tgtNode,null,target);
                        await dm.DeathDeterminate(!target.battleAttr.belongPlayer);   
                    }
                    else{
                        tgt.battleAttr.divine_shield = false;
                        await ams.LostDivineShield(tgtNode,tgt);
                    }
                    await ams.Delay(200);
                    resolve(true);
                })
            }

            let block = new Block("battleBegin",target,'minion',null,func)
            target.BattleBegin.push(block);
        }
