import Card from "../data/card";
import AnimationS from "./animation";
import Commons from "./commons";
import DriverMethods from "./driverMethods";
import Special from "./special";
//对战驱动
class BattleDriver{
    constructor(vue){
        //组件实例
        this.vue = vue;
        //屏幕的宽度
        this.screenW = vue.$store.state.ScreenSize.width;
        //屏幕的高度
        this.screenH = vue.$store.state.ScreenSize.height;
        //玩家随从队列
        this.PlayerMinions = [];
        //对手随从队列
        this.EnemyMinions = [];
        //挂载驱动器方法
        this.methods = new DriverMethods(vue,this);

        //挂载公用体
        this.com = null;
        //挂载动画体
        this.ams = null;
    }
    //开始函数
    async Start(){
        
        //初始化
        this.Init();
        this.ams = new AnimationS(this.vue);
        this.com = new Commons(this.vue);
        this.ams.Delay(500);
        //开始战斗准备阶段
        let playerFirst = Math.floor(Math.random()*2);
        if(playerFirst){
            for(let i in this.PlayerMinions) await this.PlayerMinions[i].TriggerPrepareFight();
            for(let i in this.EnemyMinions) await this.EnemyMinions[i].TriggerPrepareFight();
        }
        else{
            for(let i in this.EnemyMinions) await this.EnemyMinions[i].TriggerPrepareFight();
            for(let i in this.PlayerMinions) await this.PlayerMinions[i].TriggerPrepareFight();
        }
        
        //开始战斗
        await this.BeginBattle();
        
        //战斗结束
        this.vue.$store.state.Control.isFighting = false;
    }
    //初始化
    Init(){
        let PlayerM = this.vue.$store.state.Player.minionsQueue;
        let EnemyM = this.vue.$store.state.Enemy.minionsQueue;
        for(let i=0;i<PlayerM.length;i++){
            let card = new Card(this.vue,'minion');
            card.LoadingAttr(PlayerM[i],true,-1);
            card.load(this.vue,card);
            card['battleAttr'] = {
                ['driver'] : this,
                ['originIndex'] : i,//对应战斗前的随从索引
                ['belongPlayer'] : true,
                ['ad'] : PlayerM[i].RealAttack(),
                ['hp'] : PlayerM[i].RealHealth(),
                ['originAd'] : PlayerM[i].RealAttack(),
                ['originHp'] : PlayerM[i].RealHealth(),
                ['attacked'] : false,//是否已经攻击过
                //特性
                ['death_rattle'] : PlayerM[i].nature.death_rattle,
                ['divine_shield'] : PlayerM[i].nature.divine_shield || PlayerM[i].temp_nature.divine_shield, 
                ['poisonous'] : PlayerM[i].nature.poisonous || PlayerM[i].temp_nature.poisonous,
                ['reborn'] : PlayerM[i].nature.reborn || PlayerM[i].temp_nature.reborn,
                ['taunt'] : PlayerM[i].nature.taunt || PlayerM[i].temp_nature.taunt,
                ['wind_fury'] : PlayerM[i].nature.wind_fury || PlayerM[i].temp_nature.wind_fury,
                ['avenge']  : PlayerM[i].nature.avenge,
                ['passive'] : PlayerM[i].nature.passive,
                ['frenzy'] :PlayerM[i].nature.frenzy,
                ['aoe'] : PlayerM[i].nature.aoe,
            }
            console.log('dwa',card.battleAttr.reborn);
            //数据实体
            this.PlayerMinions.push(card);
        }
        for(let i=0;i < EnemyM.length;i++){
            let card = new Card(this.vue,'minion');
            card.LoadingAttr(EnemyM[i],true,-1);
            card.load(this.vue,card);
            card['battleAttr'] = {
                ['driver'] : this,
                ['originIndex'] : i,//对应战斗前的随从索引
                ['belongPlayer'] : false,
                ['ad'] : EnemyM[i].RealAttack(),
                ['hp'] : EnemyM[i].RealHealth(),
                ['originAd'] : EnemyM[i].RealAttack(),
                ['originHp'] : EnemyM[i].RealHealth(),
                ['attacked'] : false,//是否已经攻击过
                //特性
                ['death_rattle'] : EnemyM[i].nature.death_rattle,
                ['divine_shield'] : EnemyM[i].nature.divine_shield || EnemyM[i].temp_nature.divine_shield, 
                ['poisonous'] : EnemyM[i].nature.poisonous || EnemyM[i].temp_nature.poisonous,
                ['reborn'] : EnemyM[i].nature.reborn || EnemyM[i].temp_nature.reborn,
                ['taunt'] : EnemyM[i].nature.taunt || EnemyM[i].temp_nature.taunt,
                ['wind_fury'] : EnemyM[i].nature.wind_fury || EnemyM[i].temp_nature.wind_fury,
                ['avenge']  : EnemyM[i].nature.avenge,
                ['passive'] : EnemyM[i].nature.passive,
                ['frenzy'] :EnemyM[i].nature.frenzy,
                ['aoe'] : EnemyM[i].nature.aoe,
            }
            //数据实体
            this.EnemyMinions.push(card);
        }
    }

    //进行战斗
    async BeginBattle(){
        let maxTimes = 50;//战斗次数最大值
        
        //先手判断
        let playFirst = this.IsPlayerFirst();
        while(maxTimes > 0){
            if(!await this.Action(playFirst)) return;
            if(!await this.Action(!playFirst)) return;
            maxTimes --;
        }
    }
    
    //判断先手
    IsPlayerFirst(){
        if(this.PlayerMinions.length < this.EnemyMinions.length) return false;
        else if(this.PlayerMinions.length > this.EnemyMinions.length) return true;
        else{
            let random = Math.floor(Math.random()*2);
            return random == 0 ? true : false;
        }
    }
    //选择攻击者
    SelectAttacker(isPlayer){
        for(let i in (isPlayer ? this.PlayerMinions : this.EnemyMinions)){
            let target = isPlayer ? this.PlayerMinions[i] : this.EnemyMinions[i]
            if(!target.battleAttr.attacked && target.battleAttr.ad > 0) return target;
        }
        //表示所以随从都进行过攻击了
        if( isPlayer ? this.PlayerMinions.length > 0 : this.EnemyMinions.length > 0){
            //说明该队伍一轮攻击结束
            for(let i in (isPlayer ? this.PlayerMinions : this.EnemyMinions)){
                let target = isPlayer ? this.PlayerMinions[i] : this.EnemyMinions[i];
                target.battleAttr.attacked = false;
            }
            return isPlayer ? this.PlayerMinions[0] : this.EnemyMinions[0];
        }
        return false;
    }
    //选择被攻击者
    SelectAttacked(isPlayer){
        //从嘲讽中选
        let tauntIndexArr = [];
        if(isPlayer){
            for(let i in this.PlayerMinions){
                if(this.PlayerMinions[i].battleAttr.taunt){
                    tauntIndexArr.push(i);
                }
            }
        }
        else{
            for(let i in this.EnemyMinions){
                if(this.EnemyMinions[i].battleAttr.taunt){
                    tauntIndexArr.push(i);
                }
            }
        }
        //得到嘲讽随从的索引数组
        if(tauntIndexArr.length == 0){
            if(isPlayer && this.PlayerMinions.length == 0) return false;
            if(!isPlayer && this.EnemyMinions.length == 0) return false;
            return isPlayer? this.PlayerMinions[Math.floor(Math.random()*this.PlayerMinions.length)] : this.EnemyMinions[Math.floor(Math.random()*this.EnemyMinions.length)]
        }
        else{
            return isPlayer ? this.PlayerMinions[tauntIndexArr[Math.floor(Math.random()*tauntIndexArr.length)]] : this.EnemyMinions[tauntIndexArr[Math.floor(Math.random()*tauntIndexArr.length)]]
        }
        
    }
    //选择一个扎普斯里维克的攻击对象
    SelectZapslivikAttaked(isPlayer){
        let tgtTeam = isPlayer ? this.PlayerMinions : this.EnemyMinions;
        if(tgtTeam.length == 0) return false;
        let lowest = tgtTeam[0];
        for(let i =0;i < tgtTeam.length;i++){
            if(tgtTeam[i].battleAttr.ad < lowest.battleAttr.ad) lowest = tgtTeam[i]
            else if(tgtTeam[i].battleAttr.ad == lowest.battleAttr.ad){
                let rand = Math.floor(Math.random()*2);
                if(rand == 1) lowest = tgtTeam[i];
            }
        }
        //最终选择目标
        return lowest;
    }
    //一方行动
    Action(isPlayer,immediate = false,target = null){
        let attacker = null;
        let receiver = null;
        return new Promise(async (resolve,reject)=>{
            attacker = !immediate ? this.SelectAttacker(isPlayer) : target;
            //如果攻击者是猎马人
            if(attacker.name == '扎普斯里维克') receiver = this.SelectZapslivikAttaked(!isPlayer);
            else receiver = this.SelectAttacked(!isPlayer);
            // console.log(`【${attacker.name}】对【${receiver.name}】发起攻击`)
            if(!attacker || !receiver){
                console.log('over',attacker,receiver);
                resolve(false);
            }
            else{
                //风怒
                let attackTimes = 1;
                if(attacker.battleAttr.wind_fury && !attacker.golden) attackTimes = 2;
                else if(attacker.battleAttr.wind_fury && attacker.golden) attackTimes =  4;
                for(let i = 0;i<attackTimes;i++){
                    if(attacker.battleAttr.hp > 0) await this.Attack(attacker,receiver);
                    if(attacker.name == '扎普斯里维克') receiver = this.SelectZapslivikAttaked(!isPlayer);
                    else receiver = this.SelectAttacked(!isPlayer);
                    if(!attacker || !receiver){
                        console.log('over',attacker,receiver);
                        resolve(false);
                        return;
                    }
                }
                attacker.battleAttr.attacked = true;
                
                resolve(true);
            }
        })
    }
    
    //随从发动一次攻击
    Attack(attacker,receiver){
        let attackerTeam = attacker.battleAttr.belongPlayer ? this.PlayerMinions : this.EnemyMinions//攻击者所在的队伍
        let receiverTeam = receiver.battleAttr.belongPlayer ? this.PlayerMinions : this.EnemyMinions
        let attNodeArr = attacker.battleAttr.belongPlayer ? document.querySelectorAll('.player_row li') : document.querySelectorAll('.bob_row li');
        let recNodeArr = receiver.battleAttr.belongPlayer ? document.querySelectorAll('.player_row li') : document.querySelectorAll('.bob_row li');
        //攻击者和被攻击者的DOM结点，不可能为null
        let attNode = null,attBloodNode = null,attAdNode = null;
        let recNode = null,recBloodNode = null,recAdNode = null;
        //攻击者和被攻击者是否拥有圣盾
        let attDivineShield = attacker.battleAttr.divine_shield;
        let recDivineShield = receiver.battleAttr.divine_shield;
        return new Promise(async (resolve,reject)=>{

            for(let i  in attNodeArr){
                if(attacker == (attacker.battleAttr.belongPlayer ? this.PlayerMinions[i] : this.EnemyMinions[i])){
                    attNode = attNodeArr[i];
                    for(let j in attNode.children){
                        if(attNode.children[j].className == 'blood') attBloodNode = attNode.children[j].children[0];
                        else if(attNode.children[j].className == 'ad') attAdNode = attNode.children[j].children[0];
                    }
                    break;
                }
            }
            for(let i in recNodeArr){
                if(receiver == (receiver.battleAttr.belongPlayer ? this.PlayerMinions[i] : this.EnemyMinions[i])){
                    recNode = recNodeArr[i];
                    for(let j in recNode.children){
                        if(recNode.children[j].className == 'blood') recBloodNode = recNode.children[j].children[0];
                        else if(recNode.children[j].className == 'ad') recAdNode = recNode.children[j].children[0];
                    }
                    break;
                }
            }
            //攻击
            await this.methods.MinionAttack(attNode,attacker,recNode,receiver,attackerTeam,receiverTeam,attBloodNode,recBloodNode);
            this.com.AdjustMinionsCoordinate(true)
            this.com.AdjustMinionsCoordinate(false)
            await this.ams.Delay(10);
            //触发随从的范围伤害
            if(attacker.battleAttr.aoe){
                for(let i =0;i<receiverTeam.length;i++){
                    if(receiverTeam[i] == receiver){
                        if(i > 0){
                            let node = receiver.battleAttr.belongPlayer ? document.querySelectorAll('.player_row li')[i-1] : document.querySelectorAll('.bob_row li')[i-1];
                            if(receiverTeam[i-1].battleAttr.divine_shield){
                                receiverTeam[i-1].battleAttr.divine_shield = false;
                                this.ams.LostDivineShield(node,receiverTeam[i-1]);
                            }
                            else{
                                this.ams.MinionGetDamage(node,attacker.battleAttr.ad,'normal',1000);
                                await this.ams.AttrReduceAnimate(attacker.battleAttr.ad,receiverTeam[i-1],node,null,attacker);
                            }
                        }
                        if(i < receiverTeam.length-1){
                            let node = receiver.battleAttr.belongPlayer ? document.querySelectorAll('.player_row li')[i+1] : document.querySelectorAll('.bob_row li')[i+1];
                            if(receiverTeam[i+1].battleAttr.divine_shield){
                                receiverTeam[i+1].battleAttr.divine_shield = false;
                                this.ams.LostDivineShield(node,receiverTeam[i+1]);
                            }
                            else{
                                this.ams.MinionGetDamage(node,attacker.battleAttr.ad,'normal',1000);
                                await this.ams.AttrReduceAnimate(attacker.battleAttr.ad,receiverTeam[i+1],node,null,attacker);
                            }
                        }
    
                    }
                }
            }
            //调用攻击后立即调用攻击者的攻击后触发函数
            for(let i in attackerTeam){
                await attackerTeam[i].TriggerAfterAttack(attacker,receiver,this);
            }
            
            
            
            // //先调用受到攻击后调用的函数,先调用被攻击者，后调用攻击者
            // if(attacker.battleAttr.ad > 0 && !recDivineShield) await receiver.TriggerGetHurt(attacker,attacker.ad,this);
            // if(receiver.battleAttr.ad > 0 && !attDivineShield) await attacker.TriggerGetHurt(receiver,receiver.ad,this);
            //死亡判定(被攻击者的亡语优先触发)
            await this.methods.DeathDeterminate(receiver.battleAttr.belongPlayer);
            resolve(true);
        })

    }
    
    
    //触发圣盾被解除的函数


    
}

export default BattleDriver;