import AnimationS from "./animation";
import Commons from "./commons";

//清除类
class ClearData{
    constructor(vue){
        this.vue = vue;
        this.screenW = this.vue.$store.state.ScreenSize.width;
        this.screenH = this.vue.$store.state.ScreenSize.height;
    }
    //清除战斗后残余结点
    ClearRemainNode(){
        let playerMinions = document.querySelectorAll('.player_row li');
        let enemyMinions = document.querySelectorAll('.bob_row li');
        //清除玩家的残余结点
        for(let i=playerMinions.length - 1;i>= 0;i--){
            playerMinions[i].remove();
        }
        //清除敌对的残余
        for(let i=enemyMinions.length-1;i>=0;i--){
            enemyMinions[i].remove();
        }
        return true;
    }
    //清除临时的加成
    ClearTempAddition(){
        let Player = this.vue.$store.state.Player;
        let pmn = document.querySelectorAll('.player_row li');
        let ams = new AnimationS(this.vue);
        //清除有实效的加成效果
        for(let i = 0;i<Player.minionsQueue.length;i++){
            //清除加成效果
            for(let k = Player.minionsQueue[i].additionContainer.length-1;k>=0;k--){
                if(Player.minionsQueue[i].additionContainer[k].remain != null){
                    Player.minionsQueue[i].additionContainer[k].remain > 1 ?  Player.minionsQueue[i].additionContainer[k].remain -= 1 : Player.minionsQueue[i].additionContainer.splice(k,1);
                    ams.AttrAdditionAnimate(pmn[i],Player.minionsQueue[i],false,false,false);
                }
            }
            //清除圣盾
            for(let j = pmn[i].children.length-1;j>=0;j--){
                
                if(pmn[i].children[j].className == 'divine_shield' && Player.minionsQueue[i].temp_nature.divine_shield){
                    Player.minionsQueue[i].temp_nature.divine_shield = false;
                    pmn[i].children[j].remove();
                    break;
                }
            }
            //清除风怒
            for(let j = pmn[i].children.length-1;j>=0;j--){
                if(pmn[i].children[j].className == 'wind_fury_mask' && Player.minionsQueue[i].temp_nature.wind_fury){
                    Player.minionsQueue[i].temp_nature.wind_fury = false;
                    pmn[i].children[j].remove();
                    break;
                }
            }
            //清除复生
            for(let j in pmn[i].children){
                if(pmn[i].children[j].className == 'reborn' && Player.minionsQueue[i].temp_nature.reborn){
                    Player.minionsQueue[i].temp_nature.reborn = false;
                    pmn[i].children[j].remove();
                    break;
                }
            }
            //清除剧毒
            for(let j = pmn[i].children.length-1;j>=0;j--){
                if(pmn[i].children[j].className == 'poisonous' && Player.minionsQueue[i].temp_nature.poisonous){
                    Player.minionsQueue[i].temp_nature.poisonous = false;
                    pmn[i].children[j].remove();
                    break;
                }
            }
            //嘲讽
            for(let j in pmn[i].children){
                if((pmn[i].children[j].className == 'taunt' || pmn[i].children[j].className == 'golden_taunt' )&& Player.minionsQueue[i].temp_nature.taunt){
                    Player.minionsQueue[i].temp_nature.taunt = false;
                    pmn[i].children[j].remove();
                }
            }
                
                
                
                
                
                

            
        }
        return true;
    }
    //清除手牌中已经过期的卡牌(招募回合结束时就清除)
    ClearOverdueCard(){
        let hands = this.vue.$store.state.Player.handCardQueue;
        let ams = new AnimationS(this.vue);
        return new Promise(async (resolve,reject)=>{
            let openView = false;
            for(let i = hands.length-1;i>=0;i--){
                if(hands[i].duration != null && hands[i].duration > -1) {
                    if(hands[i].duration > 1) hands[i].duration - 1;
                    else openView = true;
                }
            }
            //没有需要删除的
            if(!openView){
                resolve(true);
                return;
            }
            this.vue.$store.state.Control.selectHandCardsView = true;
            await ams.Delay(500);
            let handNodes = document.querySelectorAll('.hand_card_select_area li');
            for(let i = hands.length-1;i>=0;i--){
                if(hands[i].duration != null && hands[i].duration > -1 && hands[i].duration <= 1){
                    //先删除DOM结点
                    handNodes[i].style.transition  = 'all .3s';
                    handNodes[i].style.marginTop =  '0';
                    handNodes[i].style.opacity = '1'
                    await ams.Delay(1);
                    handNodes[i].style.marginTop = - this.screenW * 0.09 + 'px';
                    handNodes[i].style.opacity = '0';
                    await ams.Delay(350);
                    //DOM移除
                    handNodes[i].remove();
                    //数据移除
                    hands.splice(i,1);
                }
                
            }
            let com = new Commons(this.vue);
            com.AdjustCardCoordinate(0);
            com.AdjustCardCoordinate(1);
            await ams.TransitionEffect(false,false,true,200);
            await ams.Delay(500);
            this.vue.$store.state.Control.selectHandCardsView = false;
            resolve(true);
        })
    }
}


export default ClearData;