class Statistics {
    constructor(){
        //当前回合内的统计
        this.currentTurn = {
            purchaseMinions : [],//购买的随从

        }
        //刷新次数
        this.refreshTimes = 0;
        //购买次数
        this.purchaseTimes = 0;
        //出售次数
        this.saleTimes = 0;
        //消耗铸币
        this.cost = 0;
                
        
    }
    //购买随从
    Purchase(dataItem,cost){
        let newCard = {
            star : dataItem.star,
            name : dataItem.name,
            type : dataItem.type,
        }
        this.purchaseMinions.push(newCard);
        this.purchaseTimes += 1;
        this.cost = cost;
    }
    //清除本回合的统计
    ClearCurrentStatistics(){
        this.currentTurn.purchaseMinions = [];
    }


}

export default Statistics;