import Card from "../data/card";
import Produce from "../function/produce";
import Hero from "../data/hero";
class CaptainYudora extends Hero{
    constructor(vue,dataItem){
        super(vue,dataItem);
        super.powerType = 'active';
        this.count = 0;
    }
    GameBegin(){
        this.powerDescribe = "挖出一个金色随从（还剩5次）"
    }
    Active(){
        return new Promise((resolve,reject)=>{
            this.count += 1;
            this.powerDescribe = "挖出一个金色随从（还剩" + (5 - this.count) + "次）";
            if(this.count == 5){
                this.count = 0;
                //挖出宝藏
                let allM = this.vue.$store.state.Pub.allMinions;
                let rand  = Math.floor(Math.random()*allM.length);
                let tgt = allM[rand];
                
                let min = new Card(this.vue,'minion');
                min.LoadingAttr(tgt,true,-1);
                min.load(this.vue,min);
                min.golden = true;
                min.ad *= 2;
                min.hp *= 2;
                //创建卡
                let p = new Produce(this.vue);
                p.PushMinionsHandCard(min,false);
            }
            resolve(true);
        })
    }
}


export default CaptainYudora;