import KingGalaxus from '../hero/KingGalaxus.js'
import Pacivik from '../hero/Pacivik.js';
import Curator from '../hero/Curator.js';
import Renault from '../hero/Renault.js';
import Brian from '../hero/Brian.js';
import LichKing from '../hero/LichKing.js';
import George from '../hero/George.js';
import CaptainYudora from '../hero/CaptainYudora.js';
class Loading{
    constructor(vue){
        this.vue = vue;
    }
    //加载一张图片
    LoadingImage(imageUrl,dom){
        //imageUrl : 图片资源位置
        //dom 插入的dom
        return new Promise((resolve,reject)=>{
            let d = dom;
            let img = document.createElement('img');
            img.src = imageUrl;
            img.onload = function(){
                d.appendChild(img);
                resolve(true);
            }
        })
    }
    //加载全部随从图片
    async LoadingAllMinionsImage(imgUrlArr){
        console.log("开始加载随从资源");
        let resource_container = this.vue.$refs.resource_container;
        let time = 0;
        let timer = setInterval(() => {
            time += 0.001;
        }, 1);
        //加载随从的图像资源
        for(let i=0;i<imgUrlArr.length;i++){
            await this.LoadingImage(imgUrlArr[i],resource_container);
        }
        clearInterval(timer);
        console.log(`随从资源全部加载完成,花费${time}s时间`);
        
    }
    //加载UI资源
    LoadingUIResource(){

    }


    //创建英雄对象
    CreateHero(dataItem){
        let heroItem = null;
        let par = [this.vue,dataItem]
        switch(dataItem.id){
            //帕奇维克
            case 1 : heroItem = new Pacivik(...par);break;
            //馆长
            case 2 : heroItem = new Curator(...par);break;
            //雷诺
            case 3 : heroItem = new Renault(...par);break;
            //铜须
            case 4 : heroItem = new Brian(...par);break;
            //加拉克苏斯大王
            case 5 : heroItem = new KingGalaxus(...par);break;
            //巫妖王
            case 6 : heroItem = new LichKing(...par);break;
            //堕落的乔治
            case 7 : heroItem = new George(...par);break;
            //尤朵拉船长
            case 8 : heroItem = new CaptainYudora(...par);break;
        }
        return heroItem;
    }

}

export default Loading;