import Vue from "vue";
import Vuex from 'vuex';

// import Warrior from '../mixin/structure/WarriorBattle'

//Vue实例使用Vuex
Vue.use(Vuex);
import Statistics from '../mixin/data/statistics.js'
import Enemy from "../mixin/data/enemy.js";
const store = new Vuex.Store({
    
    //公共数据存储区
    state(){
        return{
            vue : null,//酒馆组件实例
            //居中基准线
            PIVOT_LINE : 0.502,
            //bob随从的y坐标
            BOB_MINION_Y : Math.floor(0.5 * window.innerHeight - 0.10 * window.innerWidth),
            //玩家随从的y坐标
            PLAYER_MINION_Y : 0.505 * window.innerHeight,
            //随从的宽度和高度
            MINION_WIDTH : 0.065 * window.innerWidth,
            MINION_HEIGHT : 0.091 * window.innerWidth,
            //设置
            Settings:[
                {
                    name : '零消耗刷新',
                    status : false,
                },
                {
                    name : '英雄处于无敌状态',
                    status : false,
                },
                {
                    name : '铸币补充到99',
                    status : false,
                }
            ],
            //控制
            Control:{
                editionItem : null,//版本数据对象
                versionView : true,//控制版本选择界面的开关
                selectHeroView : false,//选择英雄界面
                selectHandCardsView : false,//控制手牌选择界面的开关
                hideBobMinions : false,//隐藏bob的随从
                hidePlayerMinions : false,//隐藏玩家的随从
                isJoinHands : false,//控制手牌变化是增加手牌还是手牌打出
                battleCrySelect : false,//控制战吼选择
                limitRefreshBtn : false,//控制刷新按钮的快速连点
                isFighting : false,//正在战斗控制
                alertView : false,//提示板
                alertText : '警告',
                canvasShow : false,//是否显示画布
                discover : false,//是否显示发现面板
            },
            //屏幕大小
            ScreenSize:{
                width : window.innerWidth,
                height : window.innerHeight,
            },

            //酒馆实体
            Pub:{
                turn : 1,//当前回合数
                level : 1,//酒馆等级
                purchase : true,//购买状态
                freeze : false,//是否冻结随从状态
                allMinions:[],//bob的所有随从实体
                bobMinionsQueue : [],//出售随从队列
                banArr :[],//被ban随从
                alowArr : [],//可选随从
                refreshCost : {
                    base : 1,//基础
                    zeroCostTime : 0,//0消耗时间
                    twoCostTime : 0,//2点消耗时间
                },//酒馆刷新的实际消耗
                upCost : 5,//升级酒馆实际消耗铸币
                freezeCost : 0,//冻结随从的消耗
                freezeMinionsQueue : [],//被冻结的随从队列
            },


            //玩家实体
            Player:{
                hero : null,//选择的英雄
                coin : 3,//铸币
                handCardQueue : [],//手牌队列
                minionsQueue : [],//出战随从队列
                waitingQueue : [],//等待队列
                lastEnemyMinionsQueue : [],//上一个对手的随从队列
                discoverCount : 3,
                discoverCache : [],
                discoverQueue : [],//发现队列
                //一回合内的随从购买情况
                purchaseTypeQueue : [],
                //战吼挂起
                battleCryMounted : {
                    //战吼随从目标的DOM
                    battleCryNode : null,
                    //战吼随从的数组索引
                    battleCryIndex : -1,
                    //战吼随从的执行块
                    executeBlock : null,
                    //是否处于法术执行体
                    isSpellCast : false,
                    //是否处于英雄选择
                    isHeroSelect : false,
                }
            },

            //对手
            Enemy : new Enemy(),
            // 本回合数据
            TurnStatistics : {
                purchaseArr : [],
                
            },
            //数据统计
            DataStatistics : new Statistics(),
            
        }
    },
    getters:{

    },
    //定义方法
    mutations:{
        //提示
        Alert(state,text){
            state.Control.alertView = true;
            state.Control.alertText = text;
            setTimeout(() => {
                state.Control.alertView = false;
            }, 2000);
        },
        //重置战吼数据状态
        ResetStatus(state){
            state.Player.battleCryMounted = {
                //战吼随从目标的DOM
                battleCryNode : null,
                //战吼随从的数组索引
                battleCryIndex : -1,
                //战吼随从的执行块
                executeBlock : null,
                //是否处于法术执行体
                isSpellCast : false,

                isHeroSelect : false,
            }
        },
        //重新开始酒馆
        Remake(state){
            //酒馆实体重置
            state.Pub = {
                turn : 1,//当前回合数
                level : 1,//酒馆等级
                purchase : true,//购买状态
                freeze : false,//是否冻结随从状态
                allMinions:[],//bob的所有随从实体
                bobMinionsQueue : [],//出售随从队列
                banArr :[],//被ban随从
                alowArr : [],//可选随从
                refreshCost : {
                    base : 1,//基础
                    zeroCostTime : 0,//0消耗时间
                    twoCostTime : 0,//2点消耗时间
                },//酒馆刷新的实际消耗
                upCost : 5,//升级酒馆实际消耗铸币
                freezeCost : 0,//冻结随从的消耗
                freezeMinionsQueue : [],//被冻结的随从队列
            }
            //玩家实体重置
            //玩家实体
            state.Player = {
                hero : null,//选择的英雄
                coin : 3,//铸币
                handCardQueue : [],//手牌队列
                minionsQueue : [],//出战随从队列
                waitingQueue : [],//等待队列
                lastEnemyMinionsQueue : [],//上一个对手的随从队列
                discoverCount : 3,
                discoverCache : [],
                discoverQueue : [],//发现队列
                //一回合内的随从购买情况
                purchaseTypeQueue : [],
                //战吼挂起
                battleCryMounted : {
                    //战吼随从目标的DOM
                    battleCryNode : null,
                    //战吼随从的数组索引
                    battleCryIndex : -1,
                    //战吼随从的执行块
                    executeBlock : null,
                    //是否处于法术执行体
                    isSpellCast : false,
                    //是否处于英雄选择
                    isHeroSelect : false,
                }
            }
        }
    },
    //操作异步
    actions:{

    },
    modules:{

    }
})


export default store;

