<template>
    <div class="player_hero_view">
        <!-- 英雄头像 -->
        <div class="player_hero">
            <img :src="require('../../assets/hero/' + $store.state.Player.hero.heroImage)" alt="" class="cover">
            <div class="health">{{ $store.state.Player.hero.health }}</div>
        </div>
        <!-- 英雄的技能 -->
        <div class="hero_power" ref="hero_power" @touchstart="CheckPowerInfo($refs.hero_power)" @touchend="ClosePowerInfo($refs.hero_power)" @click="UsePower">
            <div class="back_light_green" ref="power_back_light_green" v-if="IsHighLight($store.state.Player.hero.powerCost)"></div>
            <div class="back_light_white"></div>
            <img :src="require('../../assets/power/' + $store.state.Player.hero.powerImage)" alt="" class="cover">
            <img src="../../assets/ui/ui (56).png" alt="" class="frame cover">
            <div class="coin">{{ $store.state.Player.hero.powerCost >= 0 ? $store.state.Player.hero.powerCost : null }}</div>
            <div class="passive_icon" ref="passive_icon" v-if="$store.state.Player.hero.powerType == 'passive'">
                <div class="trigger_light"></div>
                <img src="../../assets/ui/ui (23).png" alt="" class="cover">
            </div>
        </div>
        <div class="hero_power_back" ref="hero_power_back" @touchstart="CheckPowerInfo($refs.hero_power_back)" @touchend="ClosePowerInfo($refs.hero_power_back)">
            <div class="back_light_white"></div>
            <img src="../../assets/ui/ui (51).png" alt="" class="cover">
        </div>
    
        <!-- 英雄技能的信息 -->
        <div class="power_info hidden" ref="power_info">
            <img src="../../assets/ui/ui (61).png" alt="" class="cover">
            <img :src="require('../../assets/power/' + $store.state.Player.hero.powerImage)" alt="" class="power_img">
            <img src="../../assets/ui/ui (59).png" alt="" class="cover">
            <div class="name">{{ $store.state.Player.hero.powerName }}</div>
            <div class="describe">{{ $store.state.Player.hero.powerDescribe }}</div>
        </div>
    
    </div>
</template>

<script>
import HeroControl from '../../mixin/function/herocontrol'
export default {
    name : 'PlayerHero',
    methods:{
        async UsePower(){
            let hc = new HeroControl(this.$store.state.vue);
            hc.UseHeroPower();
        },
        //查看英雄的技能
        CheckPowerInfo(node){
            node.classList.add('check');
            this.$refs.power_info.classList.remove('hidden');
        },
        //关闭查看英雄技能
        ClosePowerInfo(node){
            node.classList.remove('check');
            this.$refs.power_info.classList.add('hidden');
        },
        //是否亮起技能的背景色
        IsHighLight(cost){
            return (this.$store.state.Player.hero.powerType == 'active' &&  cost <= this.$store.state.Player.coin) ? true : false;
        }
    },
    mounted(){

    }

}
</script>

<style lang="less">
.player_hero_view{
    top: 37vw; left: 50%;
    width: 40vw;height:15vw;
    margin: 0 0 0 -20vw;
    transform-style: preserve-3d;
    perspective: 600px;
    z-index: 5;
    // background: rgba(0, 0, 0, 0.362);
    .player_hero{
        left: 15.6vw; top: .7vw;
        width: 8.2vw;height: 11vw;
        .health{
            font-family: 'Belwe_Outline';
            text-shadow: 0 1px rgb(0, 0, 0), 1px 0 black, -1px 0 black, 0 -1px black;
            right: -.3vw; bottom: .2vw;
            font-size: 3vw;
            line-height: 5vw;
            width: 3.2vw;height: 5vw;
            background: url('../../assets/ui/ui (9).png')no-repeat;
            background-size: 100% 100%;
        }
    }
    .hero_power{
        left: 26.5vw; top: 4vw;
        width: 4.2vw;height: 4.2vw;
        perspective: 800px;
        transition: all .2s;
        .back_light_green,.back_light_white{
            top: 50%; left: 50%;
            width: 7.1vw; height: 7.1vw;
            margin: -3.5vw 0 0 -3.5vw;
            background: url('../../assets/ui/ui (60).png')no-repeat;
            background-size: 100% 100%;
        }
        .back_light_white{
            display: none;
            width: 6.6vw; height: 6.6vw;
            margin: -3.3vw 0 0 -3.3vw;
            background: url('../../assets/ui/ui (58).png')no-repeat;
            background-size: 100% 100%;
            opacity: .8;
        }
        .frame{
            transform: scale(1.4);
        }
        .coin{
            font-family: 'Belwe_Outline';
            text-shadow: 0 1px rgb(0, 0, 0), 1px 0 black, -1px 0 black, 0 -1px black;
            left:50%; top: -2vw;
            width: 3vw; height: 3vw;
            margin-left: -1.5vw;
            line-height: 2.8vw;
            font-size: 2.2vw;
            background: url('../../assets/ui/ui (33).png')no-repeat;
            background-size: 100% 100%;
        }
        .passive_icon{
            position: absolute; left: 50%; bottom: -1.4vw;
            width: 2vw; height: 2vw;
            margin-left: -1vw;
            transition: all .2s;
            z-index: 1;
            
        }
        
        .passive_icon.trigger{
            .passive_icon::after{
                transform: scale(3);
            }
        }
    }
    .hero_power.check,.hero_power_back.check{
        .back_light_white{
            display: block;
        }
    }
    .hero_power_back{
        perspective: 800px;
        display: none;
        left: 25.7vw; top: 3.1vw;
        width: 6vw;height: 6vw;
        transform: rotateX(90deg);
        transition: all .2s;
        .back_light_white{
            top: 50%; left: 50%;
            display: none;
            width: 6.6vw; height: 6.6vw;
            margin: -3.3vw 0 0 -3.3vw;
            background: url('../../assets/ui/ui (58).png')no-repeat;
            background-size: 100% 100%;
            opacity: .8;
        }
    }
    .hero_power.rotate{
        transform: rotateX(-90deg);
    }
    .hero_power_back.rotate{
        transform: rotateX(0);
    }


    //英雄技能的信息
    .power_info{
        position: absolute; left: 50%; top: -20vw;
        width: 17vw;height: 24vw;
        margin: 0 0 0 -12vw;
        transition: all .2s;
        transition-delay: .1s;
        .power_img{
            left: 50%; top: 3.2vw;
            width: 7vw; height: 7vw;
            margin: 0 0 0 -3.5vw;
        }
        .name{
            left: 0; top: 10.6vw;
            width: 17vw;height: 2.5vw;
            line-height: 2.5vw;
            text-shadow: 0 1px rgb(0, 0, 0), 1px 0 black, -1px 0 black, 0 -1px black;
            font-weight: bold;
        }
        .describe{
            left: 3vw; top: 14.5vw;
            width: 11vw;height: 10vw;
            font-size: 1vw;
            line-height: 1.2vw;
            // color: rgb(54, 15, 0);
            text-shadow: 0 1px rgb(0, 0, 0), 1px 0 black, -1px 0 black, 0 -1px black;
            
        }
    }
    .power_info.hidden{
        transform: scale(0);
    }
}
</style>