import Hero from "../data/hero";
import Card from "../data/card";
import Commons from "../function/commons";
import AnimationS from "../function/animation";
//馆长
class Curator extends Hero{
    constructor(vue,dataItem){
        super(vue,dataItem);
    }
    GameBegin(){
        let summon = {
            name : '馆长的珍藏',
            ad : 2,
            hp : 2,
            legend : false,
            type : '混合',
            star : 1,
            describe : '',
            golden_describe : '',
            img : require('../../assets/minion/中立/min (40).png'),
            version : [20220511,20220831],
            load(that,target){}
        }
        return new Promise(async (resolve,reject)=>{
            let min = new Card(this.vue,'minion');
            min.LoadingAttr(summon,true,-1);
            min.load(this.vue,min);
            //数据添加
            this.vue.$store.state.Player.minionsQueue.push(min);
            //DOM添加
            let com = new Commons(this.vue);
            let node = com.CreateMinionDom(min,false);
            let container = document.querySelector('.player_row');
            container.appendChild(node);
            com.AdjustMinionsCoordinate(false);
            let ams = new AnimationS(this.vue);
            await ams.TransitionEffect(true,false,false,200);
            
            resolve(true);
        })
    }
}

export default Curator;