//查询类
class IQuery{
    constructor(vue){
        this.vue = vue;//vue的组件实例对象
        this.screenW = this.vue.$store.state.ScreenSize.width;
        this.screenH = this.vue.$store.state.ScreenSize.height;
    }
    

    //满足三连条件，返回true，否则返回false
    SatisfiedGolden(target,isBob = true){
        let Player = this.vue.$store.state.Player
        let time = 0;
        //遍历我的手牌
        for(let i=0;i<Player.handCardQueue.length;i++){
            if(Player.handCardQueue[i].name == target.name && !Player.handCardQueue[i].golden){
                time++;
            }
        }
        //遍历我的出场随从
        for(let i=0;i<Player.minionsQueue.length;i++){
            if(Player.minionsQueue[i].name == target.name && !Player.minionsQueue[i].golden){
                time++;
            }
        }
        if(isBob){
            return time >= 2 ? true : false;
        } 
        else{
            return time >= 3 ? true : false;
        }
    }
    

    //是否处于购买区,在返回true，反之为false
    LocatePurchaseArea(e){
        if(e == null || e == undefined)  return false;
        if(e.pageX >= this.screenW * 0.45 
        && e.pageX <= this.screenW * 0.55
        && e.pageY >= this.screenH /2 + 0.1 * this.screenW){
            return true;
        }
        else{
            return false;
        }
    }
    //是否处于出售区
    LocateSaleArea(e){
        if(e == null || e == undefined)  return false;
        if(e.pageX >= this.screenW * 0.45 
        && e.pageX <= this.screenW * 0.55
        && e.pageY <= this.screenH /2 - 0.1 * this.screenW
        && e.pageY > this.screenH /2 - 0.3 * this.screenW){
            return true;
        }
        else{
            return false;
        }
    }
    //是否处于随从区，区别手牌打出时随从区范围和调整随从位置时的随从区范围
    LocateMinionsArea(e,isHand){
        if(e == null || e == undefined)  return false;
        if(e.pageX >= this.screenW * 0.1
        && e.pageX <= this.screenW * 0.9
        && e.pageY >= (isHand ? this.screenH * 0.5 - 0.03 * this.screenW : this.screenH * 0.5)
        && e.pageY <= (isHand ? this.screenH * 0.5 + 0.09 * this.screenW : this.screenH * 0.5 + 0.1 * this.screenW )){
            return true;
        }
        else{
            return false;
        }
    }
    //是否处于法术可释放区
    LocateSpellCastArea(toTop){
        if(toTop > 0 && toTop < this.screenH  - this.screenW * 0.14)
            return true;
        else
            return false;
    }
    //是否有重复的类型
    IsRepeatType(arr,type){
        for(let i in arr){
            if(arr[i] == type){
                return true;
            }
        }
        return false;
    }

    //是否是战吼随从
    IsBattleCryMinion(dataItem){
        let res = false;
        switch(dataItem.name){
            case "鱼人斥候" : res = true;break;
            case "刷新畸体" : res = true;break;
        }
        return res;
    }
    //战吼倍数
    BattleCryTimes(){
        let times = 1;
        for(let i=0;i<this.vue.$store.state.Player.minionsQueue.length;i++){
            if(this.vue.$store.state.Player.minionsQueue[i].name == '布莱恩铜须'){
                if(this.vue.$store.state.Player.minionsQueue[i].golden){
                    return 3;
                }
                else{
                    times = 2;
                }
            }
        }
        return times;
    }
    //亡语倍数
    DeathRattleTimes(isPlayer,driver){
        let times = 1;
        let targetTeam = isPlayer ? driver.PlayerMinions : driver.EnemyMinions;
        for(let i=0;i<targetTeam.length;i++){
            if(targetTeam[i].name == '瑞文戴尔男爵'){
                if(targetTeam[i].golden){
                    return 3;
                }
                else{
                    times = 2;
                }
            }
        }
        return times;
    }
    //召唤倍数
    SummonTimes(isPlayer = true,isFighting = false,driver = null){
        let times = 1;
        let tgtT = null;
        if(isFighting) tgtT = isPlayer ? driver.PlayerMinions : driver.EnemyMinions;
        else tgtT = this.vue.$store.state.Player.minionsQueue;
        for(let i=0;i<tgtT.length;i++){
            if(tgtT[i].name == '卡德加'){
                tgtT[i].golden ? times += 2 : times ++;
            }
        }
        return times;
    }
    //获取目标所在队伍的数据元素和DOM结点
    GetTargetAndNode(isPlayer,isFighting,driver,target = null){
        //target默认为空，不传表示返回目标所在的队伍所有随从元素和结点，传入target则返回目标的数据和结点
        let res = {
            minions : null,
            nodes : null,
        }
        let tempData = null;//临时数据结点
        let tempNodes = null;//临时储存节点
        if(isFighting) tempData = isPlayer ? driver.PlayerMinions : driver.EnemyMinions;
        else tempData = isPlayer ? this.vue.$store.state.Player.minionsQueue : this.vue.$store.state.Pub.bobMinionsQueue;
        tempNodes = isPlayer ? document.querySelectorAll('.player_row li') : document.querySelectorAll('.bob_row li');
        

        if(target == null){
            res.minions = tempData;
            res.nodes = tempNodes;
        }
        else{
            for(let i =0;i<tempNodes.length;i++){
                if(target == tempData[i]){
                    res.minions = tempData[i];
                    res.nodes = tempNodes[i];
                    break;
                }
            }
        }
        return res;
    }
    //判定随从是否属于该版本
    IsBelongVersion(min){
        for(let i=0;i<min.version.length;i++){
            if(min.version[i] == this.vue.$store.state.Control.editionItem.id){
                return true;
            }
        }
        return false;
    }
    //判断该随从是否可以出场（不被禁用）
    NoneForbidden(min){
        for(let i in this.vue.$store.state.Pub.banArr){
            //某种族被禁用下，不可使用的其他非该类种族随从
            if(this.vue.$store.state.Pub.banArr[i] == "野兽"){
                if(min.name == '鸟类的伙伴') return false;
                if(min.name == '驯兽师') return false;
                if(min.name == '族群领袖') return false;
                if(min.name == '兔妖教头') return false;
            }
            else if(this.vue.$store.state.Pub.banArr[i] == "恶魔"){
                if(min.name == '愤怒编织者') return false;
                if(min.name == '灵魂杂耍者') return false;
                if(min.name == '不耐烦的末日预言者') return false;
                if(min.name == '魔瘾结晶者') return false;
            }
            else if(this.vue.$store.state.Pub.banArr[i] == "龙"){
                if(min.name == '雏龙走私商') return false;
                if(min.name == '红衣纳迪娜') return false;
            }
            else if(this.vue.$store.state.Pub.banArr[i] == "野猪人"){
                if(min.name == '野猪预言者') return false;
                if(min.name == '阿迦玛甘·野猪之神') return false;
            }
            else if(this.vue.$store.state.Pub.banArr[i] == "元素"){
                if(min.name == '管理者埃克索图斯') return false;
                if(min.name == '现境大师') return false;
            }
            else if(this.vue.$store.state.Pub.banArr[i] == "机械"){
                if(min.name == '坎格尔的学徒') return false;
            }
            else if(this.vue.$store.state.Pub.banArr[i] == "鱼人"){
                if(min.name == '海鲜投喂者') return false;
            }
            if(this.vue.$store.state.Pub.banArr[i] == min.type){
                //处理特殊情况
                if(this.vue.$store.state.Pub.banArr[i] == '野兽' && min.name == '阿迦玛甘·野猪之神'){
                    for(let j in this.vue.$store.state.Pub.alowArr) if(this.vue.$store.state.Pub.alowArr[j] == '野猪人') return true;
                }
                return false;
            }
        }
        return true;
    }

    
}

export default IQuery;