class AdditionBlock{
    constructor(ad,hp,turn,impactor,remain,halo){
        this.ad = ad;//攻击力加成值
        this.hp = hp;//生命值加成值
        this.turn = turn;//被施加时候的回合数
        this.impactor = impactor;//施加者
        this.remain = remain ?? null;//剩余时间,null表示这个加成是永久的
        this.halo = halo ?? false;
    }
}


export default AdditionBlock;