<template>
    <div class="hero_view">
        <!-- 容器 -->
        <div class="container">
            
            <div class="left"></div>
            <!-- 英雄选择区 -->
            <div class="center">
                <div class="title">选择你的英雄</div>
                <ul>
                    <li v-for="h in heroArr" :key="h.index" @click="SelectHero(h)">
                        <img :src="require('../assets/hero/' + h.heroImage)" class="cover" alt="">
                        <div class="blood">{{ h.health }}</div>
                    </li>
                </ul>
                <div class="bottom">{{ "一共 " + heroArr.length + " 个英雄" }}</div>
            </div>
            <!-- 结果预览区 -->
            <div class="right">
                <img v-if="heroSlt != null" :src="require('../assets/hero/' + heroSlt.heroImage)" alt="" class="hero_image">
                <div v-if="heroSlt != null" class="power">
                    <img :src="require('../assets/power/' + heroSlt.powerImage)" alt="" class="cover">
                    <img :src="require('../assets/ui/ui (44).png')" alt="" class="cover frame">
                    <div class="coin">{{ heroSlt.powerCost >= 0 ?  heroSlt.powerCost : null}}</div>
                </div>
                <div v-if="heroSlt != null" class="power_describe">{{heroSlt.powerDescribe }}</div>
                <div v-if="heroSlt != null" class="blood">{{ heroSlt.health }}</div>
                <div v-if="heroSlt != null" class="hero_name">{{ heroSlt.name }}</div>
                <!-- 开始按钮 -->
                <div class="start_btn" ref="start_btn"
                @touchstart="$refs.start_btn.classList.add('click')"
                @touchend="$refs.start_btn.classList.remove('click')"
                @click="StartFight"></div>
            </div>
            <!-- 对手选择区 -->
            <div class="enemy">
                <div class="flag">难度选择</div>
                <div class="icon"></div>
                <div class="level_list">
                    <li v-for="l in $store.state.Enemy.levelList" :key="l.index" @click="$store.state.Enemy.currentLevel = l">
                        <span v-if="l == $store.state.Enemy.currentLevel" style="color:aliceblue">{{l}}</span>
                        <span v-if="l != $store.state.Enemy.currentLevel">{{l}}</span>
                    </li>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import heroData from '../mixin/json/hero.json'
import Loading  from '../mixin/function/loading';
export default {
    name : 'Hero',
    data(){
        return{
            heroArr : heroData,
            heroSlt : null,
        }
    },
    methods:{
        //选择英雄
        SelectHero(hero){
            this.heroSlt = hero;
        },

        //开始游戏
        StartFight(){
            if(this.heroSlt == null){
                this.$store.commit('Alert','请先选择英雄！');
                return;
            }
            
            let loading = new Loading(this);
            let hero = loading.CreateHero(this.heroSlt);
            hero.LoadPassivePower();
            this.$store.state.Player.hero = hero;
            //关闭选择界面
            this.$store.state.Control.selectHeroView = false;
        }
    },
    mounted(){
        
    }
}
</script>

<style lang="less">
.hero_view{
    left: 0; top: 0;position: absolute;
    width: 100vw;height: 100vh;
    margin: 0 0 0 0;
    transition: all .15s;
    background: url('../assets/bg/bg\ \(8\).png')no-repeat;
    // background: url('../assets/ui/ui\ \(53\).png')no-repeat;
    background-size: 100% 100%;
    .container{
        left: 50%; top: 50%;
        margin: -21vw 0 0 -40vw;
        width: 80vw; height: 42vw;
        .enemy{
            left: 0; top: 0;
            width: 13vw;height: 100%;
            background: url('../assets/bg/bg\ \(16\).png')no-repeat;
            background-size: 100% 100%;
            .flag{
                left: 1vw; top: 0;
                width: 11vw; height: 9vw;
                background: url('../assets/ui/ui\ \(48\).png')no-repeat;
                background-size: 100% 100%;
                line-height: 9vw;
            }
            .icon{
                left: -1vw; top: -1vw;
                width: 6vw; height: 6vw;
                background: url('../assets/ui/ui\ \(47\).png')no-repeat;
                background-size: 100% 100%;
            }
            .level_list{
                left: 1.5vw; top: 21vw;
                width: 10vw; height: 17vw;
                li{
                    position: relative;
                    width: 10vw; height: 3vw;
                    line-height: 3vw;
                    font-size: 1.5vw;
                    background: url('../assets/bg/bg\ \(10\).png')no-repeat;
                    background-size: 100% 100%;
                    margin-top: 1vw;
                    color: rgb(134, 140, 145);
                    span{
                        left: 0; top: 0;
                        width: 10vw; height: 3vw;
                    }
                }
            }
        }
        // background: white;
        .left{
            left: 13vw; top: 0;
            width: 2.3vw;height: 100%;
            background: url('../assets/bg/bg\ \(15\).png')no-repeat;
            background-size: 100% 100%;
        }
        .center{
            left: 15vw; top: 0;
            width: 42.6vw;height: 100%;
            background: url('../assets/bg/bg\ \(13\).png')no-repeat;
            background-size: 100% 100%;
            ul{
                position: absolute; left: 2.8vw; top: 6vw;
                width: 37vw; height: 29vw;
                overflow-y: scroll;
                overflow-x: hidden;
                // background: red;
                li{
                    position: relative; float: left;
                    width: 7vw; height: 9.7vw;
                    margin: 0 0 0 1.8vw;
                    .blood{
                        font-family: 'Belwe_Outline';
                        right: -.2vw; bottom: .5vw;
                        width: 2.2vw;height: 3.4vw;
                        font-size: 2vw;
                        line-height: 3.4vw;
                        background: url('../assets/ui/ui\ \(9\).png')no-repeat;
                        background-size: 100% 100%;
                        text-shadow: 0 1px rgb(0, 0, 0), 1px 0 black, -1px 0 black, 0 -1px black;
                    }
                }
            }
            ul::-webkit-scrollbar{
                display: none;
            }
            .title{
                font-size: 1.4vw;
                left: 11.9vw; top: .5vw;
                width: 19.4vw;height: 2.7vw;
                line-height: 2.7vw;
                color: rgb(228, 217, 207);
                text-shadow: 0 1px rgb(0, 0, 0), 1px 0 black, -1px 0 black, 0 -1px black;
            }
            .bottom{
                font-size: 1.4vw;
                left: 11.9vw; bottom: .5vw;
                width: 19.4vw;height: 2.7vw;
                line-height: 2.7vw;
                background: url('../assets/ui/ui (49).png')no-repeat;
                background-size: 100% 100%;
                color: rgb(228, 217, 207);
                text-shadow: 0 1px rgb(0, 0, 0), 1px 0 black, -1px 0 black, 0 -1px black;
            }
        }
        .right{
            right: 0; top: 0;
            width: 22.5vw;height: 100%;
            background: url('../assets/bg/bg\ \(14\).png')no-repeat;
            background-size: 100% 100%;
            .hero_image{
                left: 6vw; top: 5vw;
                width: 10.6vw;height: 15vw;
            }
            .blood{
                font-family: 'Belwe_Outline';
                left: 13.5vw; top: 14vw;
                width: 3.6vw;height: 5vw;
                font-size: 2.5vw;
                line-height: 5vw;
                background: url('../assets/ui/ui\ \(9\).png')no-repeat;
                background-size: 100% 100%;
                text-shadow: 0 1px rgb(0, 0, 0), 1px 0 black, -1px 0 black, 0 -1px black;
            }
            .power{
                left: 5vw; top: 15.4vw;
                width: 3vw;height: 3vw;
                font-size: 2.5vw;
                line-height: 5vw;
                .frame{
                    left: 0; top: 0;
                    transform: scale(1.54);
                }
            }
            .coin{
                font-family: 'Belwe_Outline';
                left: 50%; top: -1.7vw;
                width: 2.8vw;height: 2.8vw;
                font-size: 2vw;
                line-height: 2.6vw;
                background: url('../assets/ui/ui\ \(33\).png')no-repeat;
                margin-left: -1.4vw;
                background-size: 100% 100%;
                text-shadow: 0 1px rgb(0, 0, 0), 1px 0 black, -1px 0 black, 0 -1px black;
            }
            .power_describe{
                font-size: 1.1vw;
                top: 23.4vw; right: 5vw;
                width: 13vw;height: 7vw;
                color: rgb(228, 217, 207);
                text-shadow: 0 1px rgb(0, 0, 0), 1px 0 black, -1px 0 black, 0 -1px black;
            }
            .hero_name{
                font-size: 1.2vw;
                top: 20vw; right: 5vw;
                width: 13vw;height: 2vw;
                line-height: 2vw;
                color: rgb(228, 217, 207);
                font-weight: bold;
                text-shadow: 0 1px rgb(0, 0, 0), 1px 0 black, -1px 0 black, 0 -1px black;
            }
            .start_btn{
                bottom: 1.3vw; right: 7.2vw;
                width: 8.2vw;height: 8.2vw;
                background: url('../assets/ui/ui\ \(45\).png')no-repeat;
                background-size: 100% 100%;
                transition: all .1s;
            }
            .start_btn.click{
                transform: scale(.9);
            }
        }
    }
}
</style>