import AnimationS from "../function/animation";
import AdditionBlock from "../data/addition_block";
import Special from "../function/special";
import OpeNode from "../function/openode";
import Discover from "../function/discover";
import Card from "../data/card";
class SpellCraft{
    constructor(vue,self,spellItem){
        this.vue = vue;
        //该塑造法术的来源者
        this.self = self;
        //该塑造法术的实体
        this.spellItem = spellItem;
    }
    //返回纳迦的塑造法术
    ReturnNagaSpellCraft(){
        let func = null;
        switch(this.self.name){
            //函数体内的this对象的值会被绑定到传入bind()第一个参数的值
            case "迷你侍从" : func = this.SpellCraft_1.bind(this);break;
            case "深海钓客" : func = this.SpellCraft_2.bind(this);break;
            case "滩头指挥官" : func = this.SpellCraft_3.bind(this);break;
            case "乘波骑士" : func = this.SpellCraft_4.bind(this);break;
            case "鳗缚弓箭手" : func = this.SpellCraft_5.bind(this);break;
            case "闪鳞纳迦" : func = this.SpellCraft_6.bind(this);break;
            case "照看者奥戈佐亚" : 
                func = this.SpellCraft_7.bind(this);
                //是否是选择型法术
                this.spellItem.selectSpell= false;
                //可选bob随从？
                this.spellItem.selectBob = false;
                //可选自己随从？
                this.spellItem.selectPlayer = false;
                break;
        }
        return func;
    }

    //迷你侍从的塑造法术
    SpellCraft_1(target){
        let ams = new AnimationS(this.vue);
        let mq = this.vue.$store.state.Player.minionsQueue;
        let tgtNode = null;//选中目标的DOM结点
        let self = this.self;
        let turn = this.vue.$store.state.Pub.turn;
        let that = this;
        return new Promise((resolve,reject)=>{
            for(let i in mq){
                if(mq[i] == target){
                    tgtNode = document.querySelectorAll('.player_row li')[i];
                }
            }
            if(tgtNode == null || tgtNode == undefined){
                resolve(true);
                return;
            }
            if(target.name == '熔岩潜伏者' && !target.getSpellCraft){
                let sp = new Special(this.vue);
                sp.TriggerPassive(tgtNode);
            }
            let addNumber = self.golden ? 4 : 2;
            let addBlock = (target.name == '熔岩潜伏者' && !target.getSpellCraft) ? new AdditionBlock(addNumber,0,turn,self,null) : new AdditionBlock(addNumber,0,turn,self,1);
            target.additionContainer.push(addBlock);
            if(target.name == '熔岩潜伏者') target.getSpellCraft = true;
            ams.AttrAdditionAnimate(tgtNode,target,true,false,false);
            //删除法术卡
            that.ConsumeSpellCraft();
            resolve(true);
        })
    }
    //深海钓客的塑造法术
    SpellCraft_2(target){
        let ams = new AnimationS(this.vue);
        let mq = this.vue.$store.state.Player.minionsQueue;
        let tgtNode = null;//选中目标的DOM结点
        let self = this.self;
        let turn = this.vue.$store.state.Pub.turn;
        let that = this;
        return new Promise(async (resolve,reject)=>{
            for(let i in mq){
                if(mq[i] == target){
                    tgtNode = document.querySelectorAll('.player_row li')[i];
                }
            }
            if(tgtNode == null || tgtNode == undefined){
                resolve(true);
                return;
            }
            if(target.name == '熔岩潜伏者' && !target.getSpellCraft){
                let sp = new Special(this.vue);
                sp.TriggerPassive(tgtNode);
            }
            let addNumber = self.golden ? 6 : 3;
            let addBlock = (target.name == '熔岩潜伏者' && !target.getSpellCraft) ? new AdditionBlock(0,addNumber,turn,self,null) : new AdditionBlock(0,addNumber,turn,self,1);
            target.additionContainer.push(addBlock);
            ams.AttrAdditionAnimate(tgtNode,target,false,true,false);
            //加嘲讽
            if(!target.nature.taunt && !target.temp_nature.target){
                (target.name == '熔岩潜伏者' && !target.getSpellCraft) ? target.nature.taunt = true :target.temp_nature.taunt = true;
                let sp = new Special(that.vue);
                await sp.CreateTaunt(tgtNode,target.golden);
            }
            if(target.name == '熔岩潜伏者') target.getSpellCraft = true;
            //删除法术卡
            that.ConsumeSpellCraft();
            resolve(true);
        })
    }
    //滩头指挥官
    SpellCraft_3(target){
        let ams = new AnimationS(this.vue);
        let mq = this.vue.$store.state.Player.minionsQueue;
        let ope = new OpeNode(this.vue);
        //目标的DOM结点
        let tgtNode = ope.GetTargetAndNode(true,false,null,target).nodes;
        let self = this.self;
        let turn = this.vue.$store.state.Pub.turn;
        let that = this;
        return new Promise(async (resolve,reject)=>{
            let nagaNum = 0;
            for(let i in mq){
                if(mq[i].type == '纳迦' || mq[i].type == '混合') nagaNum+=1;
            }
            if(target.name == '熔岩潜伏者' && !target.getSpellCraft){
                let sp = new Special(this.vue);
                sp.TriggerPassive(tgtNode);
            }
            let addNum = self.golden ? nagaNum * 2 : nagaNum ;
            let addBlock = (target.name == '熔岩潜伏者' && !target.getSpellCraft) ? new AdditionBlock(addNum,addNum,turn,self,null) : new AdditionBlock(addNum,addNum,turn,self,1);
            target.additionContainer.push(addBlock);
            await ams.AttrAdditionAnimate(tgtNode,target,true,true,false);
            if(target.name == '熔岩潜伏者') target.getSpellCraft = true;
            //删除法术卡
            that.ConsumeSpellCraft();
            resolve(true);
        })
    }
    //乘波骑士
    SpellCraft_4(target){
        let ams = new AnimationS(this.vue);
        let ope = new OpeNode(this.vue);
        //目标的DOM结点
        let tgtNode = ope.GetTargetAndNode(true,false,null,target).nodes;
        let self = this.self;
        let turn = this.vue.$store.state.Pub.turn;
        let that = this;
        return new Promise(async (resolve,reject)=>{
            if(target.name == '熔岩潜伏者' && !target.getSpellCraft){
                let sp = new Special(this.vue);
                sp.TriggerPassive(tgtNode);
            }
            let addNum = self.golden ? 4 : 2 ;
            let addBlock = (target.name == '熔岩潜伏者' && !target.getSpellCraft) ? new AdditionBlock(addNum,addNum,turn,self,null) : new AdditionBlock(addNum,addNum,turn,self,1);
            target.additionContainer.push(addBlock);
            await ams.AttrAdditionAnimate(tgtNode,target,true,true,false);
            //施加风怒
            if(!target.nature.wind_fury && !target.temp_nature.wind_fury){
                (target.name == '熔岩潜伏者' && !target.getSpellCraft) ? target.nature.wind_fury = true : target.temp_nature.wind_fury = true;
                let sp = new Special(this.vue);
                await sp.CreateWindFury(tgtNode);
            }
            if(target.name == '熔岩潜伏者') target.getSpellCraft = true;
            //删除法术卡
            that.ConsumeSpellCraft();
            resolve(true);
        })
    }
    //鳗缚弓箭手
    SpellCraft_5(target){
        let ams = new AnimationS(this.vue);
        let mq = this.vue.$store.state.Player.minionsQueue;
        let tgtNode = null;//选中目标的DOM结点
        let self = this.self;
        let turn = this.vue.$store.state.Pub.turn;
        let that = this;
        return new Promise((resolve,reject)=>{
            for(let i in mq){
                if(mq[i] == target){
                    tgtNode = document.querySelectorAll('.player_row li')[i];
                }
            }
            if(tgtNode == null || tgtNode == undefined){
                resolve(true);
                return;
            }
            if(target.name == '熔岩潜伏者' && !target.getSpellCraft){
                let sp = new Special(this.vue);
                sp.TriggerPassive(tgtNode);
            }
            let addNumber = self.golden ? 16 : 8;
            let addBlock = (target.name == '熔岩潜伏者' && !target.getSpellCraft) ? new AdditionBlock(addNumber,0,turn,self,null) : new AdditionBlock(addNumber,0,turn,self,1);
            target.additionContainer.push(addBlock);
            ams.AttrAdditionAnimate(tgtNode,target,true,false,false);
            if(target.name == '熔岩潜伏者') target.getSpellCraft = true;
            //删除法术卡
            that.ConsumeSpellCraft();
            resolve(true);
        })
    }
    //闪鳞纳迦
    SpellCraft_6(target){
        let ope = new OpeNode(this.vue);
        //目标的DOM结点
        let tgtNode = ope.GetTargetAndNode(true,false,null,target).nodes;
        let that = this;
        return new Promise(async (resolve,reject)=>{
            if(target.name == '熔岩潜伏者' && !target.getSpellCraft){
                let sp = new Special(this.vue);
                sp.TriggerPassive(tgtNode);
            }
            //施加风怒
            if(!target.nature.divine_shield && !target.temp_nature.divine_shield){
                (target.name == '熔岩潜伏者' && !target.getSpellCraft) ? target.nature.divine_shield = true : target.temp_nature.divine_shield = true;
                let sp = new Special(this.vue);
                await sp.CreateDivineShield(tgtNode);
            }
            if(target.name == '熔岩潜伏者') target.getSpellCraft = true;
            //删除法术卡
            that.ConsumeSpellCraft();
            resolve(true);
        })
    }
    //照看者奥戈佐亚
    SpellCraft_7(){
        let allM = this.vue.$store.state.Pub.allMinions;//Bob酒馆的所有随从
        let times = this.self.golden ? 2 : 1;
        return new Promise((resolve,reject)=>{
            let nagaArr  = [];
            for(let i in allM){
                if(allM[i].type.search("纳迦") != -1 || allM[i].type.search("混合") != -1) nagaArr.push(i);
            }
            if(nagaArr.length == 0){
                reject('酒馆里没有纳迦牌');
                return;
            }
            //选几张纳迦加入发现队列
            let res = [];
            for(let k=0;k<times;k++){
                for(let i =0;i<3 && nagaArr.length > 0;i++){
                    let rand = Math.floor(Math.random()*nagaArr.length);
                    let tgt = allM[nagaArr[rand]];
                    let newTgt = new Card(this.vue,'minion');
                    newTgt.LoadingAttr(tgt,true,-1);
                    newTgt.load(this.vue,newTgt);
                    res.push(newTgt);
                    nagaArr.splice(rand,1);
                }
            }
            let dis = new Discover(this.vue);
            dis.PushDiscoverMinions(res,3);
            resolve(true);
        })
    }

    //删除在手牌序列中被使用的法术
    ConsumeSpellCraft(){
        for(let i =0;i<this.vue.$store.state.Player.handCardQueue.length;i++){
            if(this.vue.$store.state.Player.handCardQueue[i] == this.spellItem){
                //数据删除
                this.vue.$store.state.Player.handCardQueue.splice(i,1);
                let handsNode = document.querySelectorAll('.hand_card_select_area li')[i];
                //DOM结点删除
                handsNode.remove();
                //删除手牌可能导致
                if(this.vue.$store.state.Player.handCardQueue.length <= 0) this.vue.$store.state.Control.selectHandCardsView = false;
            }
        }
    }
} 

export default SpellCraft;