import AdditionBlock from "./addition_block";
import Card from "./card";
import AnimationS from "../function/animation";
class BloodStone extends Card{
    constructor(vue,golden){
        super(vue,'spell');
        //名称
        super.name = "鲜血宝石";
        //星级
        super.star = 1;
        //图像
        super.img = require("../../assets/minion/野猪人/min (19).png")
        //是否是金卡
        super.golden = golden;
        

        //消耗的类型
        this.costType = 'coin';
        //消耗的量
        this.cost = 0;
        //数值
        this.value = 1;
        //类型
        this.selectSpell = true;//是否是选择型法术
        //可选bob随从？
        this.selectBob = false;
        //可选自己随从？
        this.selectPlayer = true;
        //限制随从
        this.limitType = [];
        //描述
        super.describe = `使一个随从获得+${this.value + this.QueryBloodStoneAddition(true)}/+${this.value + this.QueryBloodStoneAddition(true)}`;

    }

    //执行函数
    SpellExecute(dataItem,isHands = true){
        //isHands从手牌释放
        let ams = new AnimationS(this.vue);
        return new Promise(async (resolve,reject)=>{
            //所选择目标的节点
            let node= null;
            let mq = this.vue.$store.state.Player.minionsQueue;
            for(let i in mq){
                if(mq[i] == dataItem){
                    node = document.querySelectorAll('.player_row li')[i];
                }
            }
            if(node == null || node == undefined){
                resolve(true);
                return;
            }
            let addNum = this.value + this.QueryBloodStoneAddition(dataItem.isPlayer);
            let addBlock = new AdditionBlock(addNum,addNum,this.vue.$store.state.Pub.turn,"鲜血宝石",null);
            dataItem.additionContainer.push(addBlock);
            ams.AttrAdditionAnimate(node,dataItem,true,true,false);
            //（成功执行后）删除自己
            if(isHands){
                for(let i in this.vue.$store.state.Player.handCardQueue){
                    if(this.vue.$store.state.Player.handCardQueue[i] == this){
                        //数据删除
                        this.vue.$store.state.Player.handCardQueue.splice(i,1);
                        let handsNode = document.querySelectorAll('.hand_card_select_area li')[i];
                        //DOM结点删除
                        handsNode.remove();
                        //删除手牌可能导致
                        if(this.vue.$store.state.Player.handCardQueue.length <= 0) this.vue.$store.state.Control.selectHandCardsView = false;
                    }
                }
            }
            //触发鲜血宝石的额外效果
            dataItem.TriggerBloodStone();
            //劲爆额外加成
            for(let i in mq){
                if(mq[i].name == '劲爆二人组' && mq[i] != dataItem && (dataItem.type == '野猪人' || dataItem.type == '混合')){
                    await ams.Delay(100);
                    let n = document.querySelectorAll('.player_row li')[i]
                    let addNumber  = mq[i].golden ? 2 : 1;
                    let addBlock = new AdditionBlock(addNumber,addNumber,this.vue.$store.state.Pub.turn,mq[i],null);
                    mq[i].additionContainer.push(addBlock);
                    ams.AttrAdditionAnimate(n,mq[i],true,true,false);
                }
            }
            resolve(true);
        })
    }

    //查询场上的鲜血宝石加成
    QueryBloodStoneAddition(isPlayer){
        let times = 0;
        for(let i in isPlayer ? this.vue.$store.state.Player.minionsQueue : this.vue.$store.state.Pub.bobMinionsQueue){
            let target = isPlayer ? this.vue.$store.state.Player.minionsQueue[i] : this.vue.$store.state.Pub.bobMinionsQueue[i];
            if(target.name == '阿迦玛甘·野猪之神'){
                if(target.golden) times += 2;
                else times += 1;
            }
        }
        //返回鲜血宝石的加成值
        return times;
    }
    //更新鲜血宝石的信息
    UpdateBloodStoneInfo(){
        this.describe = `使一个随从获得+${this.value + this.QueryBloodStoneAddition(true)}/+${this.value + this.QueryBloodStoneAddition(true)}`;
    }
    
}

export default BloodStone;