import Hero from "../data/hero";
import Special from "../function/special";
class George extends Hero{
    constructor(vue,dataItem){
        super(vue,dataItem);
        this.powerType = 'active';
        this.immediateActive = false;
        this.selectPlayerMinion = true;
        this.selectBobMinion = false;
    }
    Active(dataItem,domItem){
        return new Promise(async (resolve,reject)=>{
            if(!dataItem.nature.divine_shield && !dataItem.temp_nature.divine_shield){
                let sp = new Special(this.vue);
                sp.CreateDivineShield(domItem);
            }
            dataItem.nature.divine_shield = true;
            resolve(true);
        })
    }
}

export default George;