import Block from "../data/block";
import AnimationS from "./animation";
class HeroControl{
    constructor(vue){
        //vue的实例
        this.vue = vue;
        //玩家的英雄实体
        this.PlayerHero = this.vue.$store.state.Player.hero;
    }
    //英雄技能的使用
    UseHeroPower(){
        return new Promise(async (resolve,reject)=>{
            if(this.PlayerHero.usePower && this.PlayerHero.powerType == 'active' && this.PlayerHero.powerCost <= this.vue.$store.state.Player.coin){
                //立即型主动技能
                if(this.PlayerHero.immediateActive){
                    this.PlayerHero.Active();
                    //消耗金币
                    this.vue.$store.state.Player.coin -= this.PlayerHero.powerCost;
                    this.RotatePowerBtn(true);
                }
                else{
                    this.vue.$store.state.Player.battleCryMounted.isHeroSelect = true;
                    let par = [
                        this.PlayerHero.minTypeArr,
                        this.PlayerHero.selectPlayerMinion,
                        this.PlayerHero.selectBobMinion
                    ]
                    let res = this.PlayerHero.HighLightMinions(...par);
                    this.vue.$store.state.Control.battleCrySelect =  res;
                    if(!res){
                        this.vue.$store.commit('Alert','没有目标可以选择');
                        resolve(true);
                        return;
                    }
                    let block = new Block('heroPower',this.PlayerHero,'hero',null,this.PlayerHero.Active.bind(this.PlayerHero));
                    this.vue.$store.state.Player.battleCryMounted.executeBlock = block;
                }
                
            }
            resolve(true);
        })
    }
    //恢复英雄技能的使用
    RestoreHeroPower(){
        return new Promise(async (resolve,reject)=>{
            if(!this.PlayerHero.usePower && this.PlayerHero.powerType == 'active' && (this.PlayerHero.useTime == null || this.PlayerHero.useTime > 0)){
                this.RotatePowerBtn(false);
            }
            resolve(true);
        })
    }
    //技能按钮翻面
    RotatePowerBtn(turnBack){
        let ams = new AnimationS(this.vue);
        return new Promise(async (resolve,reject)=>{
            //修改英雄技能的使用
            this.PlayerHero.usePower = !turnBack;
            if(turnBack) this.PlayerHero.useTime != null ? this.PlayerHero.useTime -- : null;
            //DOM调整
            let powerNode = document.querySelector('.player_hero_view .hero_power');
            let powerNode_backlight = document.querySelector('.player_hero_view .hero_power .back_light_green');
            let powerBackNode = document.querySelector('.player_hero_view .hero_power_back');
            if(turnBack){
                powerNode.classList.add('rotate');
                powerNode_backlight.style.display = 'none';
                await ams.Delay(190);
                powerBackNode.style.display = 'block';
                await ams.Delay(1);
                powerBackNode.classList.add('rotate');
                powerNode.style.display = 'none';
            }
            else{
                powerNode.style.display = 'block';
                powerBackNode.classList.remove('rotate');
                powerBackNode.style.display = 'none';
                await ams.Delay(1);
                powerNode.classList.remove('rotate');
                powerNode_backlight.style.display = 'block';
                await ams.Delay(190);
                powerBackNode.style.display = 'none';
            }
            resolve(true);
        })
    }
}

export default HeroControl;