import Hero from '../data/hero.js';
import AdditionBlock from '../data/addition_block.js';
import AnimationS from '../function/animation.js';
//加拉克苏斯大王
class KingGalaxus extends Hero{
    constructor(vue,dataItem){
        super(vue,dataItem);
        super.powerType = 'active';
    }
    //主动技能
    Active(){
        return new Promise((resolve,reject)=>{
            let friT = this.vue.$store.state.Player.minionsQueue;
            let friNodes = document.querySelectorAll('.player_row li');
            let ams = new AnimationS(this.vue);
            for(let i in friT){
                if(friT[i].type.search('恶魔') != -1 || friT[i].type == '混合'){
                    let addBlock = new AdditionBlock(1,1,this.vue.$store.state.Pub.turn,this,null);
                    friT[i].additionContainer.push(addBlock);
                    ams.AttrAdditionAnimate(friNodes[i],friT[i],true,true,false);
                }
            }
            resolve(true);
        })
    }
}



export default KingGalaxus;