import AnimationS from "./animation";
import Commons from "./commons";
import IQuery from "./iquery";
class Discover{
    constructor(vue){
        this.vue = vue;
    }
    //发现随从装入准备队列
    PushDiscoverMinions(minArr,count){
        //minArr发现的随从
        //count可选择的数量
        this.vue.$store.state.Control.discoverCount = count;//发现三个随从
        //that.$store.state.Control.selectHandCardsView = false;//手牌面板关闭
        for(let i = 0;i<minArr.length;i++){
            //填装发现池
            if(this.vue.$store.state.Player.discoverQueue.length < this.vue.$store.state.Control.discoverCount) this.vue.$store.state.Player.discoverQueue.push(minArr[i]);
            //填装缓冲池
            else this.vue.$store.state.Player.discoverCache.push(minArr[i]);
        }
        this.vue.$store.state.Control.discover = true;//打开发现面板
    }
    SelectDiscoverRes(minData){
        //minData被选择的随从数据
        let com = new Commons(this.vue);
        let q = new IQuery(this.vue);
        let ams = new AnimationS(this.vue);
        return new Promise(async (resolve,reject)=>{
            //生成手牌加入手牌序列
            this.vue.$store.state.Control.isJoinHands = true;
            minData.isPlayer = true;
            //将卡数据插入
            this.vue.$store.state.Player.handCardQueue.push(minData);
            //生成手牌的DOM实体
            let cardNode = com.CreateNewHandCard(minData,"minion");
            this.vue.$refs.hand_card_select_area.appendChild(cardNode);
            com.AdjustCardCoordinate(0)
            //清空
            this.vue.$store.state.Player.discoverQueue = [];
            

            //判断三连的过程
            let isCanGolden = q.SatisfiedGolden(minData,false);
            if(isCanGolden) com.CraftGoldenMinion(minData);
            
            this.vue.$store.state.Control.discover = false;
            com.AdjustCardCoordinate(1);
            com.AdjustCardCoordinate(0);
            await ams.TransitionEffect(false,false,true,200);
            //继续判断还有没有可以继续选择的发现牌
            if(this.vue.$store.state.Player.discoverCache.length > 0){
                for(let i = this.vue.$store.state.Player.discoverCache.length-1;i>=0;i--){
                    let temp = this.vue.$store.state.Player.discoverCache.pop();
                    this.vue.$store.state.Player.discoverQueue.push(temp);
                    if(this.vue.$store.state.Player.discoverQueue.length == this.vue.$store.state.Player.discoverCount) break;
                }
                this.vue.$store.state.Control.discover = true;
            }
            
            resolve(true);
        })
    }
}

export default Discover;