class Hero{
    constructor(vue,dataItem,mount){
        //vue实例
        this.vue = vue;
        //名字
        this.name = dataItem.name;
        //生命值
        this.health = dataItem.health;
        //生命上限
        this.maxHealth = dataItem.health;
        //技能名称
        this.powerName = dataItem.powerName;
        //技能描述
        this.powerDescribe = dataItem.powerDescribe;
        //技能消耗
        this.powerCost =dataItem.powerCost;
        //英雄的图像
        this.heroImage = dataItem.heroImage;
        //技能图像
        this.powerImage = dataItem.powerImage;
        //技能的类型
        this.powerType = 'passive';
        //立即释放的主动战法
        this.immediateActive = true;
        //选择型是指定友军还是bob还是全部
        this.selectPlayerMinion = false;
        this.selectBobMinion = false;
        //选择的随从类型数组
        this.minTypeArr = [];
        //技能使用次数
        this.useTime = null;//null表示主动技能每回合都可以使用，被动技能持续

        //本回合的英雄技能是否可用
        this.usePower = true;

        //挂载丶
        this.mount = mount;
        //购买
        this.Purchase = [];
        //出售
        this.Sale = [];
    }
    //填装被动效果
    LoadPassivePower(){
        console.log('default')
    }
    //触发被动
    TriggerPassiveIcon(){
        console.log('default')
    }
    //游戏开始触发函数
    GameBegin(){
        console.log('default')
    }
    //高亮随从,返回值为真，表示有对象可以选择，为假表示没有对象可以选择
    HighLightMinions(typeArr,isPlayer,isBob){
        console.log(typeArr,isPlayer,isBob);
        let pm = this.vue.$store.state.Player.minionsQueue;
        let bm = this.vue.$store.state.Pub.bobMinionsQueue;
        let players = document.querySelectorAll('.player_row li');
        let bobs = document.querySelectorAll('.bob_row li');

        let tgtNumber = 0;
        //高亮友军的
        if(isPlayer){
            for(let i in pm){
                for(let j =0;j<(typeArr.length > 0? typeArr.length :1);j++){
                    if(typeArr.length > 0){
                        if(pm[i].type.search(typeArr[j]) != -1 || pm[i].type.search("混合") != -1){
                            players[i].classList.add('highlight');
                            tgtNumber ++;
                        }
                    }
                    else{
                        players[i].classList.add('highlight');
                        tgtNumber ++;
                    }
                }
            }
        }
        //高亮bob的
        if(isBob){
            for(let i in bm){
                for(let j =0;j<(typeArr.length > 0? typeArr.length :1);j++){
                    if(typeArr.length > 0){
                        if(bm[i].type.search(typeArr[j]) != -1 || bm[i].type.search("混合") != -1){
                            bobs[i].classList.add('highlight');
                            tgtNumber ++;
                        }
                    }
                    else{
                        bobs[i].classList.add('highlight');
                        tgtNumber ++;
                    }
                }
            }
        }
        return tgtNumber > 0 ? true : false;
    }
    //主动技能
    Active(){
        console.log("默认");
    }

    //购买随从触发函数
    TriggerPurchase(purchaseTarget){
        //被购买的随从单位
        return new Promise(async (resolve,reject)=>{
            for(let i in this.Purchase){
                let data = {
                    purchaseTarget : purchaseTarget,
                }
                await this.Purchase[i].Execute(data);
            }
            resolve(true);
        })
    }
    
    
}

export default Hero;