import Loading from '../function/loading.js';
import heroData from '../json/hero.json'
import Card from './card.js';
import Commons from '../function/commons.js';
import AdditionBlock from './addition_block.js';
import AnimationS from '../function/animation.js';
import beats_lineup from '../json/beatslineup.json'

//导入1-6级随从
import minions1 from '../data/minions1';
import minions2 from '../data/minions2.js';
import minions3 from '../data/minions3.js';
import minions4 from '../data/minions4.js';
import minions5 from '../data/minions5.js';
import minions6 from '../data/minions6.js';
class Enemy{
    constructor(){
        this.vue = null;
        this.health = 40;//对手的真实血量值
        this.hero = null;//选择的英雄
        this.levelList = ["简单","普通","困难"];//难度等级
        this.currentLevel = "简单";//当前难度
        this.star = 1;//其酒馆等级
        this.minionsQueue = [];//其随从队列
        this.EnemyList = [];//人机的队伍
    }
    //获取vue的实例
    GetVueComponents(vue){
        this.vue = vue;
        //顺便随从初始化一个英雄对象
        this.SpanningHero();
    }
    //获得难度阵容表
    GetTeam(){
        // for(let i in enemyList){
        //     if(enemyList[i].id == this.vue.$store.state.Control.editionItem.id){
        //         this.EnemyList = enemyList[i].list;
        //         break;
        //     }
        // }
    }
    //生成英雄
    SpanningHero(){
        let heroArr = heroData;
        let heroInfo = heroArr[Math.floor(Math.random()*heroArr.length)];
        let loading = new Loading(this.vue);
        this.hero = loading.CreateHero(heroInfo);
    }
    //生成队伍
    SpanningMinionsQueue(){
        let com = new Commons(this.vue);
        return new Promise(async (resolve,reject)=>{
            this.Clear();
            let res = [];
            //第一回合
            if(this.vue.$store.state.Pub.turn == 1 || this.vue.$store.state.Pub.turn == 3){
                res.push(this.RandOneStarMinion());
                this.CreateTeam(res);
            }
            else{
                let rand = Math.floor(Math.random()*10);
                if(rand == 1){
                    this.BeastLineup();
                }
                else{
                    this.RandomLineup();
                }
            }
            
            com.AdjustMinionsCoordinate(true);
            
            resolve(true);
        })
    }


    //清理
    Clear(){
        //清空bob的随从的数据和DOM结点
        let bobM = document.querySelectorAll('.bob_row li');
        for(let i=bobM.length -1;i>=0;i--){
            bobM[i].remove();
        }
        this.vue.$store.state.Pub.bobMinionsQueue = [];
        //清空随从队列
        this.minionsQueue = [];
        this.vue.$store.state.Player.lastEnemyMinionsQueue = [];
    }
    //随机购买一星随从
    RandOneStarMinion(){
        let all = this.vue.$store.state.Pub.allMinions;
        let indexArr = [];
        for(let i in all){
            if(all[i].star == 1){
                if(this.currentLevel == '简单') indexArr.push(i);
                else if(this.currentLevel == '普通' && all[i].ad > 1 && ( all[i].nature.reborn || all[i].nature.divine_shield) ) indexArr.push(i);
            }
        }
        let rand = Math.floor(Math.random()*indexArr.length);
        return this.vue.$store.state.Pub.allMinions[indexArr[rand]];
    }
    //根据传入名称数组，来生成队伍
    SelectByName(nameArr){
        let all = this.vue.$store.state.Pub.allMinions;
        let res = [];
        for(let j in nameArr){
            for(let i in all){
                if(nameArr[j] == all[i].name){
                    res.push(all[i]);
                    break;
                }
            }
        }
        return res;
    }

    //生成实例
    CreateTeam(team){
        let com = new Commons(this.vue);
        let container = document.querySelector('.bob_row');
        for(let i in team){
            let min = new Card(this.vue,'minion');
            min.LoadingAttr(team[i],true,-1);
            min.load(this.vue,min);
            this.minionsQueue.push(min);//不仅放在自己的随从中
            this.vue.$store.state.Player.lastEnemyMinionsQueue.push(min);
            
            let minNode = com.CreateMinionDom(min,true);
            container.appendChild(minNode);
        }
    }




    //野兽阵容
    BeastLineup(){
        let curTurn = Math.ceil(this.vue.$store.state.Pub.turn/2);
        let container = document.querySelector('.bob_row');
        let com = new Commons(this.vue);
        let minList = [];
        for(let i in minions1){
            if(minions1[i].name == '雄斑虎'
            ||minions1[i].name == '食腐土狼'){
                minList.push(minions1[i]);
            }
        }
        for(let i in minions2){
            if(minions2[i].name == '暴怒的巨蜥' 
            || minions2[i].name == '下水道老鼠' 
            || minions2[i].name == '跳蛙骑士' 
            || minions2[i].name == '无私的英雄'){
                minList.push(minions2[i]);
            }
        }
        for(let i in minions3){
            if(minions3[i].name == '巨大的金刚鹦鹉' 
            || minions3[i].name == '瘟疫鼠群' 
            || minions3[i].name == '幼苗催生员' 
            || minions3[i].name == '鸟类的伙伴'){
                minList.push(minions3[i]);
            }
        }
        for(let i in minions4){
            if(minions4[i].name == '洞穴多头蛇'){
                minList.push(minions4[i]);
            }
        }
        for(let i in minions5){
            if(minions5[i].name == '瑞文戴尔男爵'
            || minions5[i].name == '莽神火车王'){
                minList.push(minions5[i]);
            }
        }
        for(let i in minions6){
            if(minions6[i].name == '巨狼戈德林'
            || minions6[i].name == '阴森巨蟒'
            || minions6[i].name == '迈克斯纳'){
                minList.push(minions6[i]);
            }
        }
        let member = [];
        switch(this.currentLevel){
            case "简单" : member = beats_lineup.easy_list[(curTurn - 3) > beats_lineup.easy_list.length-1 ? beats_lineup.easy_list.length-1 : (curTurn - 3)];break;
            case "普通" : member = beats_lineup.normal_list[(curTurn - 3) > beats_lineup.normal_list.length-1 ? beats_lineup.normal_list.length -1 : (curTurn-3)];break;
            case "困难" : member = beats_lineup.difficult_list[(curTurn - 3) > beats_lineup.difficult_list.length-1 ? beats_lineup.difficult_list.length-1 : (curTurn-3)];break;
        }
        
        for(let i in member){
            for(let j in minList){
                if(minList[j].name == member[i].name){
                    let min = new Card(this.vue,'minion');
                    min.LoadingAttr(minList[j],true,-1);
                    min.load(this.vue,min);
                    //是否金
                    if(member[i].golden ?? false){
                        min.golden = true;
                        min.ad *= 2;
                        min.hp *= 2;
                    }
                    //是否复生
                    if(member[i].reborn ?? false){
                        min.nature.reborn =  true;
                    }
                    //是否有圣盾
                    if(member[i].divine_shield ?? false){
                        min.nature.divine_shield =  true;
                    }
                    //是否有嘲讽
                    if(member[i].taunt ?? false){
                        min.nature.taunt =  true;
                    }
                    //是否有风怒
                    if(member[i].wind_fury ?? false){
                        min.nature.wind_fury =  true;
                    }
                    this.minionsQueue.push(min);
                    let minNode= com.CreateMinionDom(min,true);
                    container.appendChild(minNode);
                    break;
                }
            }
        }
    }
    //随机阵容
    RandomLineup(){
        let container = document.querySelector('.bob_row');
        let com = new Commons(this.vue);
        //第3,4,5,6回合
        if(this.vue.$store.state.Pub.turn > 3 && this.vue.$store.state.Pub.turn < 12){
            let time = (this.vue.$store.state.Pub.turn - 3)/2 + 1;
            for(let i = 0;i< time;i++){
                let targetMin = null;
                switch(this.currentLevel){
                    case "简单" : targetMin = minions1[Math.floor(Math.random()*minions1.length)]; break;
                    case "普通" : targetMin = minions2[Math.floor(Math.random()*minions2.length)]; break;
                    case "困难" : targetMin = minions3[Math.floor(Math.random()*minions3.length)]; break;
                }
                let min = new Card(this.vue,'minion');
                min.LoadingAttr(targetMin,true,-1);
                min.load(this.vue,min);
                this.minionsQueue.push(min);
                let minNode= com.CreateMinionDom(min,true);
                container.appendChild(minNode);
            }
        }
        //第7,8,9回合
        else if(this.vue.$store.state.Pub.turn > 12 && this.vue.$store.state.Pub.turn < 18){
            let time = (this.vue.$store.state.Pub.turn - 3)/2;
            time < 7 ? time += 1 : null;
            for(let i = 0;i< time;i++){
                let targetMin = null;
                if(i == 0 || i == 1){
                    switch(this.currentLevel){
                        case "简单" : targetMin = minions1[Math.floor(Math.random()*minions1.length)]; break;
                        case "普通" : targetMin = minions2[Math.floor(Math.random()*minions2.length)]; break;
                        case "困难" : targetMin = minions3[Math.floor(Math.random()*minions3.length)]; break;
                    }
                }
                else if(i == 2 || i == 3){
                    switch(this.currentLevel){
                        case "简单" : targetMin = minions2[Math.floor(Math.random()*minions2.length)]; break;
                        case "普通" : targetMin = minions3[Math.floor(Math.random()*minions3.length)]; break;
                        case "困难" : targetMin = minions4[Math.floor(Math.random()*minions4.length)]; break;
                    }
                }
                else if(i == 4 || i == 5) {
                    switch(this.currentLevel){
                        case "简单" : targetMin = minions3[Math.floor(Math.random()*minions3.length)]; break;
                        case "普通" : targetMin = minions4[Math.floor(Math.random()*minions4.length)]; break;
                        case "困难" : targetMin = minions5[Math.floor(Math.random()*minions5.length)]; break;
                    }
                }
                else{
                    switch(this.currentLevel){
                        case "简单" : targetMin = minions4[Math.floor(Math.random()*minions4.length)]; break;
                        case "普通" : targetMin = minions5[Math.floor(Math.random()*minions5.length)]; break;
                        case "困难" : targetMin = minions6[Math.floor(Math.random()*minions6.length)]; break;
                    }
                }
                let min = new Card(this.vue,'minion');
                min.LoadingAttr(targetMin,true,-1);
                min.load(this.vue,min);
                let randAd = 0;
                let randHp = 0;
                switch(this.currentLevel){
                    case "简单" : 
                        randAd = this.vue.$store.state.Pub.turn - 13;
                        randHp = this.vue.$store.state.Pub.turn - 13; 
                        break;
                    case "普通" : 
                        randAd = Math.floor((this.vue.$store.state.Pub.turn - 13)* 1.5);
                        randHp = Math.floor((this.vue.$store.state.Pub.turn - 13)* 1.5);
                        break;
                    case "困难" : 
                        randAd = Math.floor((this.vue.$store.state.Pub.turn - 13)* 2);
                        randHp = Math.floor((this.vue.$store.state.Pub.turn - 13)* 2);
                        break;
                }
                let addition = new AdditionBlock(randAd,randHp,this.vue.$store.state.Pub.turn,min,null);
                min.additionContainer.push(addition);
                
                this.minionsQueue.push(min);
                let minNode= com.CreateMinionDom(min,true);
                container.appendChild(minNode);
            }
        }
        //第10回合开始加数值
        else if(this.vue.$store.state.Pub.turn > 18){
            for(let i = 0;i<7;i++){
                let targetMin = null;
                if(i < 5) targetMin = minions5[Math.floor(Math.random()*minions5.length)];
                else targetMin = minions6[Math.floor(Math.random()*minions6.length)];
                let min = new Card(this.vue,'minion');
                min.LoadingAttr(targetMin,true,-1);
                min.load(this.vue,min);

                let randAd = 0;
                let randHp = 0;
                switch(this.currentLevel){
                    case "简单" : 
                        randAd = this.vue.$store.state.Pub.turn - 18;
                        randHp = this.vue.$store.state.Pub.turn - 18; 
                        break;
                    case "普通" : 
                        randAd = Math.floor((this.vue.$store.state.Pub.turn - 16)* 2);
                        randHp = Math.floor((this.vue.$store.state.Pub.turn - 16)* 2);
                        break;
                    case "困难" : 
                        randAd = Math.floor((this.vue.$store.state.Pub.turn - 14)* 3);
                        randHp = Math.floor((this.vue.$store.state.Pub.turn - 14)* 3);
                        break;
                }
                let addition = new AdditionBlock(randAd,randHp,this.vue.$store.state.Pub.turn,min,null);
                min.additionContainer.push(addition);

                let shieldRand = 0;
                switch(this.currentLevel){
                    case "简单" : shieldRand = Math.floor(Math.random()*12); break;
                    case "普通" : shieldRand = Math.floor(Math.random()*7);break;
                    case "困难" : shieldRand = Math.floor(Math.random()*3);break;
                }
                //刷圣盾
                if(shieldRand  ==  1) min.nature.divine_shield =  true;

                this.minionsQueue.push(min);
                let minNode= com.CreateMinionDom(min,true);
                container.appendChild(minNode);
            }
            
        }
    }
}


export default Enemy;