import AnimationS from './animation.js';
import ClearData from './cleardata.js';
import Commons from './commons';
import IQuery from './iquery';
import BattleDriver from './battleDriver.js';
import HeroControl from './herocontrol.js';
class TurnControl{
    constructor(vue){
        this.vue = vue;
    }
    //招募回合开始
    TurnBegin(){
        let Player = this.vue.$store.state.Player;
        let Pub = this.vue.$store.state.Pub;
        return new Promise(async (resolve,reject)=>{
            let cls = new ClearData(this.vue);
            let com = new Commons(this.vue);
            let query = new IQuery(this.vue);
            //恢复英雄技能的使用
            let hc = new HeroControl(this.vue);
            hc.RestoreHeroPower();
            //清除剩余结点
            cls.ClearRemainNode();
            //重新载入你的随从结点
            let min_container = document.querySelector('.player_row');
            for(let i in Player.minionsQueue){
                let node = com.CreateMinionDom(Player.minionsQueue[i],false);
                min_container.appendChild(node);
            }
            //清除临时加成
            cls.ClearTempAddition();
            com.AdjustMinionsCoordinate(false);
            //判断你的随从卡是否构成三连
            for(let i in Player.minionsQueue){
                let couldGolden = query.SatisfiedGolden(Player.minionsQueue[i],false);
                if(couldGolden) com.CraftGoldenMinion(Player.minionsQueue[i]);
            }
            //战斗回合转招募回合，酒馆随从刷新
            this.vue.RefreshMinions();
            //铸币刷新,但不超过10
            Player.coin = 3 + (Pub.turn / 2  > 7 ? 7 : Pub.turn / 2);
            // Player.coin = 100;
            //更新本回合购买随从类型的队列
            this.vue.$store.state.Player.purchaseTypeQueue = [];
            //此时调用场上随从的招募回合开始函数
            for(let i in Player.minionsQueue){
                await Player.minionsQueue[i].RecruitBeginTrigger();
            }
            Pub.turn += 1;
            resolve(true);
        })
    }

    //招募回合结束
    TurnOver(){
        let Player = this.vue.$store.state.Player;
        let Pub = this.vue.$store.state.Pub;
        let ams = new AnimationS(this.vue);
        let cls = new ClearData(this.vue);
        return new Promise(async (resolve,reject)=>{
            //先储存被冻结的随从
            for(let i=0;i<Pub.bobMinionsQueue.length;i++){
                if(Pub.bobMinionsQueue[i].isFreeze) Pub.freezeMinionsQueue.push(Pub.bobMinionsQueue[i]);
            }
            //清空canvas画布,获取画布上下文
            let ctx = document.querySelector('#canvas').getContext('2d');
            ctx.clearRect(0,0,this.vue.$store.state.ScreenSize.width,this.vue.$store.state.ScreenSize.height);
            //清除过期的卡牌
            await cls.ClearOverdueCard();
            //战斗开始控制
            this.vue.$store.state.Control.isFighting = true;
            //在转入下一回合之前，先执行招募回合结束时的随从效果
            for(let i=0;i<Player.minionsQueue.length;i++){
                await Player.minionsQueue[i].RecruitTrigger();
            }
            //升级酒馆的消耗-1
            Pub.upCost -= 1;
            //对方生成随从
            this.vue.$store.state.Enemy.SpanningMinionsQueue();
            await ams.Delay(200);
            let driver = new BattleDriver(this.vue);
            driver.Start();
            Pub.turn += 1;
            resolve(true);
        })
    }
}

export default TurnControl;