        hp : 5,
        legend : false,
        type : '恶魔',
        star : 6,
        describe : '在你的回合结束时，每个友方恶魔吞食鲍勃的酒馆中的一个随从，获得其属性值',
        golden_describe : '在你的回合结束时，每个友方恶魔吞食鲍勃的酒馆中的一个随从，获得其两倍属性值',
        img : require('../../assets/minion/恶魔/min (5).png'),
        version : [20210505,20220511,20220831],//野猪人版本
        load(that,target){
            let func = function(){
                //代换
                let mq = that.$store.state.Player.minionsQueue;
                let playerNodes = document.querySelectorAll('.player_row li');
                let bobNodes = document.querySelectorAll('.bob_row li');
                let bobM = that.$store.state.Pub.bobMinionsQueue;
                let ams = new AnimationS(that);
                let com = new Commons(that);
                return new Promise(async (resolve,reject)=>{
                    let indexArr = [];
                    let targetIndex = -1;
                    let exist = false;
                    for(let i in mq){
                        if(mq[i].type == '恶魔' || mq[i].type == '混合')indexArr.push(i);
                        if(mq[i].name == '饥饿的魔蝠' && mq[i].golden && mq[i] != target && !target.golden){
                            resolve(true);
                            return;
                        }
                        if(mq[i] == target) targetIndex = i;
                    }
                    for(let i=targetIndex -1;i>=0;i--){
                        if(mq[i].name == '饥饿的魔蝠' && !target.golden || mq[i].name == '饥饿的魔蝠' && mq[i].golden)exist = true;
                    }
                    if(exist){
                        resolve(true);
                        return;
                    }
                    for(let i = bobM.length - 1;i>=0 && indexArr.length > 0;i--){
                        let rand = Math.floor(Math.random()*indexArr.length);
                        let food = bobM[i];
                        let foodNode = bobNodes[i];
                        let eater = mq[indexArr[rand]];
                        let eaterNode = playerNodes[indexArr[rand]];
                        //开始吃
                        let adAddition = target.golden ? food.RealAttack() * 2 : food.RealAttack();
                        let hpAddition = target.golden ? food.RealHealth() * 2 : food.RealHealth();


                        let spl = new Special(that);
                        await spl.SpecialEat(eaterNode,foodNode);

                        bobM.splice(i,1);
                        foodNode.remove();
                        com.AdjustMinionsCoordinate(true);
                        await ams.TransitionEffect(false,true,false,200);
                        let addBlock = new AdditionBlock(adAddition,hpAddition,that.$store.state.Pub.turn,target,null);
                        eater.additionContainer.push(addBlock);
                        await ams.AttrAdditionAnimate(eaterNode,eater,adAddition > 0 && true,hpAddition > 0 && true);
                        indexArr.splice(rand,1);
                    }
                    resolve(true);
                })
            }
            let block = new Block("recruitOver",target,'minion',null,func)
            target.RecruitOver.push(block);
        }
