<template>
    <div class="ban_pick_view" ref="ban_pick_view">
        <div class="mask" @click="Open"></div>
        <div class="open_btn" ref="open_btn" @touchstart="$refs.open_btn.classList.add('click')" @touchend="$refs.open_btn.classList.remove('click')" @click="Open">
            <span>对局助手</span>
        </div>
        <div class="container">
            <div class="title">第{{ Math.ceil($store.state.Pub.turn/2) }}回合</div>
            <div class="ban">
                禁用的种族：{{ $store.state.Pub.banArr.toString()}}
                <!-- <span v-for="b in  $store.state.Pub.banArr" :key="b.index">{{ b }}</span> -->
            </div>
            <div class="pick">可用的种族：{{ $store.state.Pub.alowArr.toString()}}</div>
            <div class="settings">
                <li v-for="s,index in $store.state.Settings" :key="s.index">
                {{ s.name }}
                    <div class="tik" @click="Setting(index)">
                        <img src="../../assets/bg/bg (25).png" v-show="s.status" alt="" class="cover">
                        <img src="../../assets/bg/bg (24).png" alt="" class="cover">
                    </div>
                </li>
            </div>
            <div class="remake_btn" ref="remake_btn" @touchstart="$refs.remake_btn.classList.add('click')"
            @touchend="$refs.remake_btn.classList.remove('click')"
            @click="RemakeGame">重新开始</div>
        </div>
        
    </div>
</template>

<script>
export default {
    name : 'Assistant',
    data(){
        return{
            isOpen : false,
        }
    },
    methods:{
        Open(){
            this.isOpen ? this.$refs.ban_pick_view.classList.remove('open') : this.$refs.ban_pick_view.classList.add('open');
            this.isOpen = !this.isOpen;
            
        },
        Setting(id){
            this.$store.state.Settings[id].status = !this.$store.state.Settings[id].status
            switch(id){
                case 0 : this.$store.state.Settings[id].status ? this.$store.state.Pub.refreshCost.base  = 0 : this.$store.state.Pub.refreshCost.base  = 1;break;
                case 1 : break;
                case 2 : this.$store.state.Settings[id].status ? this.$store.state.Player.coin = 99: null;break;
            }
        },
        RemakeGame(){
            this.$store.commit('Remake');
            this.$store.state.Control.selectHeroView = false;
            this.$store.state.Control.versionView = true;
        }
    }
}
</script>

<style lang="less">
.ban_pick_view{
    left: 100vw; top: 0;
    width: 100vw;height: 100vh;
    z-index: 10;
    transition: all .2s;
    .mask{
        display: none;
        left: -50vw; top: 0;
        width: 50vw; height: 100vh;
    }
    .container{
        left: 0; top: 0;
        width: 60vw; height: 100vh;
        background: url('../../assets/bg/bg (22).png') no-repeat;
        background-size: 100% 100%;
        .title{
            left: 50%; top: 1vw;
            width: 24vw;height: 6vw;
            margin: 0 0 0 -12vw;
            line-height: 6vw;
            color: #4e1700;
            background: url('../../assets/bg/bg (23).png') no-repeat;
            background-size: 100% 100%;
        }
        .ban,.pick{
            text-align: left;
            left: 5vw; top: 10vw;
            width: 30vw;height: 5vw;
            font-weight: bold;
            color: #210a00;
            // text-shadow: 0 1px rgb(0, 0, 0), 1px 0 black, -1px 0 black, 0 -1px black;
        }
        .pick{
            top: 13vw;
        }
        .settings{
            position: absolute; left: 5vw; top: 18vw;
            width: 50vw; height: 15vw;
            li{
                position: relative;float: left;
                width: 25vw; height: 4vw;
                line-height: 4vw;
                text-align: left;
                color: #2d0d00;
                font-weight: bold;
                .tik{
                    right: 2.5vw; top: 1vw;
                    width: 2vw; height: 2vw;
                    background: #000;
                }
            }
        }
        .remake_btn{
            position: absolute; left: 25vw; bottom: 5vw;
            width: 10vw; height: 5vw;
            line-height: 5vw;
            color: #2d0d00;
            font-weight: bold;
            background: url('../../assets/ui/ui (5).png') no-repeat;
            background-size: 100% 100%;
        }
        .remake_btn.click{
            color: #ffffff;
            transform: scale(.9);
        }
    }
    .open_btn{
        left: -4.6vw; top: 50%;
        width: 5vw; height: 18vw;
        margin: -9vw 0 0 0;
        background: url('../../assets/bg/bg (21).png') no-repeat;
        background-size: 100% 100%;
        span{
            left: 1.5vw; top: 6vw;
            font-size: 1.2vw;
            font-weight: bold;
            width: 2vw;
            
            color: #4e1700;
            // text-shadow: 0 1px rgb(0, 0, 0), 1px 0 black, -1px 0 black, 0 -1px black;
        }
    }
    .open_btn.click{
        span{
            color: white;
        }
    }
    
}
.ban_pick_view.open{
    left: 40vw;
    .mask{
        display: block;
    }
}
</style>