        legend : false,
        type : '龙',
        star : 4,
        describe : '战斗开始时：每有一条友方的龙，便使相邻的随从获得+1/+1',
        golden_describe : '战斗开始时：每有一条友方的龙，便使相邻的随从获得+2/+2',
        img : require('../../assets/minion/龙/min (13).png'),
        version : [20210505,20220511,20220831],//野猪人版本
        load(that,target){
            target.nature.passive = true;
            let func = function(){
                let ams = new AnimationS(that);
                let friT = target.battleAttr.belongPlayer ? target.battleAttr.driver.PlayerMinions : target.battleAttr.driver.EnemyMinions;
                let count = 0;
                let addNumber = target.golden ? 2 : 1;
                let left = null;//左边随从
                let leftNode = null;
                let right = null;//左边随从
                let rightNode = null;
                return new Promise(async (resolve,reject)=>{
                    for(let i = 0;i<friT.length;i++){
                        if(friT[i] != target && (friT[i].type == '龙' || friT[i].type == '混合')) count += 1;
                        if(friT[i] == target){
                            if(i > 0){
                                left = friT[i-1];
                                leftNode = target.battleAttr.belongPlayer ? document.querySelectorAll('.player_row li')[i-1] : document.querySelectorAll('.bob_row li')[i-1]
                            }
                            if(i < friT.length - 1){
                                right = friT[i+1];
                                rightNode = target.battleAttr.belongPlayer ? document.querySelectorAll('.player_row li')[i+1] : document.querySelectorAll('.bob_row li')[i+1]
                            }
                        }
                    }
                    if(count == 0){
                        resolve(true);
                        return;
                    }
                    if(left != null){
                        left.battleAttr.ad += count * addNumber;
                        left.battleAttr.hp += count * addNumber;
                        await ams.AttrAdditionAnimate(leftNode,left,true,true,true,count * addNumber,count * addNumber);
                    }
                    if(right != null){
                        right.battleAttr.ad += count * addNumber;
                        right.battleAttr.hp += count * addNumber;
                        await ams.AttrAdditionAnimate(rightNode,right,true,true,true,count * addNumber,count * addNumber);
                    }
                    await ams.Delay(200);
                    resolve(true);
                })
            }
            let block = new Block("battleBegin",target,'minion',null,func)
            target.BattleBegin.push(block);
        }
