import IQuery from "./iquery";
import Loading from "./loading";
import Card from "../data/card.js"
//导入1-6级随从
import minions1 from '../data/minions1';
import minions2 from '../data/minions2.js';
import minions3 from '../data/minions3.js';
import minions4 from '../data/minions4.js';
import minions5 from '../data/minions5.js';
import minions6 from '../data/minions6.js';
class InitPub{
    constructor(vue){
        this.vue = vue;
    }
    //获得当前酒馆可使用随从类型和不可使用随从类型
    GetTypeOfMinions(banNumber = 3){
        //结果，arr为可以使用的随从类型，ban为被禁用的随从类型
        //banNumber为禁用的类型数目
        let res = {
            arr : [],
            ban : [],
        }
        //野猪人版本
        if(this.vue.$store.state.Control.editionItem.id == 20210505){
            res.arr = ["鱼人","恶魔","海盗","野猪人","野兽","元素","机械","龙"];
            for(let i=0;i < banNumber;i++){
                let random = Math.floor(Math.random()*res.arr.length);
                res.ban.push(res.arr[random]);
                res.arr.splice(random,1);
            }
        }
        //纳迦版本
        else if(this.vue.$store.state.Control.editionItem.id == 20220511 || this.vue.$store.state.Control.editionItem.id == 20220831){
            res.arr = ["野猪人","恶魔","海盗","元素","野兽","机械","龙","纳迦","鱼人"];
            for(let i=0;i < banNumber;i++){
                let random = Math.floor(Math.random()*res.arr.length);
                res.ban.push(res.arr[random]);
                res.arr.splice(random,1);
            }
        }
        return res;
    }

    //初始化总的酒馆随从池
    InitAllMinions(){
        let query = new IQuery(this.vue);
        return new Promise(async (resolve,reject)=>{
            let Pub = this.vue.$store.state.Pub;//变量代换
            //清空池子
            this.vue.$store.state.Pub.allMinions = [];
            //加载图片资源
            let load = new Loading(this.vue);
            let imgUrlArr = [];
            //一星随从
            for(let i =0;i<6;i++){
                let minArr = undefined;
                switch(i){
                    case 0 : minArr = minions1;break;
                    case 1 : minArr = minions2;break;
                    case 2 : minArr = minions3;break;
                    case 3 : minArr = minions4;break;
                    case 4 : minArr = minions5;break;
                    case 5 : minArr = minions6;break;
                }
                for(let j = 0;j<minArr.length ?? -1;j++){
                    if(query.IsBelongVersion(minArr[j]) && query.NoneForbidden(minArr[j]) ){
                        let min = new Card(this.vue,'minion');
                        //加载随从属性
                        min.LoadingAttr(minArr[j],true,-1);
                        min.load(this.vue,min);
                        Pub.allMinions.push(min);
                        imgUrlArr.push(min.img);
                    }
                }
            }
            //开始加载随从图片资源
            await load.LoadingAllMinionsImage(imgUrlArr);
            resolve(true);
        })
    }
    
}

export default InitPub;

