import AnimationS from "../function/animation";

class Card{
    constructor(vue,classType){
        this.vue = vue;
        //实际坐标偏移
        this.x = 0,
        this.y = 0,

        this.id = null;//标识
        this.version = null,
        this.class = classType || "minion" || "spell",//类型，选项有minion(默认),spell
        this.name = null;//随从的名字
        this.ad = null;//攻击力
        this.hp = null;//生命值
        this.legend = false;//是否是传说随从

        this.star = null;//星级
        this.type = null;//类型：野兽...

        this.golden = false;//随从是否是金色

        this.duration = null;//持续时间，有的卡牌的暂时性的，-1表示永久，正数表示剩余回合
        this.describe = "";//随从/卡牌描述
        this.golden_describe = "";//金色随从的卡牌描述

        this.isFreeze = false;//是否被冻结
        
        //表示这个随从是否属于玩家
        this.isPlayer = false;

        this.img = "";//随从的图像

        //性质
        this.nature = {
            //亡语
            death_rattle : false,
            //战吼
            battle_cry : false,
            //圣盾
            divine_shield : false,
            //磁力
            magnetic : false,
            //超杀
            overkill : false,
            //剧毒
            poisonous : false,
            //复生
            reborn : false,
            //嘲讽
            taunt : false,
            //风怒
            wind_fury : false,
            //塑造法术
            spell_craft  : false,
            //复仇
            avenge  : false,
            //被动效果
            passive : false,
            //暴怒
            frenzy :false,
            //冻结
            frozen  : false,
            //免疫
            immune : false,
            //群伤
            aoe : false,
        }
        //临时性质
        this.temp_nature = {
            //圣盾
            divine_shield : false,
            //剧毒
            poisonous : false,
            //复生
            reborn : false,
            //嘲讽
            taunt : false,
            //风怒
            wind_fury : false,
        }

        //统计
        this.statistic = {
            damage : null,
            cure : null,
        }
        //数字寄存器
        this.numberRegister = 0;
        //布尔寄存器
        this.boolRegister = false;

        //外部加成容器
        this.additionContainer = [];

        //外挂随从技能
        this.load = function(){},

        

        //攻击前触发函数
        this.AttackBefore = [];
        //攻击后触发函数
        this.AttackAfter = [];
        //超杀后触发函数
        this.OverKill = [];

        //被攻击前触发函数
        this.BeAttackedBefore = [];
        //被攻击后触发函数队列(暴怒)
        this.GetHurt = [];

        //战斗回合开始触发函数队列（喷火龙）
        this.BattleBegin = [];
        //战斗回合结束触发函数队列
        this.BattleOver = [];

        //招募回合开始触发函数队列
        this.RecruitBegin = [];
        //招募回合结束触发函数队列
        this.RecruitOver = [];

        //放置随从触发函数队列（战吼）
        this.PutMinions = [];
        //使用随从卡触发函数低劣（海上掠夺者）
        this.UseTrigger = [];
        //该随从死亡触发队列
        this.AfterDead = [];

        //购买该随从触发函数队列
        this.Purchase = [];
        //出售该随从触发函数队列
        this.Sale = [];
        //酒馆升级触发函数队列
        this.UpgradePub = [];

        //卡牌被置入你的手牌触发函数队列
        this.JoinHands = [];
        //该卡被合成金卡触发函数队列
        this.BecomeGolden = [];
        //使用鲜血宝石触发函数
        this.UseBloodStone = [];
        //花费铸币后触发函数
        this.CostCoin = [];
        //召唤随从触发函数队列
        this.SummonMinions = [];
        
        //复仇触发航速
        this.Revenge = [];
        //失去圣盾时触发
        this.LostDivineShield = [];
        //获得属性时触发
        this.GetAttrAddition = [];
        //战斗中退场时触发
        this.OutBattle = [];
        //玩家释放法术后触发队列
        this.CatsSpell = [];
        //随从受到法术加成后执行函数队列
        this.GetSpell = [];
        
    }
    //法术的执行函数
    SpellExecute(){
        
    }
    //填装属性
    LoadingAttr(item,isMinion,duration){
        this.version = item.version;
        this.name = item.name;
        this.minion = isMinion;//是否是随从卡还是道具卡
        this.ad = item.ad;//攻击力
        this.hp = item.hp;//生命值

        this.star = item.star;//星级
        this.type = item.type;//类型：野兽。。。

        this.duration = duration; //持续时间，有的卡牌的暂时性的，-1表示永久，正数表示剩余回合
        this.describe = item.describe;//随从描述
        this.golden_describe = item.golden_describe;//随从描述
        this.legend = item.legend;

        this.img = item.img;
        this.load = item.load;
        if(item.golden) this.golden = item.golden;
        //填装加成
        for(let i=item.additionContainer ? item.additionContainer.length - 1 : -1;i >= 0;i--){
            let block = item.additionContainer[i];
            this.additionContainer.push(block);
        }
        
    }
    //实际攻击力
    RealAttack(fight = false){
        let ad = fight ? this.battleAttr.ad : this.ad;
        for(let i =0;i<this.additionContainer.length;i++){
            if(this.additionContainer[i].ad != null){
                ad += this.additionContainer[i].ad;
            }
        }
        return ad;
    }
    //实际生命值
    RealHealth(fight = false){
        let hp = fight ? this.battleAttr.hp : this.hp;
        for(let i =0;i<this.additionContainer.length;i++){
            if(this.additionContainer[i].hp != null){
                hp += this.additionContainer[i].hp;
            }
        }
        return hp;
    }

    /*-------------------------------触发随从的特性-----------------------------------*/
    //触发立即执行的战吼
    TriggerBattleCry(immediateFind = false){
        return new Promise(async (resolve,reject)=>{
            for(let i=0;i<this.PutMinions.length;i++){
                if(this.PutMinions[i].type == 'battleCryImmediate'){
                    let data = {
                        immediateFind : immediateFind,
                    }
                    await this.PutMinions[i].Execute(data);
                }
            }
            resolve(true);
        })
    }
    //触发选择性的战吼
    TriggerSelectBattleCry(handsNode,immediate = false){
        //handsNode为该战吼随从正处于手牌状态的结点
        for(let i=0;i<this.PutMinions.length;i++){
            //选择友军目标类型的战吼
            if(this.PutMinions[i].type == 'battleCrySelectFriendlyMinion'){
                if(!immediate){
                    let Block = this.PutMinions[i];
                    let mq = this.vue.$store.state.Player.minionsQueue;
                    let nodes = document.querySelectorAll('.player_row li');
                    //暂存目标执行块
                    this.vue.$store.state.Player.battleCryMounted.executeBlock = Block;
                    
                    //随从限制并高亮可以选择的目标
                    let tgtNumber = 0;
                    if(Block.battleCryLimit != null){
                        for(let j=0;j<mq.length;j++){
                            for(let k=0;k<Block.battleCryLimit.length;k++){
                                if(mq[j].type == Block.battleCryLimit[k] && mq[j] != this){
                                    //把节点展示
                                    nodes[j].classList.add('highlight');
                                    tgtNumber += 1;
                                }
                            }
                        }
                    }
                    //全选
                    else if(Block.battleCryLimit == null){
                        for(let j=0;j<mq.length;j++){
                            if(mq[j] != this){
                                nodes[j].classList.add('highlight');
                                tgtNumber += 1;
                            }
                        }
                    }
                    //找到自己数组索引
                    let index = -1;
                    for(let i=0;i<this.vue.$store.state.Player.handCardQueue.length;i++){
                        if(this.vue.$store.state.Player.handCardQueue[i] == this){
                            index = i;
                        }
                    }
                    
                    //保存该随从的DOM结点
                    this.vue.$store.state.Player.battleCryMounted.battleCryNode = handsNode;
                    //保存该战吼随从的数组索引
                    this.vue.$store.state.Player.battleCryMounted.battleCryIndex = index;

                    tgtNumber > 0 ? this.vue.$store.state.Control.battleCrySelect = true : null;
                    return tgtNumber > 0 ? true : false;
                }
                //立即触发(立即随机选中目标执行)
                else{
                    let mq = this.vue.$store.state.Player.minionsQueue;
                    //获得可以选择的所有目标
                    let indexArr=  [];
                    let Block = this.PutMinions[i];
                    if(Block.battleCryLimit != null){
                        //遍历友军
                        for(let j=0;j<mq.length;j++){
                            for(let k=0;k<Block.battleCryLimit.length;k++){
                                if(mq[j].type.search(Block.battleCryLimit[k]) != -1 && mq[j] != this){
                                    //把目标放入最终数组
                                    indexArr.push(j);
                                }
                            }
                        }
                    }
                    else for(let j in mq) indexArr.push(j);
                    if(indexArr.length == 0) return;
                    //随机选一个
                    let rand = Math.floor(Math.random()*indexArr.length);
                    //选中到的目标
                    let tgt = mq[indexArr[rand]];
                    let tgtNode = document.querySelectorAll('.player_row li')[indexArr[rand]];
                    Block.Execute(tgt,tgtNode);
                }
            }
            //选择友军和bob的战吼
            else if(this.PutMinions[i].type == 'battleCrySelectAllMinion'){
                let Block = this.PutMinions[i];
                let mq = this.vue.$store.state.Player.minionsQueue;
                let bq = this.vue.$store.state.Pub.bobMinionsQueue;
                let playerNodes = document.querySelectorAll('.player_row li');
                let bobNodes = document.querySelectorAll('.bob_row li');
                //暂存目标执行块
                this.vue.$store.state.Player.battleCryMounted.executeBlock = Block;
                //随从限制并高亮可以选择的目标
                let tgtNumber = 0;
                if(Block.battleCryLimit != null){
                    //高亮友军的
                    for(let j=0;j<mq.length;j++){
                        for(let k=0;k<Block.battleCryLimit.length;k++){
                            if(mq[j].type == Block.battleCryLimit[k] && mq[j] != this){
                                //把节点展示
                                playerNodes[j].classList.add('highlight');
                                tgtNumber += 1;
                            }
                        }
                    }
                    //高亮bob的
                    for(let j=0;j<bq.length;j++){
                        for(let k=0;k<Block.battleCryLimit.length;k++){
                            if(bq[j].type == Block.battleCryLimit[k] && bq[j] != this){
                                //把节点展示
                                bobNodes[j].classList.add('highlight');
                                tgtNumber += 1;
                            }
                        }
                    }
                }
                else{
                    //高亮友军的
                    for(let j in mq){
                        playerNodes[j].classList.add('highlight');
                        tgtNumber += 1;
                    }
                    //高亮bob的
                    for(let j in bq){
                        bobNodes[j].classList.add('highlight');
                        tgtNumber += 1;
                    }
                }
                //找到自己数组索引
                let index = -1;
                for(let i=0;i<this.vue.$store.state.Player.handCardQueue.length;i++){
                    if(this.vue.$store.state.Player.handCardQueue[i] == this){
                        index = i;
                    }
                }
                
                //保存该随从的DOM结点
                this.vue.$store.state.Player.battleCryMounted.battleCryNode = handsNode;
                //保存该战吼随从的数组索引
                this.vue.$store.state.Player.battleCryMounted.battleCryIndex = index;

                tgtNumber > 0 ? this.vue.$store.state.Control.battleCrySelect = true : null;
                return tgtNumber > 0 ? true : false;
            }
        }
        //执行到这表示选择型战吼没有目标可以选择的或者没有选择型战吼
        return false;
    }
    //触发三连后执行函数
    TriggerGoldenEvent(goldenTarget){
        for(let i in goldenTarget.BecomeGolden){
            goldenTarget.BecomeGolden.Execute(goldenTarget);
        }
    }
    //触发购买随从事件函数
    TriggerPurchaseEvent(purchaseTarget){
        //purchaseTarget是被购买的随从
        return new Promise((resolve,reject)=>{
            for(let i in this.Purchase){
                this.Purchase[i].Execute(purchaseTarget);
            }
            resolve(true);
        })
    }
    //触发卡牌被置入你的手牌触发函数队列
    TriggerJoinHands(){
        for(let i=0;i<this.JoinHands.length;i++){
            //立即触发的战吼
            this.JoinHands[i].Execute(this);
        }
    }
    //触发使用卡牌调用
    UseTriggerFunc(dataItem){
        //dataItem是所打出的手牌数据对象
        for(let i=0;i<this.UseTrigger.length;i++){
            this.UseTrigger[i].Execute(dataItem);
        }
    }
    //触发使用法术牌的调用
    TriggerCatsSpell(targetData,spellData){
        //targetData是被法术所指定到的随从
        //spellData是法术的实体
        return new Promise(async (resolve,reject)=>{
            for(let i in this.CatsSpell){
                let data = {
                    targetData : targetData,
                    spellData : spellData,
                }
                await this.CatsSpell[i].Execute(data);
            }
            resolve(true);
        })
    }
    //随从受到法术加持后执行函数
    TriggerGetSpell(spellItem){
        //spellItem被施加上的法术实体
        return new Promise(async (resolve,reject)=>{
            for(let i in this.GetSpell){
                await this.GetSpell.Execute(spellItem);
            }
            resolve(true);
        })
    }
    //触发回合开始时执行的函数
    RecruitBeginTrigger(){
        for(let i in this.RecruitBegin){
            this.RecruitBegin[i].Execute();
        }
    }
    //触发回合结束函数
    RecruitTrigger(){
        return new Promise(async (resolve,reject)=>{
            for(let i=0;i<this.RecruitOver.length;i++){
                await this.RecruitOver[i].Execute();
            }
            setTimeout(() => {
                resolve(true);
            }, 100);
        })
    }
    //触发酒馆升级事件函数
    TriggerUpgradePub(){
        for(let i in this.UpgradePub){
            this.UpgradePub[i].Execute();
        }
    }
    //出售随从
    TriggerSale(){
        return new Promise(async (resolve,reject)=>{
            for(let i in this.Sale){
                await this.Sale[i].Execute();
            }
            resolve(true);
        })
    }
    //触发鲜血宝石
    TriggerBloodStone(){
        for(let i in this.UseBloodStone){
            this.UseBloodStone[i].Execute();
        }
    }
    //触发选择型法术
    SpellSelect(){
        this.vue.$store.state.Control.battleCrySelect = true;
        //表示当前是法术选择阶段
        this.vue.$store.state.Player.battleCryMounted.isSpellCast = true;
        //高亮可以选择的目标
        let nodes = document.querySelectorAll('.player_row li');
        let mq = this.vue.$store.state.Player.minionsQueue;
        for(let i in mq){
            nodes[i].classList.add('highlight');
        }
        //暂存法术的实体,用于选择目标后的重新调用
        this.vue.$store.state.Player.battleCryMounted.executeBlock = this;
    }
    //法术的默认执行函数
    SpellExecute(dataItem){
        console.log("这是默认执行的法术函数，获取的目标为：",dataItem);
    }
    //花费铸币后触发函数
    TriggerCostCoin(costValue){
        //costValue是花费的铸币值
        return new Promise((resolve,reject)=>{
            for(let i in this.CostCoin){
                this.CostCoin[i].Execute(costValue)
            }
            resolve(true);
        })
    }
    //提升属性时触发
    TriggerGetAttrAddition(target){
        //target提升属性者
        return new Promise((resolve,reject)=>{
            for(let i in this.GetAttrAddition){
                this.GetAttrAddition[i].Execute(target);
            }
            resolve(true);
        })
    }
    
    
    //------------------------------------------ 战斗阶段----------------------------------------
    //战斗准备节点
    TriggerPrepareFight(){
        return new Promise(async (resolve,reject)=>{
            for(let i in this.BattleBegin) await this.BattleBegin[i].Execute();
            resolve(true);
        })
    }
    //随从发起攻击前触发
    TriggerBeforeAttack(attacker,driver,receiver){
        //attacker进攻者的数据
        return new Promise(async (resolve,reject)=>{
            for(let i in this.AttackBefore){
                let data = {
                    attacker : attacker,
                    receiver : receiver,
                    driver : driver
                }
                await this.AttackBefore[i].Execute(data)
            }
            resolve(true);
        })
    }
    //随从受到攻击前触发
    TriggerBeforeBeAttacked(attacker,receiver,driver){
        return new Promise(async (resolve,reject)=>{
            for(let i in this.BeAttackedBefore){
                let data = {
                    attacker : attacker,
                    receiver : receiver,
                    driver : driver,
                }
                await this.BeAttackedBefore[i].Execute(data);
            }
            resolve(true);
        })
    }
    //触发超杀
    TriggerOverKill(index){
        return new Promise(async (resolve,reject)=>{
            for(let i in this.OverKill){
                let data = {
                    index : index,
                }
                await this.OverKill[i].Execute(data);
            }
            resolve(true);
        })
    }
    //随从发起攻击后立即触发
    TriggerAfterAttack(attacker,receiver,driver){
        return new Promise(async (resolve,reject)=>{
            for(let i in this.AttackAfter){
                let data = {
                    attacker : attacker,
                    receiver : receiver,
                    driver : driver
                }
                await this.AttackAfter[i].Execute(data);
            }
            resolve(true);
        })
    }


    //随从被召唤出来时触发效果
    TriggerSummonMinions(summonOne,node,driver = null){
        //summonOne被召唤出来的随从
        return new Promise(async (resolve,reject)=>{
            for(let i in this.SummonMinions){
                let data = {
                    summonOne : summonOne,
                    driver : driver,
                }
                await this.SummonMinions[i].Execute(data,node);
            }
            resolve(true);
        })
    }
    //受到伤害触发的函数
    TriggerGetHurt(damageSource,damageValue,driver){
        //damageSource伤害来源者
        //damageValues是伤害量
        return new Promise(async (resolve,reject)=>{
            for(let i in this.GetHurt){
                let data = {
                    damageSource : damageSource,
                    damageValue : damageValue,
                    driver : driver,//对局驱动器实例
                }
                await this.GetHurt[i].Execute(data);
            }
            resolve(true);
        })
    }
    //触发亡语
    TriggerDeathRattle(data){
        return new Promise(async (resolve,reject)=>{
            //传入死亡随从的原数组下标
            for(let i in this.AfterDead){
                await this.AfterDead[i].Execute(data);
            }
            resolve(true);
        })
    }
    //触发复仇函数队列
    TriggerRevenge(dead,driver = null){
        let ams = new AnimationS(this.vue);
        return new Promise(async (resolve,reject)=>{
            for(let i in this.Revenge){
                let data = {
                    dead : dead,
                    driver : driver
                }
                await this.Revenge[i].Execute(data)
            }
            await ams.Delay(100);
            resolve(true);
        })
    }
    //触发退场函数队列
    TriggerOutBattle(index){
        //index为退场随从在原始数组的数组下标位置
        return new Promise(async (resolve,reject)=>{
            for(let i in this.OutBattle){
                await this.OutBattle[i].Execute(index);
            }
            resolve(true);
        })
    }
    //触发失去圣盾函数队列
    TriggerLostDivineShield(driver,lostOne){
        return new Promise(async (resolve,reject)=>{
            for(let i in this.LostDivineShield){
                let data = {
                    driver : driver,
                    lostOne : lostOne//失去圣盾的人
                }
                await this.LostDivineShield[i].Execute(data);
            }
            resolve(true);
        })
    }

}

export default Card;