                let add_num = target.golden ? 2 : 1;
                //拿到所有随从
                let minions = that.$store.state.Player.minionsQueue;
                let classArr = [];
                for(let i=0;i<minions.length;i++){
                    let repeat = false;
                    for(let j=0;j<classArr.length;j++){
                        if(classArr[j].type == minions[i].type){
                            repeat = true;
                            break;
                        }
                    }
                    if(minions[i].type == '混合' || !repeat && minions[i].type != "中立"){
                        let obj = {
                            type : minions[i].type,
                            index : i,
                        }
                        classArr.push(obj);
                    }
                }
                //获得一个不重复的种族数组
