<template>
    <div class="index_panel">
        <!-- 容器 -->
        <div class="container">
            <!-- 版本海报 -->
            <div class="version_post">
                <img :src="VersionPostUrl(currentVersion)" alt="" class="cover">
                <div class="icon"></div>
            </div>
            <div class="frame"></div>
            <div class="title">选 择 版 本</div>
            <div class="describe_text">{{ currentVersion.describe }}</div>
            <!-- 选择列表 -->
            <div class="version_list">
                <li v-for="(v,index) in versions" :key="v.index" @click="SelectThisVersion(index)">
                    <span v-if="v.id == currentVersion.id" class="current">{{ v.name }}</span>
                    <span v-if="v.id != currentVersion.id" class="disCurrent">{{ v.name }}</span>
                </li>
            </div>
            <!-- 开始按钮 -->
            <div class="start_btn" ref="start_btn" 
            @touchstart="$refs.start_btn.classList.add('click')" @touchend="$refs.start_btn.classList.remove('click')"
            @click="StartSelectHero">
                <img :src="require('../assets/ui/ui (45).png')"  class="cover" alt="">
            </div>
        </div>
        
        
    </div>
</template>

<script>
import versionData from '../mixin/json/version.json'
export default {
    name : 'Version',
    data(){
        return{
            versions: versionData ,
            //当前选择的版本
            currentVersion : versionData[2],
        }
    },
    methods:{
        //版本海报地址
        VersionPostUrl(target){
            let url = "";
            switch(target.id){
                case 20191106 :url = require("../assets/bg/bg (18).png");break;
                case 20210505 :url = require("../assets/bg/bg (11).png");break;
                case 20220511 :url = require("../assets/bg/bg (12).png");break;
                case 20220831 :url = require("../assets/bg/bg (19).png");break;
            }
            return url;
        },
        //更改当前版本
        SelectThisVersion(id){
            this.currentVersion = this.versions[id];
        },
        //开始选择英雄界面
        StartSelectHero(){
            //修改版本控制信息
            this.$store.state.Control.editionItem = this.currentVersion;
            //关闭这个界面
            this.$store.state.Control.versionView = false;
            //打开选择英雄界面
            this.$store.state.Control.selectHeroView = true;
        }
    },
    mounted(){
        this.currentVersion = versionData[2];
    }
}
</script>

<style lang="less">

.index_panel{
    left: 0; top: 0;position: absolute;
    width: 100vw;height: 100vh;
    margin: 0 0 0 0;
    transition: all .3s ease-out;
    background: url('../assets/bg/bg\ \(8\).png')no-repeat;
    background-size: 100% 100%;
    .container{
        left: 50%; top: 50%;
        width: 78vw; height: 44vw;
        margin: -22vw 0 0 -39vw;
        .version_post{
            left: 3vw; top: 3.5vw;
            width: 51.8vw;height: 29.6vw;
            .icon{
                left: 1vw; bottom: .5vw;
                width: 10vw;height: 10vw;
                background: url('../assets/ui/ui\ \(46\).png')no-repeat;
                background-size: 100% 100%;
            }
        }
        .frame{
            left: 0; top: 0;
            width: 100%;height: 100%;
            background: url('../assets/bg/bg\ \(9\).png')no-repeat;
            background-size: 100% 100%;
        }
        .title{
            font-family: 'FZLBJW';
            position: absolute; left: 0; top: 0.5vw;
            height: 3vw;width: 57.5vw;
            font-size: 1.8vw;
            text-align: center;
            line-height: 3vw;
            color: rgb(228, 217, 207);
            text-shadow: 0 1px rgb(0, 0, 0), 1px 0 black, -1px 0 black, 0 -1px black;
            font-weight: bolder;
        }
        .describe_text{
            left: 5vw; top: 34vw;
            width: 47vw; height: 6vw;
            text-align: left;
            font-size: 1.5vw;
            color: rgb(71, 36, 0);
            font-weight: bolder;
        }
        .version_list{
            right: 4vw; top: 8vw;
            width: 15vw; height: 22vw;
            // background: rgba(0, 0, 0, 0.647);
            li{
                position: relative;
                width: 11vw; height: 2.8vw;
                
                margin: 1vw 0 0 2vw;
                
                background: url('../assets/bg/bg\ \(10\).png')no-repeat;
                background-size: 100% 100%;
                .current,.disCurrent{
                    left: 0; top: 0;
                    font-size: 1.3vw;
                    line-height: 2.8vw;
                    width: 100%; height: 100%;
                    color: rgb(255, 238, 228);
                }
                .disCurrent{
                    color: rgb(139, 132, 128);
                }
            }
        }
        .start_btn{
            bottom: 2.1vw; right: 6.9vw;
            width: 9.3vw;height: 9.3vw;
            transition: all .1s;
        }
        .start_btn.click{
            transform: scale(.9);
        }
        
        
    }
    
}
</style>