import AnimationS from "./animation";
import IQuery from "./iquery";
import Card from "../data/card";
import HeroControl from "./herocontrol";
// import DiscoverCard from "../spell/discover";
//公共体,提供公共的方式
class Commons{
    constructor(vue){
        this.vue = vue;
        //屏幕的宽度
        this.screenW = vue.$store.state.ScreenSize.width;
        //屏幕的高度
        this.screenH = vue.$store.state.ScreenSize.height;
        //是否可拖动随从
        this.dragAvailable = true;
        //是否可以拖动卡牌
        this.dragCardAvailable = true;
        //拖动的dom实体
        this.dragDOMItem = null;
        //拖动目标的数据实体在其数组中的下标
        this.dragIndex = -1;
        //拖动的dom实体属于bob还是玩家
        this.dragBelongBob = true;
        //拖动dom实体与触摸事件的内部相对偏移
        this.innerX = 0;
        this.innerY = 0;

        //查询类
        this.query = new IQuery(vue);

    }
    //根据点击开始的偏移值得出目标的数据实体,返回数据实体在其源数组下标
    GetMinionData(touch){
        let res = {
            index : -1,
            isBob : true,
        }
        let x = touch.touches[0].pageX;
        let y = touch.touches[0].pageY;
        if(y > 0.5 * this.screenH){
            res.isBob = false;
        }
        this.dragBelongBob = res.isBob;
        let minions = res.isBob ? document.querySelectorAll('.bob_row li') : document.querySelectorAll('.player_row li');
        for(let i=0;i<minions.length;i++){
            if(x >= minions[i].offsetLeft && x <= minions[i].offsetLeft + 0.13 * this.screenH){
                res.index = i;
                return res;
            }
        }
        return res;
    }
    
    //在father创建一个随从dom实体
    CreateMinionDom(item,isBob = true){
        //item：是随从的数据实体
        //bob：true表示新建的随从是bob的随从
        //father：在father的节点下插入随从结点
        //创建随从实体
        let minion = document.createElement("li");
        //背景光效
        let saleLoop = document.createElement('div');
        saleLoop.classList.add('sale_avail');
        minion.appendChild(saleLoop);
        //嘲讽标记
        if(item.nature.taunt || item.temp_nature.taunt){
            let taunt = document.createElement('div');
            item.golden ? taunt.classList.add('golden_taunt') : taunt.classList.add('taunt');
            minion.appendChild(taunt);
        }
        //随从的边框
        let frame = document.createElement("div");
        //随从的图像
        let minImg = document.createElement('div');
        minImg.classList.add('min_img');
        let img = document.createElement("img");
        img.src = item.img;
        minImg.appendChild(img);
        if(item.golden){
            minion.appendChild(minImg);
            frame.classList.add('golden_frame');
            minion.appendChild(frame);
        }else{
            frame.classList.add('frame');
            minion.appendChild(frame);
            minion.appendChild(minImg);
        } 
        
        
        //复生标记
        if(item.nature.reborn || item.temp_nature.reborn){
            let reborn = document.createElement('div');
            reborn.classList.add('reborn');
            minion.appendChild(reborn);
        }
        
        //传说标志
        if(item.legend){
            let legend = document.createElement('div');
            item.golden ? legend.classList.add('golden_legend') : legend.classList.add('legend');
            minion.appendChild(legend);
        }
        //被动标记
        if(item.nature.passive){
            let passive = document.createElement('div');
            passive.classList.add('passive');
            minion.appendChild(passive);
        }
        //暴怒标记
        if(item.nature.frenzy){
            let frenzy = document.createElement('div');
            frenzy.classList.add('frenzy');
            minion.appendChild(frenzy);
        }
        //亡语标记
        if(item.nature.death_rattle){
            let deathRattle = document.createElement('div');
            deathRattle.classList.add('death_rattle');
            minion.appendChild(deathRattle);
        }
        //剧毒
        if(item.nature.poisonous || item.temp_nature.poisonous){
            let poisonous = document.createElement('div');
            poisonous.classList.add('poisonous');
            minion.appendChild(poisonous);
        }
        //随从的攻击力
        let ad = document.createElement("div");
        ad.classList.add('ad');
        let adNumber = document.createElement('div');
        adNumber.classList.add('number');
        adNumber.innerText = item.RealAttack();
        adNumber.innerText > item.ad ? adNumber.classList.add('higher') : null;
        ad.appendChild(adNumber);
        minion.appendChild(ad);
        //随从的生命值
        let blood = document.createElement("div");
        let bloodNumber = document.createElement('div');
            blood.classList.add('blood');
            bloodNumber.classList.add('number');
            bloodNumber.innerText = item.RealHealth();
            bloodNumber.innerText > item.hp ? bloodNumber.classList.add('higher') : null;
        blood.appendChild(bloodNumber);
        minion.appendChild(blood);
        //风怒表示
        if(item.nature.wind_fury || item.temp_nature.wind_fury){
            let windFury = document.createElement('div');
            for(let i=0;i<3;i++){
                let tape = document.createElement('span');
                tape.classList.add(`tape${i+1}`);
                windFury.appendChild(tape);
            }
            windFury.classList.add('wind_fury_mask');
            minion.appendChild(windFury);
        }
        //圣盾标记
        if(item.nature.divine_shield || item.temp_nature.divine_shield){
            let divineShield = document.createElement('div');
            divineShield.classList.add('divine_shield')
            minion.appendChild(divineShield);
        }
        //随从等级标记
        if(isBob && !this.vue.$store.state.Control.isFighting){
            let starBar = document.createElement('div');
            starBar.classList.add('star_bar');
            let img = document.createElement("img");
            img.classList.add('cover');
            switch(item.star){
                case 1 : 
                    img.src = require("../../assets/ui/ui (11).png");
                    break;
                case 2 :
                    img.src = require("../../assets/ui/ui (15).png");
                    break;
                case 3 :
                    img.src = require("../../assets/ui/ui (16).png");
                    break;
                case 4 :
                    img.src = require("../../assets/ui/ui (17).png");
                    break;
                case 5 :
                    img.src = require("../../assets/ui/ui (18).png");
                    break;
                case 6 :
                    img.src = require("../../assets/ui/ui (19).png");
                    break;
            }
            starBar.appendChild(img);
            minion.appendChild(starBar);
        }
        //判断是否构成三连条件(从酒馆角度)
        if(isBob){
            let res = this.query.SatisfiedGolden(item,true);
            if(res){
                let comboTip = document.createElement('div');
                comboTip.classList.add('combo_tip');
                minion.appendChild(comboTip);
            }
        }
        //给该随从绑定事件
        if(!this.vue.$store.state.Control.isFighting) this.BindMinionsEvent(minion);
        return minion;
        
    }
    //给随从的DOM节点绑定触屏事件
    BindMinionsEvent(targetDom){
        let that = this;
        //添加触摸事件
        targetDom.addEventListener('touchstart',function(e){
            if(that.vue.$store.state.Control.isFighting) return;
            if(that.vue.$store.state.Control.battleCrySelect) return;
            let isBob = true;
            targetDom.offsetTop < that.screenH * 0.45 ? isBob = true : isBob = false;
            let NodeArr = isBob ? document.querySelectorAll('.bob_row li') : document.querySelectorAll('.player_row li');
            for(let i=0;i<NodeArr.length;i++){
                if(targetDom == NodeArr[i]){
                    that.dragIndex = i;
                    break;
                }
            }
            if(that.dragIndex == -1){
                console.log('没有获取到目标索引');
                return;
            }
            that.dragDOMItem = targetDom;
            that.dragBelongBob = isBob;
            targetDom.style.zIndex = "100";
            //计算触摸开始时和目标结点内部的相对偏移
            that.innerX = e.touches[0].pageX - targetDom.offsetLeft;
            that.innerY = e.touches[0].pageY - targetDom.offsetTop;
        })
        //添加拖动事件
        targetDom.addEventListener('touchmove',function(e){
            if(that.vue.$store.state.Control.isFighting) return;
            if(that.vue.$store.state.Control.battleCrySelect){
                return;
            }
            if(that.dragDOMItem == null){
                return;
            }
            targetDom.style.left = e.touches[0].pageX -  that.innerX + 'px';
            targetDom.style.top = e.touches[0].pageY - that.innerY + 'px' ;
            if(that.query.LocateSaleArea(e.touches[0]) && !that.dragBelongBob || that.query.LocatePurchaseArea(e.touches[0]) && that.dragBelongBob)  targetDom.children[0].style.display = 'block';
            else targetDom.children[0].style.display = 'none';
        })
        //添加触摸结束事件
        targetDom.addEventListener('touchend',async function(e){
            if(that.vue.$store.state.Control.isFighting) return;
            if(that.vue.$store.state.Control.battleCrySelect){
                return;
            }
            let ams = new AnimationS(that.vue);
            //拖动的是bob的随从
            if(that.dragBelongBob){
                //是否处于购买区
                if(that.query.LocatePurchaseArea(e.changedTouches[0])){
                    that.PurchaseMinions(targetDom);
                    targetDom.style.zIndex = "0";
                    targetDom.children[0].style.display = 'none';
                    return;
                }
                that.AdjustMinionsCoordinate(true);
                //过渡效果
                ams.ControlMinionsTransition("bob",200,true);
                await ams.Delay(200);
                ams.ControlMinionsTransition('bob',200,false);
            }
            //拖动的是玩家的随从
            else{
                //出售随从
                if(that.query.LocateSaleArea(e.changedTouches[0])){
                    await that.SaleMinions();
                }
                //调整随从位置
                if(that.query.LocateMinionsArea(e.changedTouches[0],false)){
                    await that.InsertIntoBattleField(targetDom,that.dragIndex,false);
                }
                that.AdjustMinionsCoordinate(false);
                //过渡效果
                ams.ControlMinionsTransition("player",200,true);
                await ams.Delay(200);
                ams.ControlMinionsTransition('player',200,false);
            }
            targetDom.children[0].style.display = 'none'
            targetDom.style.zIndex = "0";
        })
        //添加点击事件
        targetDom.addEventListener('click',async function(e){
            if(!that.vue.$store.state.Control.battleCrySelect){
                return;
            }
            //判断该随从是bob的还是玩家的
            let isBob = true;
            if(targetDom.offsetTop > 0.45 * that.screenH) isBob = false;
            //保存目标在源数组中的索引
            let index = -1;
            //清除高亮
            let bobNodes = document.querySelectorAll('.bob_row li');
            let playerNodes = document.querySelectorAll('.player_row li');
            for(let i=0;i<bobNodes.length;i++){
                bobNodes[i].classList.remove('highlight');
                if(bobNodes[i] == targetDom && isBob) index = i;
            }
            for(let i=0;i<playerNodes.length;i++){
                playerNodes[i].classList.remove('highlight');
                if(playerNodes[i] == targetDom && !isBob) index = i;
            }
            let targetData = isBob ? that.vue.$store.state.Pub.bobMinionsQueue[index] : that.vue.$store.state.Player.minionsQueue[index];
            let ams = new AnimationS(that.vue);
            //法术选择
            if(that.vue.$store.state.Player.battleCryMounted.isSpellCast){
                const handCardLength = that.vue.$store.state.Player.handCardQueue.length;
                let minionLength = that.vue.$store.state.Player.minionsQueue.length;
                //实际执行
                that.vue.$store.state.Player.battleCryMounted.executeBlock.SpellExecute(targetData);
                //调整手牌
                if(handCardLength != that.vue.$store.state.Player.handCardQueue.length){
                    that.AdjustCardCoordinate(1);
                    that.AdjustCardCoordinate(2);
                    await ams.TransitionEffect(false,false,true,200);
                }
                //关闭选择界面
                that.vue.$store.state.Control.battleCrySelect = false;
                //调整场上随从位置
                if(minionLength != that.vue.$store.state.Player.minionsQueue.length){
                    that.AdjustMinionsCoordinate(true);
                    await ams.TransitionEffect(true,true,true,200);
                }
                // //触发受到法术施加随从的效果
                // targetData.TriggerGetSpell(that.vue.$store.state.Player.battleCryMounted.executeBlock);
                
                
                
                //触发场上友方随从的释放法术后的效果
                for(let i in that.vue.$store.state.Player.minionsQueue){
                    await that.vue.$store.state.Player.minionsQueue[i].TriggerCatsSpell(targetData,that.vue.$store.state.Player.battleCryMounted.executeBlock);
                }
                //状态重置
                that.vue.$store.commit('ResetStatus');
                
            }
            else if(that.vue.$store.state.Player.battleCryMounted.isHeroSelect){
                let minionLength = that.vue.$store.state.Player.minionsQueue.length;
                //实际调用英雄的技能
                that.vue.$store.state.Player.battleCryMounted.executeBlock.Execute(targetData,targetDom);
                //关闭选择界面
                that.vue.$store.state.Control.battleCrySelect = false;
                //调整场上随从位置
                if(minionLength != that.vue.$store.state.Player.minionsQueue.length){
                    that.AdjustMinionsCoordinate(true);
                    await ams.TransitionEffect(true,true,true,200);
                }
                //状态重置
                that.vue.$store.commit('ResetStatus');
                //把英雄技能反面
                let hc = new HeroControl(that.vue);
                hc.RotatePowerBtn(true);
            }
            //执行战吼
            else{
                //插入手牌(战吼随从从手牌插入战场)
                let res = await that.InsertIntoBattleField(that.vue.$store.state.Player.battleCryMounted.battleCryNode,that.vue.$store.state.Player.battleCryMounted.battleCryIndex,true);
                that.AdjustMinionsCoordinate(false);
                that.AdjustMinionsCoordinate(true);
                //调整手牌位置
                that.AdjustCardCoordinate(1);
                //过渡
                await ams.TransitionEffect(true,true,true,200);
                for(let i=0;i<res.dataItem.PutMinions.length;i++){
                    res.dataItem.PutMinions[i].Execute(targetData,targetDom);
                }
                that.vue.$store.state.Control.battleCrySelect = false;
                //调用所有打出手牌触发效果的随从
                for(let i=0;i<that.vue.$store.state.Player.minionsQueue.length;i++){
                    that.vue.$store.state.Player.minionsQueue[i].UseTriggerFunc(res.dataItem);
                }
            }
        })
    }
    //在创建一张手牌DOM实体
    CreateNewHandCard(dataItem,cardClass){
        //dataItem ： 数据实体
        //cardClass ： 创建随从卡="minion",创建法术卡="spell"
        //创建一张卡牌实例
        let card = document.createElement('li');
        //法术卡绿色背景
        let greenLightSpell = document.createElement('div');
        greenLightSpell.classList.add('green_backlight_spell');
        card.appendChild(greenLightSpell);
        //随从或者法术的图像
        let minionImg = document.createElement('div');
        if(cardClass == 'minion') minionImg.classList.add('minion_img');
        else if(cardClass == 'spell') minionImg.classList.add('spell_img');
        let mImg = document.createElement("img");
        mImg.src = dataItem.img;
        mImg.classList.add('cover');
        minionImg.appendChild(mImg);
        card.appendChild(minionImg);
        //随从或者法术的卡牌框
        let cardFrame = document.createElement('img');
        if(cardClass == 'minion'){
            cardFrame.classList.add('card_frame');
            cardFrame.src = dataItem.golden ? require("../../assets/card/card (4).png") : require("../../assets/card/card (3).png"); 
        }
        else if(cardClass == 'spell'){
            dataItem.golden ? cardFrame.classList.add('golden_spell_frame') : cardFrame.classList.add('spell_card_frame');
            cardFrame.src = dataItem.golden ? require("../../assets/card/card (11).png") : require("../../assets/card/card (10).png")
        }
        card.appendChild(cardFrame);
        //添加传说标记
        if(dataItem.legend){
            let legendFrame = document.createElement('div');
            dataItem.golden ? legendFrame.classList.add('golden_legend_icon'):legendFrame.classList.add('legend_icon');
            card.appendChild(legendFrame);
             
        }
        
        //随从的等级标记
        if(cardClass == 'minion'){
            let star_bar = document.createElement('div');
            star_bar.classList.add('star_bar');
            let img = document.createElement("img");
            img.classList.add('cover');
            switch(dataItem.star){
                case 1 : img.src = require("../../assets/ui/ui (11).png");break;
                case 2 :img.src = require("../../assets/ui/ui (15).png");break;
                case 3 :img.src = require("../../assets/ui/ui (16).png");break;
                case 4 :img.src = require("../../assets/ui/ui (17).png");break;
                case 5 :img.src = require("../../assets/ui/ui (18).png");break;
                case 6 : img.src = require("../../assets/ui/ui (19).png");break;
            }
            star_bar.appendChild(img);
            card.appendChild(star_bar);
        }
        else if(cardClass == 'spell'){
            let coin = document.createElement('div');
            coin.classList.add('coin');
            if(dataItem.cost > 0) coin.innerText =  dataItem.cost;
            card.appendChild(coin);
        }
        //名字
        let name = document.createElement('div');
        if(cardClass == 'minion') dataItem.golden ? name.classList.add('golden_name') : name.classList.add('name');
        else if(cardClass == 'spell') dataItem.golden ? name.classList.add('spell_golden_name') : name.classList.add('spell_name');
        let nameText = document.createElement('div');
        nameText.classList.add('text');
        nameText.innerText = dataItem.name;
        name.appendChild(nameText);
        card.appendChild(name);
        //描述
        let describe = document.createElement('div');
        if(cardClass == 'minion') dataItem.golden ? describe.classList.add('golden_describe') : describe.classList.add('describe');
        else if(cardClass == 'spell') dataItem.golden ? describe.classList.add('golden_spell_describe') : describe.classList.add('spell_describe');
        describe.innerText = dataItem.describe;
        card.appendChild(describe);

        if(cardClass == 'minion'){
            //随从的攻击力
            let ad = document.createElement("div");
            let adNumber = document.createElement('div')
            adNumber.classList.add('number');
            adNumber.innerText = dataItem.RealAttack();
            adNumber.innerText > dataItem.ad ? adNumber.classList.add('higher') : null;
            ad.classList.add('ad');
            ad.appendChild(adNumber);
            card.appendChild(ad);
            //随从的生命值
            let blood = document.createElement("div");
            let bloodNumber = document.createElement('div');
            bloodNumber.classList.add('number');
            bloodNumber.innerText = dataItem.RealHealth();
            bloodNumber.innerText > dataItem.hp ? bloodNumber.classList.add('higher') : null;
            blood.classList.add('blood');
            blood.appendChild(bloodNumber);
            card.appendChild(blood);
            //随从卡的类型
            if(cardClass == 'minion'){
                let type = document.createElement('div');
                dataItem.golden ? type.classList.add('golden_type') : type.classList.add('type');
                type.innerText = dataItem.type;
                card.appendChild(type);
            }
            this.BindHandCardEvent(card);
        }
        else{
            if(dataItem.type == 'spellCraft'){
                let spellCraftMask = document.createElement('div');
                spellCraftMask.classList.add('spell_craft_mask');
                card.appendChild(spellCraftMask);
            }
            this.BindSpellTouchEvent(card);
        }
        return card;
    }
    //给手牌的DOM节点绑定触屏事件
    BindHandCardEvent(targetDom){
        //targetDom : 需要绑定事件的DOM节点
        let that = this;
        targetDom.addEventListener('touchstart',function(e){
            if(that.vue.$store.state.Control.isFighting) return;
            let lis = document.querySelectorAll('.hand_card_select_area li');
            for(let i =0;i<lis.length;i++){
                if(targetDom == lis[i]){
                    that.dragIndex = i;
                    break;
                }
            }
            that.dragDOMItem = targetDom;
            targetDom.style.zIndex = "100";
            targetDom.style.transition = "all .2s";
            targetDom.style.marginTop = 0.1 * that.screenW + 'px';
            targetDom.style.transform = "rotate(0) scale(2)";
            //计算触摸开始时和目标结点内部的相对偏移
            that.innerX = (e.touches[0].pageX - targetDom.offsetLeft) - (that.dragIndex - lis.length/2) * 0.02 * that.screenW;
            that.innerY = (e.touches[0].pageY - targetDom.offsetTop)  + 0.07 * that.screenW ;
        })
        targetDom.addEventListener('touchmove',function(e){
            if(that.vue.$store.state.Control.isFighting) return;
            targetDom.style.transition = 'none';
            targetDom.style.marginTop = "0";
            targetDom.style.left = e.touches[0].pageX -  that.innerX  + 'px';
            targetDom.style.top = e.touches[0].pageY - that.innerY+  'px' ;
            targetDom.style.transform = "rotate(0) scale(.7)";
            if(that.query.LocateMinionsArea(e.touches[0],true)) targetDom.children[0].style.display = 'block';
            else targetDom.children[0].style.display = 'none';
        })
        targetDom.addEventListener("touchend",async function(e){
            if(that.vue.$store.state.Control.isFighting) return;
            setTimeout(() => {
                targetDom.style.transition = 'none';
            }, 150);
            if(that.query.LocateMinionsArea(e.changedTouches[0],true)  && that.vue.$store.state.Player.minionsQueue.length < 7){
                //触发随从选择型战吼
                let lis = document.querySelectorAll('.hand_card_select_area li');
                for(let i=0;i<lis.length;i++){
                    if(lis[i] == targetDom){
                        if(that.vue.$store.state.Player.handCardQueue[i].nature.battle_cry){
                            let info = that.vue.$store.state.Player.handCardQueue[i].TriggerSelectBattleCry(targetDom);
                            if(info){
                                targetDom.children[0].style.display = 'none';
                                return;
                            }
                        }
                        break;
                    }
                }
                let ams = new AnimationS(that.vue);
                //正式插入战场
                let res = await that.InsertIntoBattleField(that.dragDOMItem,that.dragIndex,true);
                //调整手牌位置
                that.AdjustCardCoordinate(1);
                //调整随从位置
                that.AdjustMinionsCoordinate(false);
                await ams.TransitionEffect(true,false,true,200);
                //触发随从立即型战吼
                if(res.dataItem.nature.battle_cry){
                    let battleCryTimes = that.query.BattleCryTimes();
                    for(let i=0;i<battleCryTimes;i++){
                        await res.dataItem.TriggerBattleCry();
                    }
                }
                //调用所有打出手牌触发效果的随从
                for(let i=0;i<that.vue.$store.state.Player.minionsQueue.length;i++){
                    that.vue.$store.state.Player.minionsQueue[i].UseTriggerFunc(res.dataItem);
                }
                //触发金卡的三连奖励
                if(res.dataItem.golden) that.ComboReward();
            }
            else{
                //恢复初始状态
                targetDom.style.transform = "rotate(0) scale(.7)";
                targetDom.style.marginTop = "0";
                targetDom.style.marginTop = "0";
                targetDom.style.zIndex = "0";
                targetDom.children[0].style.display = 'none';
                that.AdjustCardCoordinate(1);
                let ams = new AnimationS(that.vue);
                await ams.HandCardRebound();
            }   
        })
    }
    //给法术卡绑定触屏事件
    BindSpellTouchEvent(targetDom){
        let that = this;
        targetDom.addEventListener('touchstart',function(e){
            if(that.vue.$store.state.Control.isFighting) return;
            let lis = document.querySelectorAll('.hand_card_select_area li');
            for(let i =0;i<lis.length;i++){
                if(targetDom == lis[i]){
                    that.dragIndex = i;
                    break;
                }
            }
            that.dragDOMItem = targetDom;

            targetDom.style.zIndex = "100";
            targetDom.style.transform = "rotate(0) scale(2)";
            targetDom.style.transition = "all .2s";
            targetDom.style.marginTop = 0.1 * that.screenW + 'px';
            //计算触摸开始时和目标结点内部的相对偏移
            that.innerX = (e.touches[0].pageX - targetDom.offsetLeft) - (that.dragIndex - lis.length/2) * 0.02 * that.screenW;
            that.innerY = (e.touches[0].pageY - targetDom.offsetTop)  + 0.07 * that.screenW ;
        })
        targetDom.addEventListener('touchmove',async function(e){
            if(that.vue.$store.state.Control.isFighting) return;
            targetDom.style.transition = 'none';
            targetDom.style.marginTop = "0";
            targetDom.style.left = e.touches[0].pageX -  that.innerX  + 'px';
            targetDom.style.top = e.touches[0].pageY - that.innerY+  'px' ;
            targetDom.style.transform = "rotate(0) scale(.7)";
        })
        targetDom.addEventListener('touchend',async function(e){
            if(that.vue.$store.state.Control.isFighting) return;
            //手指在施法区域
            if(that.query.LocateSpellCastArea(e.changedTouches[0].pageY)){
                //选择型法术
                if(that.vue.$store.state.Player.handCardQueue[that.dragIndex].selectSpell){
                    if(that.vue.$store.state.Player.handCardQueue[that.dragIndex].selectPlayer && that.vue.$store.state.Player.minionsQueue.length == 0){
                        that.vue.$store.commit('Alert','没有可选择目标！');
                        targetDom.style.transform = "rotate(0) scale(1)";
                        targetDom.style.marginTop = "0";
                        targetDom.style.zIndex = "0";
                        that.AdjustCardCoordinate(1);
                        let ams = new AnimationS(that.vue);
                        await ams.HandCardRebound();
                        return;
                    }
                    targetDom.style.opacity = '0';
                    //触发该法术的选择效果
                    that.vue.$store.state.Player.handCardQueue[that.dragIndex].SpellSelect(that.dragDOMItem);
                    
                }   
                //非选择法术
                else{
                    targetDom.style.transition = 'all .2s';
                    let ams = new AnimationS(that.vue);
                    targetDom.style.opacity = "1";
                    await ams.Delay(1);
                    targetDom.style.opacity = "0";
                    await ams.Delay(200);
                    that.vue.$store.state.Player.handCardQueue[that.dragIndex].SpellExecute();//立即执行
                    //结点删除
                    let handsCards = document.querySelectorAll('.hand_card_select_area li');
                    handsCards[that.dragIndex].remove();
                    //数据删除
                    that.vue.$store.state.Player.handCardQueue.splice(that.dragIndex,1);
                    //调整手牌位置状态
                    that.AdjustCardCoordinate(0);
                    that.AdjustCardCoordinate(1);
                    ams.TransitionEffect(false,false,true,200);
                    //触发场上友方随从的释放法术后的效果
                    for(let i in that.vue.$store.state.Player.minionsQueue){
                        await that.vue.$store.state.Player.minionsQueue[i].TriggerCatsSpell();
                    }
                }
            }
            else{
                targetDom.style.transform = "rotate(0) scale(1)";
                targetDom.style.marginTop = "0";
                targetDom.style.zIndex = "0";
                that.AdjustCardCoordinate(1);
                let ams = new AnimationS(that.vue);
                await ams.HandCardRebound();
            }
            
            if(that.vue.$store.state.Player.handCardQueue.length == 0) that.vue.$store.state.Control.selectHandCardsView = false;
        })
    }
    //合成金卡
    CraftGoldenMinion(target){
        let ams = new AnimationS(this.vue);
        //target ：合成金卡的随从数据目标
        let numberRegister = 0;
        let taunt = false,death_rattle = false,wind_fury = false,poisonous = false,reborn = false,divine_shield = false;
        let t_taunt = false,t_wind_fury = false,t_poisonous = false,t_reborn = false,t_divine_shield = false;
        //遍历手牌
        let Player = this.vue.$store.state.Player;
        let hands = document.querySelectorAll('.hand_card_select_area li');
        let minions = document.querySelectorAll('.player_row li');
        let additionSave = [];
        return new Promise(async (resolve,reject)=>{
            //删除手牌中的满足条件的卡
            for(let i = Player.handCardQueue.length - 1;i >= 0;i--){
                if(Player.handCardQueue[i].name == target.name && !Player.handCardQueue[i].golden){
                    //先去除光环加成
                    for(let j = Player.handCardQueue[i].additionContainer.length-1;j>=0;j--){
                        if(Player.handCardQueue[i].additionContainer[j].halo) Player.handCardQueue[i].additionContainer.splice(j,1);
                    }
                    additionSave = additionSave.concat(Player.handCardQueue[i].additionContainer);
                    //嘲讽
                    if(Player.handCardQueue[i].nature.taunt) taunt = true;
                    if(Player.handCardQueue[i].temp_nature.taunt) t_taunt = true;
                    //亡语
                    if(Player.handCardQueue[i].nature.death_rattle) death_rattle = true;
                    //圣盾
                    if(Player.handCardQueue[i].nature.divine_shield) divine_shield = true;
                    if(Player.handCardQueue[i].temp_nature.divine_shield) t_divine_shield = true;
                    //风怒
                    if(Player.handCardQueue[i].nature.wind_fury) wind_fury = true;
                    if(Player.handCardQueue[i].temp_nature.wind_fury) t_wind_fury = true;
                    //剧毒
                    if(Player.handCardQueue[i].nature.poisonous) poisonous = true;
                    if(Player.handCardQueue[i].temp_nature.poisonous) t_poisonous = true;
                    //重生
                    if(Player.handCardQueue[i].nature.reborn) reborn = true;
                    if(Player.handCardQueue[i].temp_nature.reborn) t_reborn = true;
                    //保存数字寄存器内容(最大值)
                    if(Player.handCardQueue[i].numberRegister > numberRegister) numberRegister = Player.handCardQueue[i].numberRegister;
                    //数据删除
                    Player.handCardQueue.splice(i,1);
                    //节点删除
                    hands[i].remove();
                }
            }
            //删除出场的满足条件的随从
            for(let i = Player.minionsQueue.length - 1;i >= 0;i--){
                if(Player.minionsQueue[i].name == target.name && !Player.minionsQueue[i].golden){
                    //先去除光环加成
                    for(let j = Player.minionsQueue[i].additionContainer.length-1;j>=0;j--){
                        if(Player.minionsQueue[i].additionContainer[j].halo) Player.minionsQueue[i].additionContainer.splice(j,1);
                    }
                    additionSave = additionSave.concat(Player.minionsQueue[i].additionContainer);
                    //嘲讽
                    if(Player.minionsQueue[i].nature.taunt) taunt = true;
                    if(Player.minionsQueue[i].temp_nature.taunt) t_taunt = true;
                    //亡语
                    if(Player.minionsQueue[i].nature.death_rattle) death_rattle = true;
                    //圣盾
                    if(Player.minionsQueue[i].nature.divine_shield) divine_shield = true;
                    if(Player.minionsQueue[i].temp_nature.divine_shield) t_divine_shield = true;
                    //风怒
                    if(Player.minionsQueue[i].nature.wind_fury) wind_fury = true;
                    if(Player.minionsQueue[i].temp_nature.wind_fury) t_wind_fury = true;
                    //剧毒
                    if(Player.minionsQueue[i].nature.poisonous) poisonous = true;
                    if(Player.minionsQueue[i].temp_nature.poisonous) t_poisonous = true;
                    //重生
                    if(Player.minionsQueue[i].nature.reborn) reborn = true;
                    if(Player.minionsQueue[i].temp_nature.reborn) t_reborn = true;
                    //保存数字寄存器内容(最大值)
                    if(Player.minionsQueue[i].numberRegister > numberRegister) numberRegister = Player.minionsQueue[i].numberRegister;
                    //数据删除
                    Player.minionsQueue.splice(i,1);
                    //节点删除
                    minions[i].remove();
                }
            }
            
            this.AdjustMinionsCoordinate(false);
            await ams.TransitionEffect(true,false,false,200);
            //合成金卡并加入手牌
            target.golden = true;
            target.ad *= 2; target.hp *= 2;
            //继承性质(三连需要金卡继承后天的特性加成)
            target.nature.taunt = taunt;
            target.nature.death_rattle = death_rattle;
            target.nature.divine_shield = divine_shield;
            target.nature.wind_fury = wind_fury;
            target.nature.poisonous = poisonous;
            target.nature.reborn = reborn;
            //继承临时性的特性
            target.temp_nature.taunt = t_taunt;
            target.temp_nature.divine_shield = t_divine_shield;
            target.temp_nature.wind_fury = t_wind_fury;
            target.temp_nature.poisonous = t_poisonous;
            target.temp_nature.reborn = t_reborn;
            //继承随从的加成(三连需要金卡继承后天的特性加成)
            target.additionContainer = additionSave;
            //赋值数字寄存器内容(三连需要金卡继承后天的特性加成)
            target.numberRegister = numberRegister;
            //打开手牌变化伐
            this.vue.$store.state.Control.isJoinHands = true;
            //数据添加
            Player.handCardQueue.push(target);
            Player.handCardQueue[Player.handCardQueue.length-1].TriggerGoldenEvent(Player.handCardQueue[Player.handCardQueue.length-1]);
            //DOM添加
            let goldenCard = this.CreateNewHandCard(target,"minion");
            this.vue.$refs.hand_card_select_area.appendChild(goldenCard);
            this.AdjustCardCoordinate(0);
            this.AdjustCardCoordinate(1);
            await ams.TransitionEffect(false,false,true,200);
            resolve(true);
        })
    }
    //三连奖励
    ComboReward(){
        return new Promise(async (resolve,reject)=>{
            let ams = new AnimationS(this.vue);
            let disC = new Card(this.vue,'spell');
            disC.name = '发现随从';
            disC.star = this.vue;//星级
            disC.img = require('../../assets/spell/spell.png');//图像
            disC.golden = false;//是否是金卡
            disC.describe = `发现一个等级${this.vue.$store.state.Pub.level + 1 > 6 ? 6 : this.vue.$store.state.Pub.level + 1}`;//描述
            disC.costType = 'coin';//消耗的类型
            disC.cost = -1;//消耗的量
            disC.value = 1;//数值
            disC.selectSpell = false;//是否是选择型法术
            disC.selectBob = false;//可选bob随从？
            disC.selectPlayer = true;//可选自己随从？
            disC.limitType = [];//限制随从
            disC.SpellExecute = function(){
                let tgtArr = [];
                for(let i = 0;i<this.vue.$store.state.Pub.allMinions.length;i++){
                    if(this.vue.$store.state.Pub.allMinions[i].star == (this.vue.$store.state.Pub.level + 1 > 6 ? 6 : this.vue.$store.state.Pub.level + 1) ) tgtArr.push(i);
                }
                for(let i = 0;i<3 && tgtArr.length > 0;i++){
                    let rand = Math.floor(Math.random()*tgtArr.length);
                    let tgt = this.vue.$store.state.Pub.allMinions[tgtArr[rand]];
                    let discoverM = new Card(this.vue,'minion');
                    discoverM.LoadingAttr(tgt,true,-1);
                    discoverM.load(this.vue,discoverM);
                    //放入发现随从的卡槽
                    this.vue.$store.state.Player.discoverQueue.push(discoverM);
                    tgtArr.splice(rand,1);
                }
                //打开发现面板
                this.vue.$store.state.Control.discover = true;
            }
            if(this.vue.$store.state.Player.handCardQueue.length < 10){
                //打开手牌变化伐
                this.vue.$store.state.Control.isJoinHands = true;
                this.vue.$store.state.Player.handCardQueue.push(disC);
                //生成手牌DOM实例
                let disCNode = this.CreateNewHandCard(disC,'spell');
                this.vue.$refs.hand_card_select_area.appendChild(disCNode);
                //调整手牌位置状态
                this.AdjustCardCoordinate(0);
                this.AdjustCardCoordinate(1);
                await ams.TransitionEffect(false,false,true,200);
            }else{
                this.vue.$store.state.Player.waitingQueue.unshift(disC);
            }
            resolve(true);
        })
    }
    //购买随从
    async PurchaseMinions(dragDOM){
        let Player = this.vue.$store.state.Player;
        let Pub = this.vue.$store.state.Pub;
        //铸币足够并且手牌数小于10张
        if(Player.coin >= 3 && Player.handCardQueue.length < 10){
            //消耗铸币
            Player.coin -= 3;
            //调用场上购买随从的函数
            for(let i in this.vue.$store.state.Player.minionsQueue){
                await this.vue.$store.state.Player.minionsQueue[i].TriggerCostCoin(3);
            }
            //调用英雄购买随从触发函数
            this.vue.$store.state.Player.hero.TriggerPurchase(Pub.bobMinionsQueue[this.dragIndex]);
            
            this.vue.$store.state.Control.isJoinHands = true;
            Pub.bobMinionsQueue[this.dragIndex].isPlayer = true;
            //此时触发购买随从的事件函数
            for(let i in Player.minionsQueue){
                await Player.minionsQueue[i].TriggerPurchaseEvent(Pub.bobMinionsQueue[this.dragIndex]);
            }
            //更新本回合购买随从类型的队列
            this.vue.$store.state.Player.purchaseTypeQueue.push(Pub.bobMinionsQueue[this.dragIndex].type);
            //将卡插入手牌
            Player.handCardQueue.push(Pub.bobMinionsQueue[this.dragIndex]);
            Player.handCardQueue[Player.handCardQueue.length - 1].TriggerPurchaseEvent(Player.handCardQueue[Player.handCardQueue.length - 1]);

            //生成手牌的DOM实体
            let card = this.CreateNewHandCard(Pub.bobMinionsQueue[this.dragIndex],"minion");
            this.vue.$refs.hand_card_select_area.appendChild(card);
            
            //DOM节点删除
            dragDOM.remove();
            
            this.AdjustMinionsCoordinate();
            this.AdjustCardCoordinate(0);
            

            let a = new AnimationS(this.vue);
            await a.MinionsRebound(this.dragBelongBob);
            
            
            
            //判断是否可以合成金卡
            let isCanGolden = this.query.SatisfiedGolden(Pub.bobMinionsQueue[this.dragIndex],false);
            if(isCanGolden) this.CraftGoldenMinion(Pub.bobMinionsQueue[this.dragIndex]);
            //将原始bob随从从数组中删除
            Pub.bobMinionsQueue.splice(this.dragIndex,1);
        }
        //铸币不足
        else{
            this.AdjustMinionsCoordinate();
            let a = new AnimationS(this.vue);
            await a.MinionsRebound(this.dragBelongBob);
        }
        
    }
    //出售随从
    SaleMinions(){
        return new Promise(async (resolve,reject)=>{
            this.dragDOMItem.style.opacity = '0';//隐藏被出售随从
            let temp = this.vue.$store.state.Player.minionsQueue[this.dragIndex];
            //调用出售随从的出售事件函数
            await temp.TriggerSale();
            //删除数据实体
            this.vue.$store.state.Player.minionsQueue.splice(this.dragIndex,1);
            //删除dom实体
            this.dragDOMItem.remove();
            //清除拖动目标
            this.dragDOMItem = null;
            this.AdjustMinionsCoordinate(false);
            this.vue.$store.state.Player.coin += 1;
            resolve(true);
        })
    }
    

    //调整随从的坐标
    AdjustMinionsCoordinate(isBob = true){
        //(bool)bob_minions : true表示调整bob随从，false表示调整player随从
        let span = 0.08;//跨度,表示元素宽度加两个元素之间的距离
        let pivot = this.vue.$store.state.PIVOT_LINE;//居中基准
        let minions = isBob ? document.querySelectorAll('.bob_row li') : document.querySelectorAll('.player_row li');
        
        for(let i = 0;i < minions.length; i++){
            let left = 0; 
            let top = 0.5 * this.screenH;
            //奇数组排列方式
            if(minions.length%2 != 0){
                left = pivot * this.screenW - 0.04 * this.screenW;
                left += (i - Math.floor(minions.length/2)) * span * this.screenW;
            }
            
            //偶数组排列方式
            else{
                if(i < minions.length/2){
                    left = pivot * this.screenW;
                    left -= (minions.length/2 - i) * span * this.screenW;
                }
                else{
                    left = pivot * this.screenW - span * this.screenW;
                    left += (i - minions.length/2 + 1) * span * this.screenW;
                }
            }
            //取整
            left = Math.floor(left);
            top = Math.floor(top);
            //调整随从
            minions[i].style.left = left + 'px';
            minions[i].style.top = isBob ? this.vue.$store.state.BOB_MINION_Y + 'px': top + 'px';
        }
        return true;
    }
    //调整手牌区的手牌位置状态
    AdjustCardCoordinate(target = 0){
        //target = 0:调整右下角卡牌的摆放
        //target = 1:调整中间卡牌的摆放
        let handCards = this.vue.$store.state.Player.handCardQueue;
        
        //调整右下角卡牌的摆放
        if(target == 0){
            //遍历手牌
            for(let i =0;i<handCards.length;i++){
                let left = 0;
                let top = this.screenH - 0.12 * this.screenW;//手牌的向上偏移高度
                let deg = 0;//手牌的旋转角度
                //卡牌放置在右下角
                {
                    left = 0.5 * this.screenW + 0.5 * this.screenH + i * 0.03 * this.screenH;
                    top -= (i - Math.floor(handCards.length/2)) * 0.001 *  this.screenH;
                    deg += 10 * (i - Math.floor(handCards.length/2))
                }
                this.vue.$nextTick(()=>{
                    this.vue.$refs.hand_cards[i].style.left = left + 'px';
                    this.vue.$refs.hand_cards[i].style.top = top + 'px';
                    this.vue.$refs.hand_cards[i].style.transform = `rotate(${deg - 5}deg)`;
                })
            }
        }
        //调整中间卡牌的摆放
        else if(target == 1){
            let handCards = document.querySelectorAll('.hand_card_select_area li');
            let span = 0.06;
            for(let i=0;i<handCards.length;i++){
                let left = 0;
                let top = this.screenH - 0.14 * this.screenW;//手牌的向上偏移高度
                let deg = 0;//手牌的旋转角度
                
                //奇数组排列方式
                if(handCards.length%2 != 0){
                    left = 0.46 * this.screenW;
                    left += (i - Math.floor(handCards.length/2)) * span * this.screenW;
                }
                //偶数组排列方式
                else{
                    // i < handCards.length/2 ? left = 0.5 * this.screenW : left = 0.5 * this.screenW - span * this.screenW;
                    left = 0.46 * this.screenW;
                    //左半边
                    if(i < (handCards.length+1)/2){
                        i == handCards.length/2 - 1 ? left -= 0.5 * span * this.screenW : left -= (handCards.length/2 - i) * span * this.screenW - 0.5 * span * this.screenW;
                    }
                    //右半边
                    else{
                        i == handCards.length/2 ? left += 0.5 * span * this.screenW : left += (i - handCards.length/2) * span * this.screenW + 0.5 * span * this.screenW;
                    }   
                }
                //奇数张手牌
                if(handCards.length%2 != 0){
                    deg += 5 * (i - Math.floor(handCards.length/2))
                    top += Math.abs((i - Math.floor(handCards.length/2))) * 0.01 * this.screenH;
                }
                //偶数张手牌
                else{
                    deg += 5 * (i - Math.floor(handCards.length/2)) + 2.5;
                    if(i < handCards.length/2 -1){
                        top += Math.abs((i - Math.floor(handCards.length/2))) * 0.01 * this.screenH;
                    }
                    if(i > handCards.length/2){
                        top += Math.abs((i - Math.floor(handCards.length/2))) * 0.01 * this.screenH;
                    }

                }
                
                handCards[i].style.transform = `scale(1) rotate(${deg}deg)`;
                handCards[i].style.left = left + 'px';
                handCards[i].style.top = top + 'px';
            }
        }
        return true;
    }

    
    //将随从插入战场上,返回true表示插入成功，false表示插入失败
    InsertIntoBattleField(dragItem,index,handCard = true){
        //dragItem : 拖拽的dom实体
        //index : 目标在原来数组中的索引，用于索引删除原始目标
        //handCard : true为从手牌插入战场，false为将场上随从位置调整
        return new Promise(async(resolve,reject)=>{
            
            let left = dragItem.offsetLeft;
            let Player = this.vue.$store.state.Player;
            let dataItem = handCard? Player.handCardQueue[index] : Player.minionsQueue[index];
            let res = {
                status : true,
                index : -1,
                dataItem : dataItem
            }
            
            //场上没有随从，直接放置（只可能是手牌插入情况）
            if(Player.minionsQueue.length == 0){
                //先删除数据实体部分
                handCard ? Player.handCardQueue.splice(index,1) : Player.minionsQueue.splice(index,1);
                //我的随从添加数据实体
                Player.minionsQueue.push(dataItem);
                //添加DOM实体
                let minion = await this.CreateMinionDom(dataItem,false);
                this.vue.$refs.player_minions.appendChild(minion);
                this.AdjustMinionsCoordinate(false);
                //删除手牌DOM
                dragItem.remove();
                if(Player.handCardQueue.length == 0){
                    this.vue.$store.state.Control.selectHandCardsView = false;
                }
                res.index = 0;
                resolve(res);
                /*-------
                        此处应该有动画
                ---------*/
                return;
            }
            let lis = document.querySelectorAll('.player_row li');
            if(lis.length == 0){
                resolve(false);
            }
            //插入最左边
            if(left < lis[0].offsetLeft){
                //先删除
                handCard ? Player.handCardQueue.splice(index,1) : Player.minionsQueue.splice(index,1);
                //手牌插入
                Player.minionsQueue.unshift(dataItem);
                let minion = await this.CreateMinionDom(dataItem,false);
                this.vue.$refs.player_minions.insertBefore(minion,lis[0]);
                //删除手牌DOM
                dragItem.remove();
                if(Player.handCardQueue.length == 0){
                    this.vue.$store.state.Control.selectHandCardsView = false;
                }
                res.index = 0;
                resolve(res);
                /*-------
                    此处应该有动画
                ---------*/
                
            }
            //插入最右边
            else if(left > lis[lis.length-1].offsetLeft){
                //先删除
                handCard ? Player.handCardQueue.splice(index,1) : Player.minionsQueue.splice(index,1);
                //添加数据实体
                Player.minionsQueue.push(dataItem);
                //删除DOM
                dragItem.remove();
                //添加DOM实体
                let minion = await this.CreateMinionDom(dataItem,false);
                this.vue.$refs.player_minions.appendChild(minion);
                if(Player.handCardQueue.length == 0){
                    this.vue.$store.state.Control.selectHandCardsView = false;
                }
                res.index = Player.minionsQueue.length - 1;
                resolve(res);
                /*-------
                    此处应该有动画
                ---------*/
            }
            //插入中间
            else{
                
                for(let i=0;i<lis.length;i++){
                    if(i < lis.length -1 ){
                        if(left > lis[i].offsetLeft && left < lis[i + 1].offsetLeft){
                            //添加数据实体
                            Player.minionsQueue.splice(i+1,0,dataItem);
                            //添加DOM实体
                            let minion = this.CreateMinionDom(dataItem,false);
                            this.vue.$refs.player_minions.insertBefore(minion,lis[i+1]);
                            
                            //删除数据实体
                            if(!handCard){
                                if(i+1 > index){
                                    Player.minionsQueue.splice(index,1);
                                }
                                else{
                                    Player.minionsQueue.splice(index + 1,1);
                                }
                            }
                            else{
                                Player.handCardQueue.splice(index,1)
                            }
                            //删除手牌DOM
                            dragItem.remove();
                            //关闭手牌选择
                            if(Player.handCardQueue.length == 0){
                                this.vue.$store.state.Control.selectHandCardsView = false;
                            }
                            res.index = i+1;
                            resolve(res);
                            return;
                        }
                    }
                }
                resolve(res);
            }
            

            
        })

    }

    //招募回合结束，触发对应随从效果
    RecruitOver(){
        let minions = this.vue.$store.state.player.minions_queue;
        for(let i=0;i<minions.length;i++){
            for(let j=0;j<minions.RecruitOver.length;j++){
                minions.RecruitOver[j].Execute();
            }
        }
    }


    //战斗中召唤一个随从
    SummonMinionsInFighting(minData,target,targetBelongPlayer,driver,index,impactorDead = true,hesitateGolden = true,addSummonTime = true){
        //minData随从对象的信息数据
        //target是召唤这个召唤随从的主人
        //targetBelongPlayer : 是召唤该随从的主人的所处阵营
        //driver : 驱动器实体
        //index : 是召唤调用者发起时所处的数组下标
        return new Promise(async (resolve,reject)=>{
            if(addSummonTime){
                //被召唤出来的随从数据对象
                for(let i = 0;i< this.query.SummonTimes(targetBelongPlayer,true,driver);i++){
                    await this.SummonMinionsCommons(minData,target,targetBelongPlayer,driver,index,impactorDead,hesitateGolden);
                }
                resolve(true);
                return;
            }
            else{
                let min = await this.SummonMinionsCommons(minData,target,targetBelongPlayer,driver,index,impactorDead,hesitateGolden);
                resolve(min);
                return;
            }
        })

    }
    //中间过渡函数
    SummonMinionsCommons(minData,target,targetBelongPlayer,driver,index,impactorDead,hesitateGolden){
        let ams = new AnimationS(this.vue);
        return new Promise(async (resolve,reject)=>{
            let targetTeam = targetBelongPlayer ? driver.PlayerMinions: driver.EnemyMinions;
            if(targetTeam.length >= 7){
                resolve(true);
                return;
            }
            let min = this.InitBattleAttr(minData,target,targetBelongPlayer,hesitateGolden,driver);
            //被召唤随从的节点
            let minNode = this.CreateMinionDom(min,!targetBelongPlayer);
            minNode.style.transform = 'scale(0)';
            //确定所有兄弟结点
            let broNodes = targetBelongPlayer ? document.querySelectorAll('.player_row li') : document.querySelectorAll('.bob_row li');
            let fatherNode = targetBelongPlayer ? document.querySelector('.player_row') : document.querySelector('.bob_row');
            
            if(broNodes.length == 0){
                fatherNode.appendChild(minNode);
                targetTeam.push(min);
            }
            else{
                let lastNode = null;
                if(index == 0){
                    lastNode = broNodes[0];
                    //召唤者是否死亡，死亡则在源数组坐标上的前一个位置召唤
                    impactorDead ? fatherNode.insertBefore(minNode,lastNode): lastNode.after(minNode);
                    impactorDead ? targetTeam.splice(0,0,min) : targetTeam.splice(1,0,min);
                }
                else{
                    lastNode = impactorDead ? broNodes[index - 1] : broNodes[index];
                    //特殊处理
                    if(lastNode == null || lastNode == undefined){
                        fatherNode.appendChild(minNode);
                        console.log('亡语特殊处理');
                    }
                    else{
                        lastNode.after(minNode);
                    }
                    
                    if(impactorDead){
                        targetTeam.splice(index,0,min);
                    }
                    else{
                        let temp = [];
                        for(let j =targetTeam.length-1;j>=0;j--){
                            if(j > index){
                                temp.push(targetTeam[j]);
                                targetTeam.pop();
                            }
                        }
                        targetTeam.push(min);
                        for(let j=temp.length-1;j>=0;j--){
                            targetTeam.push(temp[j]);
                        }
                    }
                }
            }
            
            //调整位置
            this.AdjustMinionsCoordinate(false)
            this.AdjustMinionsCoordinate(true);
            await ams.TransitionEffect(true,true,false,200);
            
            minNode.style.transition = 'all .2s';
            minNode.style.transform = 'scale(1)';
            await ams.Delay(200);
            minNode.style.transition = 'none';                    
            //触发召唤随从后调用事件
            for(let i in targetTeam){
                await targetTeam[i].TriggerSummonMinions(min,minNode,driver);
            }
            resolve(min);
        })
    }
    //初始化随从的战斗属性
    InitBattleAttr(minion,target,targetBelongPlayer,hesitateGolden,driver){
        //minion:是被召唤随从的数据
        //target是被召唤随从的主人
        let card = new Card(this.vue,'minion');
        //创建一个数据对象
        card.LoadingAttr(minion,true,-1);
        card.load(this.vue,card);
        hesitateGolden ? card.golden = target.golden : null;
        card.additionContainer  =[];
        card['battleAttr'] = {
            ['driver'] : driver,
            ['originIndex'] : -1,//对应战斗前的随从索引
            ['belongPlayer'] : targetBelongPlayer,
            ['ad'] : card.ad,
            ['hp'] : card.hp,
            ['originAd'] : card.ad,
            ['originHp'] : card.hp,
            ['attacked'] : false,//是否已经攻击过
            //特性
            ['death_rattle'] : card.nature.death_rattle,
            ['divine_shield'] : card.nature.divine_shield || card.temp_nature.divine_shield, 
            ['poisonous'] : card.nature.poisonous || card.temp_nature.poisonous,
            ['reborn'] : card.nature.reborn || card.temp_nature.reborn,
            ['taunt'] : card.nature.taunt || card.temp_nature.taunt,
            ['wind_fury'] : card.nature.wind_fury || card.temp_nature.wind_fury,
            ['avenge']  : card.nature.avenge,
            ['passive'] : card.nature.passive,
            ['frenzy'] :card.nature.frenzy,
            ['aoe'] : card.nature.aoe,
        }
        return card;
    }
    //招募阶段召唤出一个随从后调用


    
}

export default Commons;