import BloodStone from "../data/bloodStone";
import Commons from "./commons";
import AnimationS from "./animation";
import Card from "../data/card";
import SpellCraft from "../spell/spell_craft.js";
import IQuery from "./iquery";
//产生道具卡
class Produce{
    constructor(vue){
        this.vue = vue;
        this.screenW = this.vue.$store.state.ScreenSize.width;
        this.screenH = this.vue.$store.state.ScreenSize.height;
    }
    //追加object随从牌到你的手牌中
    PushMinionsHandCard(target,needGolden = true){
        console.log(this);
        //target要追加的手牌数据对象（必须是new出来的）
        //needGolden表示是否需要构成三连，小瞎眼触发发现鱼并不会立即构成三连，而是下回合开始才执行三连
        let com = new Commons(this.vue);
        let ams = new AnimationS(this.vue);
        return new Promise(async (resolve,reject)=>{
            if(this.vue.$store.state.Player.handCardQueue.length < 10){
                this.vue.$store.state.Control.isJoinHands = true;
                target.isPlayer = true;
                //数据追加
                this.vue.$store.state.Player.handCardQueue.push(target);
                //DOM结点追加
                let node = com.CreateNewHandCard(target,'minion');
                
                this.vue.$refs.hand_card_select_area.appendChild(node);
                com.AdjustCardCoordinate(0);
                com.AdjustCardCoordinate(1);
                await ams.TransitionEffect(false,false,true,200);
                //三连判断
                if(needGolden){
                    let query = new IQuery(this.vue);
                    let isCanGolden = query.SatisfiedGolden(target,false);
                    if(isCanGolden) com.CraftGoldenMinion(target);
                }
            }
            resolve(true);
        })

    }
    //将铸币置入手牌
    ProduceCoinCard(tgt,x,y){
        let ams= new AnimationS(this.vue);
        let com = new Commons(this.vue);
        return new Promise(async (resolve,reject)=>{
            this.vue.$store.state.Control.selectHandCardsView = true;
            await ams.Delay(10);
            let card = new Card(this.vue,'spell');
            //法术的名字
            card.name = "一枚铸币";
            //星级
            card.star = 1;
            //法术的图像
            card.img = require('../../assets/spell/spell (1).png');
            //是否是金卡
            card.golden = tgt.golden;
            //法术的描述
            card.describe = "获得一枚铸币";
            //消耗的类型
            card.costType = 'coin';
            //消耗的量
            card.cost = 0;
            //数值
            card.value = 0;
            //卡牌的持续时间
            card.duration = null;
            //是否是选择型法术
            card.selectSpell = false;
            //可选bob随从？
            card.selectBob = false;
            //可选自己随从？
            card.selectPlayer = false;
            //限制随从
            card.limitType = [];
            //法术的回调函数
            card.SpellExecute = this.GetCoin;

            if(this.vue.$store.state.Player.handCardQueue.length < 10){
                if(!this.vue.$store.state.Control.selectHandCardsView) this.vue.$store.state.Control.selectHandCardsView = true;
                await ams.Delay(2);
                //数据添加手牌序列
                this.vue.$store.state.Control.isJoinHands = true;
                card.isPlayer = true;
                this.vue.$store.state.Player.handCardQueue.push(card);

                let coinNode = com.CreateNewHandCard(card,'spell');
                coinNode.style.transform = 'scale(.5) rotate(0deg)';
                coinNode.style.left = x + 'px';
                coinNode.style.top = y - 0.15 * this.screenW + 'px';
                this.vue.$refs.hand_card_select_area.appendChild(coinNode);
                await ams.Delay(2);
                com.AdjustCardCoordinate(0);
                com.AdjustCardCoordinate(1);
                ams.TransitionEffect(false,false,true,400);
                await ams.Delay(405);
            }
            resolve(true);
        })
    }
    //野猪人生产鲜血宝石
    ProduceBloodStone(tgt,x,y){
        // window.requestAnimFrame = (function(){
        //     return  window.requestAnimationFrame       || 
        //             window.webkitRequestAnimationFrame || 
        //             window.mozRequestAnimationFrame    || 
        //             window.oRequestAnimationFrame      || 
        //             window.msRequestAnimationFrame     || 
        //             function( callback ){
        //               window.setTimeout(callback, 1000 / 60);
        //             };
        //   })();
        //x,y为生产宝石的结点的x，y坐标
        //tgt为生产者的DOM结点
        let com = new Commons(this.vue);
        let ams = new AnimationS(this.vue);
        return new Promise(async (resolve,reject)=>{
            this.vue.$store.state.Control.selectHandCardsView = true;
            await ams.Delay(10);
            let bs = new BloodStone(this.vue,tgt.golden);
            //数据添加手牌序列
            this.vue.$store.state.Control.isJoinHands = true;
            bs.isPlayer = true;
            this.vue.$store.state.Player.handCardQueue.push(bs);
            // let fatherNode = document.querySelector('.hand_card_select_area')
            //添加到手牌序列
            let bsNode = com.CreateNewHandCard(bs,'spell');
            bsNode.style.transition = 'all .4s';
            bsNode.style.transform = 'scale(.5) rotate(0deg)';
            bsNode.style.left = x + 'px';
            bsNode.style.top = y - 0.15 * this.screenW + 'px';
            this.vue.$refs.hand_card_select_area.appendChild(bsNode);
            // let move = 0;
            // let w = this.screenW;
            // let timer = requestAnimationFrame(function fn(){
            //     bsNode.style.top = tgtNode.offsetTop - 0.15*w + move + 'px';
            //     move ++;
            //     if(move > 1000){
            //         cancelAnimationFrame(timer);
            //     }
            //     else{
            //         requestAnimationFrame(fn);
            //     }
            // })
            await ams.Delay(2);
            com.AdjustCardCoordinate(0);
            com.AdjustCardCoordinate(1);
            ams.TransitionEffect(false,false,true,400);
            await ams.Delay(405);
            resolve(true);
        })
    }
    //纳迦生成塑造法术
    ProduceSpellCraft(tgtNode,tgt,spellName,spellDescribe){
        let ams = new AnimationS(this.vue);
        let com = new Commons(this.vue);
        return new Promise(async (resolve,reject)=>{
            let spell = new Card(this.vue,'spell');
            //法术的名字
            spell.name = spellName;
            //法术的类别
            spell.type = 'spellCraft';
            //星级
            spell.star = tgt.star;
            //法术的图像
            spell.img = tgt.img;
            //是否是金卡
            spell.golden = tgt.golden;
            //法术的描述
            spell.describe = spellDescribe;
            //卡牌的持续时间
            spell.duration = 1;
            //消耗的类型
            spell['costType'] = 'coin';
            //消耗的量
            spell['cost'] = 0;
            //数值
            spell['value'] = 0;
            //是否是选择型法术
            spell['selectSpell'] = true;
            //可选bob随从？
            spell['selectBob'] = true;
            //可选自己随从？
            spell['selectPlayer'] = true;
            //限制随从
            spell['limitType'] = [];
            //法术的回调函数
            let sc = new SpellCraft(this.vue,tgt,spell);
            spell.SpellExecute = sc.ReturnNagaSpellCraft();
            
            //添加或进入等待队列
            if(this.vue.$store.state.Player.handCardQueue.length < 10){
                if(!this.vue.$store.state.Control.selectHandCardsView) this.vue.$store.state.Control.selectHandCardsView = true;
                await ams.Delay(2);
                //数据添加手牌序列
                this.vue.$store.state.Control.isJoinHands = true;
                spell.isPlayer = true;
                this.vue.$store.state.Player.handCardQueue.push(spell);

                let spellNode = com.CreateNewHandCard(spell,'spell');
                spellNode.style.transition = 'all .4s';
                spellNode.style.transform = 'scale(.5) rotate(0deg)';
                spellNode.style.left = tgtNode.offsetLeft + 'px';
                spellNode.style.top = tgtNode.offsetTop - 0.15 * this.screenW + 'px';
                this.vue.$refs.hand_card_select_area.appendChild(spellNode);
                await ams.Delay(2);
                com.AdjustCardCoordinate(0);
                com.AdjustCardCoordinate(1);
                ams.TransitionEffect(false,false,true,400);
                await ams.Delay(405);
            }
            else{
                this.vue.$store.state.Player.waitingQueue.unshift(spell);
            }
            resolve(true);
        })
    }
    
    
    //铸币的执行函数
    GetCoin(){
        return new Promise((resolve,reject)=>{
            this.vue.$store.state.Player.coin += 1;
            resolve(true);
        })
    }
}

export default Produce;