import Hero from "../data/hero";
import Block from '../data/block'
import Card from "../data/card";
import Produce from "../function/produce";
class Brian extends Hero{
    constructor(vue,dataItem){
        super(vue,dataItem);
        this.battleCryCount = 0;

    }
    //填装被动
    LoadPassivePower(){
        let that = this;
        let func = function(data){
            let target = data.purchaseTarget;
            return new Promise((resolve,reject)=>{
                if(target.nature.battle_cry && that.battleCryCount < 5){
                    
                    that.battleCryCount += 1;
                    if(that.battleCryCount == 5){
                        let gift = null;
                        for(let i in that.vue.$store.state.Pub.allMinions){
                            if(that.vue.$store.state.Pub.allMinions[i].name == '布莱恩铜须'){
                                gift = that.vue.$store.state.Pub.allMinions[i];
                                break;
                            }
                        }
                        if(gift == null){
                            reject('没有找到铜须');
                            return;
                        }
                        //给铜须
                        if(that.vue.$store.state.Player.handCardQueue.length < 10){
                            let card = new Card(that.vue);
                            card.LoadingAttr(gift,true,-1);
                            card.load(that.vue,card);
                            let p = new Produce(that.vue);
                            p.PushMinionsHandCard(card,true);
                        }
                    }
                }
                resolve(true);
            })
        }
        let block = new Block("purchase",this,'hero',null,func)
        this.Purchase.push(block);
    }
}


export default Brian;